import React, { FC, ReactElement,useState } from "react";
import { CheckSchools_Interface, FC_SelectSchoolSchool, UniversityItem } from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import InputSelect from "../../components/Inputs/InputSelect";
import Modal, { ModalSize } from "../../components/Modal/Modal";

type UpdateSchoolUniversityProps = {
  close: () => void;
  data: CheckSchools_Interface | null;
  user_id: string;
  successFunc: (data: UniversityItem) => void;
};



const UpdateSchoolUniversity: FC<UpdateSchoolUniversityProps> = ({
  close, data,user_id,successFunc
}): ReactElement => {
const [loading, setLoading] = useState(false)
const [value, setValue] = useState('')
const [error, setError] = useState('')
const [success, setSuccess] = useState('')
const [done, setDone] = useState(false)

const submitData = (e:any) => {
    e.preventDefault();



    if (!value) return window.alert("Please choose a value");

    if (!window.confirm("Make sure you choose the right choice, because this can't be updated")) return;

    setLoading(true);
    FC_SelectSchoolSchool({
        university_id: value,
        user_id: user_id
    }, (status,msg) => {
        if (!status || msg !== "") setError(`Error: ${msg}`);
        if (status) setSuccess(`Your Information has been updated`);
        setLoading(false);
        let foundItem = data?.schools.find(el => el.university_id === value);
        if (foundItem) successFunc(foundItem)
        setDone(true)
        setTimeout(() => {
            close()
        }, 3000);
    })
}

let schools_list = data? data.schools.map(el => ({
    title: `${el.university} (${el.qualification_name})`,
    value: el.university_id
})) : []
  /* function body */
  return <>
    <Modal 
    backDrop={true} 
    // theme={ModalInterface} 
    close={close} 
    backDropClose={false}  
    widthSizeClass={ModalSize.medium} 
    displayClose={true} 
    padding={{
        title: true,
        body: undefined,
        footer: undefined
    }}
    title="Application form"
    >
        <div className="border-t p-4">
        

{/* <SelectCustom loading={false} id={""} title={""} close={undefined} data={undefined} click={undefined} /> */}
<form onSubmit={submitData}>
{success !== "" && <div className="py-2">
    <Alert theme="success">{success}</Alert>
    </div>}
 
<InputSelect 
    onChange={e => setValue(e.target.value)} 
    value={value} 
    error={error !== "" ? error : ""} 
    label={"Select the nearest TTC you want to study in"} 
    placeholder={"Select"} 
    name={""} 
    disabled={loading || done}
    data={[{title: "Select a school", value: ""}, ...schools_list]} />

    {done === false && <div className=" ">

        <div className="text-right">
        <Button loading={loading} className="text-white">Submit your application</Button>
        </div>
    </div>}
    
</form>
        </div> 
            
    </Modal>
  </>
  
;
};
export default UpdateSchoolUniversity;

