import { FC, ReactElement } from "react";
import { MdAccountCircle } from "react-icons/md";
import { NavLink } from "react-router-dom";
import { Auth } from "../../actions";
import Button from "../Buttons/Button";

export const AccountModal: FC<{
  auth: Auth;
  setAccount: (status: boolean) => void;
  Logout: () => void;
}> = ({ auth, setAccount, Logout }): ReactElement => {
  const { district, school, sector, stakeholder, user } = auth;

  return (
    <>
      <div
        className="fixed top-0 right-0 left-0 bottom-0 z-40 bg-dark-800 opacity-10"
        onClick={setAccount.bind(this, false)}
      ></div>

      <div className=" rounded w-80 bg-white border-blue-500 border mt-1 border-opacity-60 z-60 dark:bg-dark-800 fixed top-29 right-3">
        <div className="text-center p-2 bg-primary-900 bg-opacity-60">
          <MdAccountCircle className="text-8xl text-white mx-auto" />

          <h6 className="text-center mt-1 text-white">{user?.username}</h6>
          <p className="pt-1 font-bold text-lg text-white ">
            {auth?.user?.first_name} {auth?.user?.first_name}
          </p>

          <label className="text-white bg-blue-600 px-3 py-1 text-sm rounded-full">
            {auth.role?.role}
          </label>
        </div>

        <div className="flex-1 p-1">
          {district?.district_name && (
            <div className=" border-b border-blue-600 border-opacity-50 m-1">
              <CardDetails title="District" value={district?.district_name} />
            </div>
          )}

          {sector?.sector_name && (
            <div className=" border-b border-blue-600 border-opacity-50 m-1 ">
              <CardDetails title="Sector" value={sector?.sector_name} />
            </div>
          )}

          {stakeholder?.development_partner_name && (
            <div className=" border-b border-blue-600 border-opacity-50 m-1 ">
              <CardDetails
                title="School"
                value={stakeholder?.development_partner_name}
              />
            </div>
          )}

          {school?.school_name && (
            <div className=" border-b border-blue-600 border-opacity-50 m-1 ">
              <CardDetails title="School" value={school?.school_name} />
            </div>
          )}

          {auth.position && (
            <div className=" border-b border-blue-600 border-opacity-50 m-1 ">
              <CardDetails
                title="Qualification"
                value={`${auth.position.qualification_name}`}
              />
            </div>
          )}

          {auth.position && (
            <div className=" border-b border-blue-600 border-opacity-50 m-1 ">
              <CardDetails
                className="flex-col text-left"
                title="Position"
                value={`${auth.position.position_code} - ${auth.position.position_name}`}
              />
            </div>
          )}
        </div>

        <div className="p-2">
          <NavLink to={"/profile"} className={`mb-2 block`} exact>
            <Button theme="primary-light" full>
              My profile
            </Button>
          </NavLink>

          <Button theme="danger-light" full onClick={Logout}>
            Logout
          </Button>
        </div>
      </div>
    </>
  );
};

const CardDetails: FC<{ title: string; value: string; className?: string }> = ({
  title,
  value,
  className = "",
}): ReactElement => (
  <div className={`p-1 flex items-center ${className}`}>
    <label className="text-md opacity-50 pr-2">{title}:</label>
    <h6 className="text-md flex-1 font-bold">{value}</h6>
  </div>
);
