export const capitalizeFirstLetter = (value: string): string => {
  return value.charAt(0).toUpperCase() + value.slice(1);
};

const sortCompare = (property: string, sortOrder: 1 | -1) => {
  return function (a: any, b: any) {
    let result =
      a[property] < b[property] ? -1 : a[property] > b[property] ? 1 : 0;
    return result * sortOrder;
  };
};

export const sortThisArray = (
  TYPE: boolean,
  data: any[],
  property: string,
  sortOrder: "ASC" | "DESC" = "ASC"
): any[] => {
  if (TYPE && property.length > 0) {
    let _sortOrder: 1 | -1 = sortOrder === "ASC" ? 1 : -1;
    return data.sort(sortCompare(property, _sortOrder));
  }
  return data;
};

export const isEmptyOrSpaces = (str: string): boolean => {
  return str === null || str.match(/^ *$/) !== null;
};

export const toAnyDigit = (
  value: string | number,
  padding: number = 3
): string => {
  var zeroes = new Array(padding + 1).join("0");
  return (zeroes + value).slice(-padding);
};

export const commaFy = (num: number) => {
  var str = num.toString().split(".");
  if (str[0].length >= 4) {
    str[0] = str[0].replace(/(\d)(?=(\d{3})+$)/g, "$1,");
  }
  if (str[1] && str[1].length >= 4) {
    str[1] = str[1].replace(/(\d{3})/g, "$1 ");
  }
  return str.join(".");
};

export const displayNumbers = (value: number): string => {
  if (value < 1000) return value + "";

  if (value < 1000000) return value / 1000 + "K";
  if (value >= 1000000) return value / 1000000 + "M";

  return "";
};

export const capitalizeFirstWord = (value: string): string => {
  let res = "";
  let spaceFound: boolean = false;
  for (let item of value) {
    if (item !== " " && spaceFound === false) {
      res += capitalizeFirstLetter(item);
    } else {
      res += item;
      spaceFound = true;
    }
  }
  return res;
};

export const getRandomInt = (max: number) => {
  return Math.floor(Math.random() * max);
};

export const FC_ConvertArrayToString = (data: any[]): string => {
  let result: string = "";
  for (let x of data) {
    result += x + ", ";
  }

  result = result.slice(0, -2);
  return result;
};

export function sortGroups(a: GroupItem, b: GroupItem) {
  const as = a.group_id + "";
  const bs = b.group_id + "";
  if (+as < +bs) {
    return -1;
  }
  if (+as > +bs) {
    return 1;
  }
  return 0;
}

