import { App, Action, ActionTypes } from "../actions";

// default state
const defaultState: App = {
  side_menu: false,
  search: "",
};

export const appReducer = (state: App = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.TOGGLE_MENU:
      return { ...state, side_menu: action.payload } as App;
    case ActionTypes.MAIN_SEARCH:
      return { ...state, search: action.payload } as App;
    default:
      return state;
  }
};
