import React, { Component, Fragment } from "react";
import { BsInfoCircleFill, BsShieldFillCheck } from "react-icons/bs";
import { FaTimes } from "react-icons/fa";
import { MdCheckCircle, MdCheckCircleOutline } from "react-icons/md";
import { RouteComponentProps } from "react-router";
import { Link } from "react-router-dom";
import {
  DPMembershipApplicationGetInterface,
  DPMembershipApplicationStatus,
  GetDPMembershipApplicationDetailsById,
  UpdateDPMembershipApplicationFn,
  UpdateDPMembershipApplicationInterface,
} from "../../actions/dp_membership_application";
import { GetSubSectorsInterface } from "../../actions/training_settings_create_edit";
import Button from "../../components/Buttons/Button";
import Loading from "../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { DPMembershipApplication } from "./DPMembershipApplication";
import { CountryCodeInterface } from "./fragments/data";
import { RegisterAccount } from "./fragments/RegisterAccount";

export interface FormatDataToEdit {
  organization_category_id: string;
  organization_category_name: string;
  organization_name: string;
  phone_number: string;
  email: string;
  location: string;
  rgb_certificate: string;
  implementation_plan: string;
  selected_country_code: CountryCodeInterface;
  website_link: string;
  sub_sectors: GetSubSectorsInterface[];
  added_sub_sectors: string[];
  application_id: string;
  status: DPMembershipApplicationStatus;
  comment: string;
  tin_number: string;
}

interface UpdateDPMembershipApplicationProps
  extends RouteComponentProps<{ application_id: string }> {}

interface UpdateDPMembershipApplicationState {
  error: string;
  application_details: DPMembershipApplicationGetInterface | null;
  loading: boolean;
  submitting: boolean;
  success: string;
  view_success_message: boolean;
}

export class UpdateDPMembershipApplication extends Component<
  UpdateDPMembershipApplicationProps,
  UpdateDPMembershipApplicationState
> {
  constructor(props: UpdateDPMembershipApplicationProps) {
    super(props);

    this.state = {
      loading: false,
      error: "",
      success: "",
      application_details: null,
      submitting: false,
      view_success_message: false,
    };
  }
  UpdateApplication = (data: UpdateDPMembershipApplicationInterface) => {
    this.setState({ application_details: null, submitting: true });
    UpdateDPMembershipApplicationFn(
      data,
      (
        loading: boolean,
        res: { type: "success" | "error"; msg: string } | null
      ) => {
        this.setState({ submitting: loading });
        if (res !== null && res.type === "success") {
          this.setState({
            success: res.msg,
            error: "",
            loading: false,
            submitting: false,
            view_success_message: true,
          });
        }
        if (res !== null && res.type === "error") {
          this.setState({
            error: res.msg,
            success: "",
            loading: false,
            submitting: false,
          });
        }
      }
    );
  };
  LoadDetails = () => {
    const application = this.props.match.params.application_id;
    if (application.length === 0) {
      return this.setState({ error: "Failed to verify this link" });
    }
    this.setState({ loading: true, view_success_message: false });
    GetDPMembershipApplicationDetailsById(
      application,
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
          data: DPMembershipApplicationGetInterface | null;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null) {
          if (res.type === "success") {
            this.setState({
              success: res.msg,
              error: "",
              loading: false,
              application_details: res.data,
            });
          }
          if (res.type === "error") {
            this.setState({ error: res.msg, success: "", loading: false });
          }
        }
      }
    );
  };
  componentDidMount(): void {
    this.LoadDetails();
  }
  render() {
    // Loading
    if (this.state.loading === true) {
      return (
        <div className="grid grid-cols-12">
          <div className="col-span-12 md:col-span-4"></div>
          <div className="bg-white rounded-xl p-10 m-10 col-span-12 md:col-span-4 flex flex-col items-center justify-center w-full">
            <Loading />
            <div className="text-center text-xl font-light mt-2">
              Verifying and checking the link, please wait...
            </div>
          </div>
          <div className="col-span-12 md:col-span-4"></div>
        </div>
      );
    }
    // Error
    if (this.state.error !== "") {
      return (
        <div className="grid grid-cols-12">
          <div className="col-span-12 md:col-span-4"></div>
          <div className="bg-white rounded-xl p-10 m-10 col-span-12 md:col-span-4 flex flex-col items-center justify-center w-full">
            <div>
              <div className="h-20 w-20 bg-red-100 text-red-600 rounded-md flex items-center justify-center animate__animated animate__shakeX">
                <FaTimes className="text-7xl" />
              </div>
            </div>
            <div className="text-center text-xl font-light mt-2">
              {this.state.error}
            </div>
            <div className="mt-4">
              <Link
                to="/"
                className="px-3 py-2 rounded-md bg-accent-400 text-accent-900 hover:bg-accent-800 hover:text-black"
              >
                Go back to Homepage
              </Link>
            </div>
          </div>
          <div className="col-span-12 md:col-span-4"></div>
        </div>
      );
    }
    // Approved
    if (
      this.state.application_details !== null &&
      this.state.application_details.status ===
        DPMembershipApplicationStatus.APPROVED
    ) {
      if (this.state.application_details.has_account === 0) {
        return (
          <div className="grid grid-cols-12 gap-2">
            <div className="col-span-12 lg:col-span-2"></div>
            <div className="col-span-12 lg:col-span-8 px-2 md:px-6 py-2 md:py-6">
              <div className="bg-white rounded mb-3 p-3 px-5">
                <div className="text-black animate__animated animate__fadeIn font-bold text-xl border-b pb-3 border-white">
                  Organization registered successfully
                </div>
                <div className="flex flex-row items-center gap-4">
                  <div>
                    <BsShieldFillCheck className="text-4xl animate__animated animate__zoomIn text-green-600" />
                  </div>
                  <div className="">
                    <div>Organization name</div>
                    <div className="text-lg font-bold">
                      {this.state.application_details.development_partner_name}
                    </div>
                  </div>
                </div>
              </div>
              <div className="bg-white border border-accent-800 text-black rounded-md mb-3 p-3 px-5 flex flex-row items-center gap-4">
                <div>
                  <BsInfoCircleFill className="text-4xl animate__animated animate__fadeIn text-accent-800" />
                </div>
                <div className="">
                  Your organization has been approved, Now you are required to
                  register the account that will be used to login the system
                </div>
              </div>
              <RegisterAccount
                developmentPartner={this.state.application_details}
                onCancel={() => {}}
              />
            </div>
            <div className="col-span-12 lg:col-span-2"></div>
          </div>
        );
      } else {
        return (
          <div className="grid grid-cols-12">
            <div className="col-span-12 md:col-span-4"></div>
            <div className="bg-white rounded-xl p-10 m-10 col-span-12 md:col-span-4 flex flex-col items-center justify-center w-full">
              <div>
                <div className="h-20 w-20 bg-green-100 text-green-600 rounded-md flex items-center justify-center animate__animated animate__zoomIn">
                  <MdCheckCircleOutline className="text-7xl" />
                </div>
              </div>
              <div className="text-center text-base font-bold my-2">
                {
                  "Your application has been accepted! Please login using your phone number and password"
                }
              </div>
              <div className="mt-4">
                <Link
                  to="/login"
                  className="px-3 py-2 rounded-md bg-accent-400 text-accent-900 hover:bg-accent-800 hover:text-black"
                >
                  Go back to login
                </Link>
              </div>
            </div>
            <div className="col-span-12 md:col-span-4"></div>
          </div>
        );
      }
    }
    // Rejected
    if (
      this.state.application_details !== null &&
      this.state.application_details.status ===
        DPMembershipApplicationStatus.REJECTED
    ) {
      return (
        <div className="grid grid-cols-12">
          <div className="col-span-12 md:col-span-4"></div>
          <div className="bg-white rounded-xl p-10 m-10 col-span-12 md:col-span-4 flex flex-col items-center justify-center w-full">
            <div>
              <div className="h-20 w-20 bg-red-100 text-red-600 rounded-md flex items-center justify-center animate__animated animate__shakeX">
                <FaTimes className="text-7xl" />
              </div>
            </div>
            <div className="text-center text-base font-bold mt-2">
              {
                "Thank you for your interest in this application. After review, unfortunately we are not able to continue with your application!"
              }
            </div>
            <div className="mt-4">
              <Link
                to="/"
                className="px-3 py-2 rounded-md bg-accent-400 text-accent-900 hover:bg-accent-800 hover:text-black"
              >
                Go back to Homepage
              </Link>
            </div>
          </div>
          <div className="col-span-12 md:col-span-4"></div>
        </div>
      );
    }
    // Submitting updates
    if (this.state.submitting === true) {
      return (
        <div className="grid grid-cols-12">
          <div className="col-span-12 md:col-span-4"></div>
          <div className="bg-white rounded-xl p-10 m-10 col-span-12 md:col-span-4 flex flex-col items-center justify-center w-full">
            <Loading />
            <div className="text-center text-xl font-light mt-2">
              Submitting the updates, please wait...
            </div>
          </div>
          <div className="col-span-12 md:col-span-4"></div>
        </div>
      );
    }
    return (
      <Fragment>
        {this.state.application_details !== null && (
          <DPMembershipApplication
            editApplication={{
              application_id: this.state.application_details.id,
              added_sub_sectors: this.state.application_details.subareas.map(
                (item) => item.sub_area_id.toString()
              ),
              email: this.state.application_details.email,
              implementation_plan:
                this.state.application_details.implementation_plan_file_name,
              location: this.state.application_details.location,
              organization_category_id:
                this.state.application_details.organization_category_id.toString(),
              organization_category_name:
                this.state.application_details.organization_category,
              organization_name:
                this.state.application_details.development_partner_name,
              phone_number: this.state.application_details.phone,
              rgb_certificate:
                this.state.application_details.rgb_certificate_file_name,
              selected_country_code:
                this.state.application_details.country_code !== null
                  ? (JSON.parse(
                      this.state.application_details.country_code
                    ) as CountryCodeInterface)
                  : {
                      name: "Rwanda",
                      dial_code: "+250",
                      code: "RW",
                    },
              sub_sectors: this.state.application_details.subareas.map(
                (item) => ({
                  description: "",
                  id: item.sub_area_id,
                  intervention_area_id: item.intervention_area_id,
                  name: item.subarea_name,
                  status: item.status.toString(),
                })
              ) as unknown as GetSubSectorsInterface[],
              website_link: this.state.application_details.website_link,
              status: this.state.application_details.status,
              comment: this.state.application_details.comment,
              tin_number: this.state.application_details.tin_number,
            }}
            onUpdate={(file: File) => {
              this.setState({
                application_details: null,
                loading: false,
                submitting: false,
                view_success_message: true,
              });
              // this.LoadDetails();
            }}
            onSubmitUpdates={this.UpdateApplication}
          />
        )}
        {this.state.view_success_message === true && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ view_success_message: false })}
            backDropClose={false}
            widthSizeClass={ModalSize.medium}
            displayClose={false}
            padding={{
              title: undefined,
              body: true,
              footer: undefined,
            }}
          >
            <div className="flex flex-col w-full items-center justify-center animate__animated animate__fadeIn">
              <div>
                <MdCheckCircle className="text-6xl text-green-600" />
              </div>
              <div className="text-center text-lg text-green-700 font-bold">
                The application details has been updated successfully!
              </div>
              <div className="mt-3 flex flex-row items-center justify-center w-full gap-3">
                <Link to={"/"}>
                  <Button theme="default" className="">
                    Back to Home
                  </Button>
                </Link>
                <Button onClick={() => this.LoadDetails()} theme="accent">
                  View application details
                </Button>
              </div>
            </div>
          </Modal>
        )}
      </Fragment>
    );
  }
}
