import axios from "axios";
import { APP_URL } from "../config/app.config";
import { ActiveTabPermutation } from "../containers/RebAprovePermutation/RebAprovePermutation";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";

// action url
const url = APP_URL;

export interface SendPermutation__Interface {
  message: string;
  permutation_code: string;
}

export interface SendPermutation__Interface_Res {
  message: string;
  permutation_code: string;
}

export interface SendMutationData {
  role_id: string;
  user_one: string;
  user_two: string;
  position_code: string;
  qualification_id: string;
  school_one: string;
  school_two: string;
  academic_year_id: string;
}

export const FC_CreatePermutation = async (
  data: SendMutationData,
  callBack: (
    status: boolean,
    data: SendPermutation__Interface_Res | null,
    error: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.post<SendPermutation__Interface_Res>(
      `${url}/permutation/create`,
      data
    );

    callBack(true, res.data, "");
  } catch (error) {
    callBack(
      false,
      null,
      errorToText(error, () => {})
    );
  }
};

