export const FC_SearchPermutationByCode = async (
  code: string,
  callBack: (
    status: boolean,
    data: PermutationItem__Interface[],
    error: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.get<PermutationItem__Interface[]>(
      `${url}/permutation/usercode/${code}`
    );

    console.log({ res });

    callBack(true, res.data, "");
  } catch (error) {
    callBack(
      false,
      [],
      errorToText(error, () => {})
    );
  }
};

