export const UpdateDPMembershipApplicationFiles = async (
  data: UpdateFileDataSubmit,
  setResponse: (
    loading: boolean,
    res: { type: "success" | "error"; msg: string } | null
  ) => void
) => {
  console.log("Submitted: ", data);
  setResponse(true, null);
  const formData = new FormData();
  if (data.type === "RGB") {
    formData.append("rgb_certificate_file_name", data.file);
  } else {
    formData.append("implementation_plan_file_name", data.file);
  }
  try {
    const res = await axios.patch(
      `${TRAINING_APP_URL}/dpartner/${
        data.type === "RGB" ? "certificate" : "plan"
      }/${data.application_id}`,
      formData
    );
    if (res) {
      setResponse(false, {
        type: "success",
        msg: `${
          data.type === "RGB" ? "RGB Certificate" : "Implementation plan"
        } document updated successfully!`,
      });
    }
  } catch (error: any) {
    setResponse(false, { type: "error", msg: returnErrorToText(error) });
    console.log("Err: ", { ...error });
  }
};

