import { Action, ActionTypes, PermutationsStore } from "../actions";

// default state
const defaultState: PermutationsStore = {
  permutations: null,
};

export const permutationsReducer = (
  state: PermutationsStore = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.GET_PERMUTATIONS:
      return {
        ...state,
        permutations:
          state.permutations === null
            ? [action.payload]
            : [...state.permutations, action.payload],
      };
    case ActionTypes.LOGOUT:
      return defaultState;
    default:
      return state;
  }
};
