import React, { Component, Fragment } from "react";
import { AiOutlineCheckCircle } from "react-icons/ai";
import { BsArrowRight, BsFileEarmarkPdf } from "react-icons/bs";
import { MdRadioButtonUnchecked } from "react-icons/md";
import {
  Annex3ToBePlaced,
  Auth,
  Basic,
  DistrictList,
  Teaching_Level__Interface,
  VacantPostsByDistrictInterface,
} from "../../../actions";
import ExportToExcel from "../../../components/GenerateReport/ExportToExcel";
import Loading from "../../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { commaFy, search } from "../../../utils/functions";
import { DownloadToBePlaced } from "./DownloadToBePlaced";

interface ToBePlacedProps {
  Selected_district: DistrictList | undefined;
  basic: Basic;
  select_district: boolean;
  open_select_district: (status: boolean) => void;
  FC_LoadAnnex3: (district_code: string) => void;
  data: Annex3ToBePlaced | null;
  loading: boolean;
  auth: Auth;
}
interface ToBePlacedState {
  loading: boolean;
  selected_level: Teaching_Level__Interface | null;
  searchData: string;
  selected_row: VacantPostsByDistrictInterface | null;
  selected_qualification_name: string;
  chooseStatus: boolean;
  selectedStatus: "APPROVED" | "WAITING" | "REJECTED" | "ALL";
}

export class ToBePlaced extends Component<ToBePlacedProps, ToBePlacedState> {
  constructor(props: ToBePlacedProps) {
    super(props);

    this.state = {
      loading: false,
      selected_level: null,
      searchData: "",
      selected_row: null,
      selected_qualification_name: "",
      chooseStatus: false,
      selectedStatus: "ALL",
    };
  }

  DataByLevel = (level: Teaching_Level__Interface | null) => {
    let response =
      this.props.data === null
        ? []
        : level === null
        ? this.props.data.district_needed_posts
        : this.props.data.district_needed_posts.filter(
            (itm) => itm.teaching_level_id === level.teaching_level_id
          );
    response = response.filter(
      (itm) =>
        itm.status === this.state.selectedStatus ||
        this.state.selectedStatus === "ALL"
    );
    if (this.state.selected_qualification_name !== "") {
      response = response.filter(
        (itm) =>
          itm.qualification_name.toLocaleLowerCase() ===
          this.state.selected_qualification_name.toLocaleLowerCase()
      );
    }
    return search(
      response,
      this.state.searchData
    ) as VacantPostsByDistrictInterface[];
  };

  GetTotalByQualification = (qualification_name: string) => {
    let response =
      this.props.data === null
        ? []
        : this.state.selected_level === null
        ? this.props.data.district_needed_posts
        : this.props.data.district_needed_posts.filter(
            (itm) =>
              this.state.selected_level !== null &&
              itm.teaching_level_id ===
                this.state.selected_level.teaching_level_id
          );
    const filtered =
      this.props.data === null
        ? []
        : response.filter(
            (itm) =>
              itm.qualification_name.toLocaleLowerCase() ===
              qualification_name.toLocaleLowerCase()
          );
    return filtered.filter(
      (itm) =>
        itm.status === this.state.selectedStatus ||
        this.state.selectedStatus === "ALL"
    ).length;
  };

  GetPbByLevel = (level_name: string) => {
    if (this.props.data !== null) {
      const level_count = this.props.data.total_teach_level.find(
        (itm) =>
          itm.teaching_level_id.toLocaleLowerCase() ===
          level_name.toLocaleLowerCase()
      );
      if (level_count !== undefined) {
        return parseInt(level_count.total);
      }
    }
    return 0;
  };

  GetTeacherPB = (level_name: string, school_need_post_id: string) => {
    var filteredData =
      this.props.data === null
        ? []
        : level_name === null
        ? this.props.data.district_needed_posts
        : this.props.data.district_needed_posts.filter(
            (itm) =>
              itm.teaching_level.toLocaleLowerCase() ===
              level_name.toLocaleLowerCase()
          );
    var PDData = [];
    var iteration = 0;
    for (const item of filteredData) {
      iteration += 1;
      let pb = this.GetPbByLevel(item.teaching_level) + iteration;
      PDData.push({
        ...item,
        pb: pb,
      });
    }
    const selectedPB = PDData.find(
      (itm) => itm.school_need_post_id === school_need_post_id
    );
    if (selectedPB !== undefined) {
      return selectedPB.pb;
    }
    return 0;
  };

  GetTotalByLevelId = (level_id: string) => {
    const filtered =
      this.props.data === null
        ? []
        : this.props.data.district_needed_posts.filter(
            (itm) =>
              itm.teaching_level_id.toLocaleLowerCase() ===
              level_id.toLocaleLowerCase()
          );
    return filtered.filter(
      (itm) =>
        itm.status === this.state.selectedStatus ||
        this.state.selectedStatus === "ALL"
    ).length;
  };

  componentDidMount(): void {
    if (this.props.data === null && this.props.Selected_district) {
      this.props.FC_LoadAnnex3(this.props.Selected_district.district_code);
    }
  }
  render() {
    if (
      this.state.loading === true ||
      this.props.Selected_district === undefined ||
      this.props.data === null ||
      this.props.loading === true
    ) {
      return (
        <div className="flex flex-col items-center justify-center gap-3 w-full bg-gray-100 p-4 rounded-md mt-3">
          <Loading />
          <div className="text-xl font-light text-center">
            Loading ({this.props.Selected_district?.district_name}), please
            wait...
          </div>
        </div>
      );
    }
    return (
      <Fragment>
        <div>
          <div>
            <div className="flex flex-row items-center gap-3 justify-between -mt-2">
              <div className="flex flex-col mt-0">
                <div className="font-extrabold text-primary-800 text-xl">
                  List of vacant posts to be placed
                </div>
                <div
                  onClick={() => {
                    this.props.auth.role?.role_id !== "1" &&
                      this.props.auth.role?.role_id !== "2" &&
                      this.props.auth.role?.role_id !== "3" &&
                      this.props.open_select_district(
                        !this.props.select_district
                      );
                  }}
                  className="flex flex-row items-center gap-3 text-base rounded-md bg-accent-400 cursor-pointer border border-accent-400 hover:bg-accent-400 hover:border-accent-800 hover:text-accent-900 w-max px-3 py-1"
                >
                  <span className="font-light">District: </span>
                  <span className="font-bold">
                    {this.props.Selected_district.district_code}-
                    {this.props.Selected_district.district_name}
                  </span>
                </div>
              </div>
              {/* Right side: qualifications */}
              <div className="flex flex-row items-center justify-end gap-2">
                {this.props.basic.qualifications.map((item, i) => (
                  <div
                    key={i + 1}
                    className={`rounded px-3 py-1 cursor-pointer ${
                      this.state.selected_qualification_name ===
                      item.qualification_name
                        ? "border bg-primary-100 border-primary-800 text-primary-800"
                        : "bg-gray-100 hover:bg-accent-400 hover:text-accent-900"
                    } group`}
                    onClick={() => {
                      this.setState({
                        selected_qualification_name:
                          this.state.selected_qualification_name.toLocaleLowerCase() !==
                          item.qualification_name.toLocaleLowerCase()
                            ? item.qualification_name
                            : "",
                      });
                    }}
                  >
                    <div className="font-light ">{item.qualification_name}</div>
                    <div
                      className={`font-extrabold ${
                        this.state.selected_qualification_name ===
                        item.qualification_name
                          ? "text-primary-800"
                          : "text-primary-800 group-hover:text-accent-900"
                      }`}
                    >
                      {commaFy(
                        this.GetTotalByQualification(item.qualification_name)
                      )}
                    </div>
                  </div>
                ))}
                <div
                  onClick={() => {
                    this.setState({
                      selected_qualification_name: "",
                    });
                  }}
                  className="bg-primary-100 cursor-pointer hover:bg-accent-400 hover:text-accent-900 group rounded px-3 py-1"
                >
                  <div className="font-light ">Total</div>
                  <div className="font-extrabold text-primary-800 group-hover:text-accent-900">
                    {commaFy(
                      this.props.data === null
                        ? 0
                        : this.state.selected_level === null
                        ? this.props.data.district_needed_posts.filter(
                            (itm) =>
                              itm.status === this.state.selectedStatus ||
                              this.state.selectedStatus === "ALL"
                          ).length
                        : this.GetTotalByLevelId(
                            this.state.selected_level.teaching_level_id
                          )
                    )}
                  </div>
                </div>
              </div>
            </div>
            {/* Filters */}
            <div className="flex flex-row items-center gap-2 justify-between mt-3">
              <div className="flex flex-row items-center gap-2">
                {this.props.basic.teaching_levels !== null &&
                  this.props.basic.teaching_levels.map((item, i) => (
                    <div
                      key={i + 1}
                      onClick={() =>
                        this.setState({
                          selected_level:
                            this.state.selected_level?.teaching_level_id ===
                            item.teaching_level_id
                              ? null
                              : item,
                        })
                      }
                      className={`flex flex-row items-center gap-1 cursor-pointer rounded-md px-1 py-1 text-sm ${
                        this.state.selected_level?.teaching_level_id ===
                        item.teaching_level_id
                          ? "bg-primary-800 text-white animate__animated animate__fadeIn"
                          : "hover:bg-primary-100 border border-gray-300"
                      }`}
                    >
                      <div>
                        {this.state.selected_level?.teaching_level_id ===
                        item.teaching_level_id ? (
                          <AiOutlineCheckCircle className="text-xl" />
                        ) : (
                          <MdRadioButtonUnchecked className="text-xl text-gray-500" />
                        )}
                      </div>
                      <span>{item.teaching_level.toUpperCase()}</span>
                      <div
                        className={`px-1 rounded-md ${
                          this.state.selected_level?.teaching_level_id ===
                          item.teaching_level_id
                            ? "bg-white text-primary-900"
                            : "bg-primary-800 text-white"
                        } text-sm`}
                      >
                        {commaFy(
                          this.GetTotalByLevelId(item.teaching_level_id)
                        )}
                      </div>
                    </div>
                  ))}
              </div>
              <div className="flex flex-row items-center justify-end gap-2">
                <input
                  type="text"
                  placeholder="Search..."
                  value={this.state.searchData}
                  onChange={(e) =>
                    this.setState({ searchData: e.target.value })
                  }
                  className="px-3 py-2 text-sm font-light rounded border border-primary-800"
                />
                <div>
                  <ExportToExcel
                    fileData={this.DataByLevel(this.state.selected_level).map(
                      (item, i) => ({
                        No: `${i + 1}`,
                        PB: `${this.GetTeacherPB(
                          item.teaching_level,
                          item.school_need_post_id
                        )}`,
                        DistrictCode: `${item.district_code}`,
                        DistrictName: `${item.district_name}`,
                        SchoolCode: `${item.school_code}`,
                        SchoolName: `${item.school_name}`,
                        PositionCode: `${item.occupation}`,
                        PositionName: `${item.position_name}`,
                        Specialization: `${item.specialization_name}`,
                        TeachingLevel: `${item.teaching_level}`,
                        Qualification: `${item.qualification_name}`,
                        NeededPosts: `${item.need_post}`,
                        TotalPeriods: `${item.total_period}`,
                      })
                    )}
                    fileName={`${this.props.Selected_district.district_name}-Annex 3: Vacant posts to be placed`}
                    btnName="Download Excel"
                  />
                </div>
                <div>
                  <div
                    onClick={() =>
                      this.props.Selected_district !== undefined &&
                      this.props.data !== null &&
                      DownloadToBePlaced(
                        {
                          district_needed_posts: this.DataByLevel(
                            this.state.selected_level
                          ),
                          total_teach_level: this.props.data.total_teach_level,
                        },
                        this.props.basic.teaching_levels,
                        this.props.Selected_district,
                        (loading: boolean) =>
                          this.setState({ loading: loading })
                      )
                    }
                    className="flex flex-row items-center gap-2 justify-center px-3 py-1 pl-1 rounded bg-accent-800 hover:bg-accent-900 border border-accent-800 hover:border-accent-900 font-bold cursor-pointer hover:text-white"
                  >
                    <div>
                      <BsFileEarmarkPdf className="text-xl" />
                    </div>
                    <span>Download PDF</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="mt-5">
              {/* Table here */}
              <div className="w-full overflow-x-auto">
                <table className="w-full text-left">
                  <thead>
                    <tr className="text-xs bg-primary-100 text-primary-800">
                      <th className="px-2 border border-blue-300 py-2">#</th>
                      <th className="px-2 border border-blue-300 py-2">PB</th>
                      <th className="px-2 border border-blue-300 py-2">
                        School
                      </th>
                      <th className="px-2 border border-blue-300 py-2">
                        Qualification
                      </th>
                      <th className="px-2 border border-blue-300 py-2">
                        Position code
                      </th>
                      <th className="px-2 border border-blue-300 py-2">
                        Position
                      </th>
                      <th className="px-2 border border-blue-300 py-2">
                        Specialization
                      </th>
                      <th className="px-2 border border-blue-300 py-2">
                        Teaching level
                      </th>

                      <th className="px-2 border border-blue-300 py-2">
                        Need posts
                      </th>
                      <th className="px-2 border border-blue-300 py-2">
                        Total period
                      </th>
                      <th className="px-2 border border-blue-300 py-2 relative">
                        <div
                          onClick={() => {
                            this.setState({
                              chooseStatus: !this.state.chooseStatus,
                            });
                          }}
                          className="flex flex-row items-center justify-between gap-2 w-max rounded bg-accent-400 border border-yellow-400 px-3 py-1 cursor-pointer"
                        >
                          <span>
                            {this.state.selectedStatus === "ALL"
                              ? "Status"
                              : this.state.selectedStatus}
                          </span>
                          <div>
                            <BsArrowRight className="text-xl" />
                          </div>
                        </div>
                        {this.state.chooseStatus === true && (
                          <div className="bg-white shadow-lg rounded p-3 absolute w-56 z-50 right-2 animate__animated animate__zoomIn animate__faster border border-gray-300">
                            <div className="font-extrabold mb-3">
                              Filter by Status
                            </div>
                            <div className="flex flex-col w-full gap-2">
                              <div
                                onClick={() =>
                                  this.setState({
                                    selectedStatus: "ALL",
                                    chooseStatus: false,
                                  })
                                }
                                className={`cursor-pointer px-3 py-2 rounded ${
                                  this.state.selectedStatus === "ALL"
                                    ? "bg-primary-800 text-white font-bold"
                                    : "bg-gray-100 hover:bg-primary-100 text-gray-700 hover:text-primary-800"
                                }`}
                              >
                                ALL STATUSES
                              </div>
                              <div
                                onClick={() =>
                                  this.setState({
                                    selectedStatus: "WAITING",
                                    chooseStatus: false,
                                  })
                                }
                                className={`cursor-pointer px-3 py-2 rounded ${
                                  this.state.selectedStatus === "WAITING"
                                    ? "bg-primary-800 text-white font-bold"
                                    : "bg-gray-100 hover:bg-primary-100 text-gray-700 hover:text-primary-800"
                                }`}
                              >
                                WAITING
                              </div>
                              <div
                                onClick={() =>
                                  this.setState({
                                    selectedStatus: "APPROVED",
                                    chooseStatus: false,
                                  })
                                }
                                className={`cursor-pointer px-3 py-2 rounded ${
                                  this.state.selectedStatus === "APPROVED"
                                    ? "bg-primary-800 text-white font-bold"
                                    : "bg-gray-100 hover:bg-primary-100 text-gray-700 hover:text-primary-800"
                                }`}
                              >
                                APPROVED
                              </div>
                              <div
                                onClick={() =>
                                  this.setState({
                                    selectedStatus: "REJECTED",
                                    chooseStatus: false,
                                  })
                                }
                                className={`cursor-pointer px-3 py-2 rounded ${
                                  this.state.selectedStatus === "REJECTED"
                                    ? "bg-primary-800 text-white font-bold"
                                    : "bg-gray-100 hover:bg-primary-100 text-gray-700 hover:text-primary-800"
                                }`}
                              >
                                REJECTED
                              </div>
                            </div>
                          </div>
                        )}
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {this.DataByLevel(this.state.selected_level).length ===
                    0 ? (
                      <tr>
                        <td
                          colSpan={11}
                          className="text-center text-xl font-light p-5"
                        >
                          No data found!
                        </td>
                      </tr>
                    ) : (
                      this.DataByLevel(this.state.selected_level).map(
                        (item, i) => (
                          <tr
                            key={i + 1}
                            className="text-sm animate__animated animate__fadeIn hover:bg-accent-400 hover:text-accent-900 cursor-pointer group"
                            onClick={() =>
                              this.setState({ selected_row: item })
                            }
                          >
                            <td className="px-2 py-1 border border-gray-300 text-center">
                              {i + 1}
                            </td>
                            <td className="px-2 py-1 border border-gray-300 text-center">
                              {this.GetTeacherPB(
                                item.teaching_level,
                                item.school_need_post_id
                              )}
                            </td>

                            <td className="px-2 py-1 border border-gray-300">
                              ({item.school_code}) {item.school_name}
                            </td>
                            <td className="px-2 py-1 border border-gray-300 text-center">
                              {item.qualification_name}
                            </td>
                            <td className="px-2 py-1 border border-gray-300 text-center">
                              {item.occupation}
                            </td>
                            <td className="px-2 py-1 border border-gray-300">
                              {item.position_name}
                            </td>
                            <td className="px-2 py-1 border border-gray-300">
                              {item.specialization_name}
                            </td>
                            <td className="px-2 py-1 border border-gray-300">
                              {item.teaching_level.toLocaleUpperCase()}
                            </td>

                            <td className="px-2 py-1 border border-gray-300 text-center">
                              {item.need_post}
                            </td>
                            <td className="px-2 py-1 border border-gray-300 text-center">
                              <div className="rounded-md p-1 bg-primary-100 font-extrabold cursor-pointer group-hover:bg-primary-800 group-hover:text-white w-max">
                                {item.total_period}
                              </div>
                            </td>
                            <td className="px-2 py-1 border border-gray-300">
                              {item.status}
                            </td>
                          </tr>
                        )
                      )
                    )}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
        {this.state.selected_row !== null && (
          <Modal
            backDrop={true}
            close={() => this.setState({ selected_row: null })}
            backDropClose={true}
            widthSizeClass={ModalSize.large}
            displayClose={true}
            padding={{
              title: true,
              body: true,
            }}
            theme={Themes.default}
            title={`${this.state.selected_row.position_name} details`}
          >
            <div>
              <table className="w-full text-sm text-left">
                <tbody>
                  <tr className="bg-primary-100">
                    <th className="px-2 py-2 border border-blue-300 text-primary-800">
                      #
                    </th>
                    <th className="px-2 py-2 border border-blue-300 text-primary-800">
                      Combination name
                    </th>
                    <th className="px-2 py-2 border border-blue-300 text-primary-800">
                      Grade name
                    </th>
                    <th className="px-2 py-2 border border-blue-300 text-primary-800">
                      Course name
                    </th>
                    <th className="px-2 py-2 border border-blue-300 text-primary-800">
                      Teaching period
                    </th>
                  </tr>
                </tbody>
                <tbody>
                  {this.state.selected_row.subjects.map((item, i) => (
                    <tr
                      key={i + 1}
                      className="animate__animated animate__fadeIn"
                    >
                      <td className="px-2 py-2 border border-gray-300 font-light">
                        {i + 1}
                      </td>
                      <td className="px-2 py-2 border border-gray-300 font-light">
                        {item.combination_name}
                      </td>
                      <td className="px-2 py-2 border border-gray-300 font-light">
                        {item.grade_name}
                      </td>
                      <td className="px-2 py-2 border border-gray-300 font-light">
                        {item.course_name}
                      </td>
                      <td className="px-2 py-2 border border-gray-300 font-light">
                        {item.teaching_period}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
              <div className="flex flex-row items-center justify-between mt-3">
                <div className="text-xl">
                  <div className="font-light text-sm">Total hours tough: </div>
                  <div className="font-extrabold">
                    {this.state.selected_row.total_period}
                  </div>
                </div>
                <div
                  onClick={() => this.setState({ selected_row: null })}
                  className="px-3 py-2 rounded bg-gray-200 font-bold hover:bg-gray-300 w-max cursor-pointer text-sm"
                >
                  Continue
                </div>
              </div>
            </div>
          </Modal>
        )}
      </Fragment>
    );
  }
}
