import React, { useState } from "react";
import { FaCheckCircle } from "react-icons/fa";
import { ImRadioUnchecked } from "react-icons/im";
import { DistrictList } from "../../../actions";
import Loading from "../../../components/Loading/Loading";
import { search } from "../../../utils/functions";

interface SelectDistrictProps {
  districtList: DistrictList[] | null;
  selected_district_code: string;
  onSelect: (selected_district: DistrictList) => void;
}

export const SelectDistrict = (props: SelectDistrictProps) => {
  const [searchData, setSearchData] = useState<string>("");
  return (
    <div>
      {props.districtList === null ? (
        <div className="bg-white w-full p-3 rounded border flex flex-col gap-3 items-center justify-center">
          <Loading />
          <div>Loading, Please wait...</div>
        </div>
      ) : (
        <>
          <div className="mb-4">
            <div className="text-gray-700">Search school</div>
            <input
              type="text"
              value={searchData}
              onChange={(e) => setSearchData(e.target.value)}
              className="px-3 py-2 rounded border border-gray-400 w-full"
            />
          </div>
          {(search(props.districtList, searchData) as DistrictList[]).length ===
          0 ? (
            <div>
              <div>
                <div className="bg-gray-200 p-5 rounded-md text-2xl font-extrabold text-center text-gray-700">
                  No result found!
                </div>
              </div>
            </div>
          ) : (
            (search(props.districtList, searchData) as DistrictList[]).map(
              (item, i) => (
                <div
                  key={i + 1}
                  onClick={() => props.onSelect(item)}
                  className={`mb-2 rounded ${
                    item.district_code === props.selected_district_code
                      ? "bg-accent-400 text-accent-900 font-extrabold border border-accent-900"
                      : "bg-gray-100 hover:bg-accent-400 hover:text-accent-900 border border-gray-100 hover:border-accent-900"
                  } px-3 py-2 flex flex-row items-center rounded-md gap-3 justify-between cursor-pointer`}
                >
                  <div className="text-lg">{item.district_name}</div>
                  <div>
                    {item.district_code === props.selected_district_code ? (
                      <FaCheckCircle className="text-2xl text-accent-900" />
                    ) : (
                      <ImRadioUnchecked className="text-2xl text-gray-400 group-hover:text-gray-700" />
                    )}
                  </div>
                </div>
              )
            )
          )}
        </>
      )}
    </div>
  );
};
