import React, { Component } from "react";
import { BsFillInfoCircleFill } from "react-icons/bs";
import { FaRegEdit } from "react-icons/fa";
import { HiShieldCheck } from "react-icons/hi";
import { IoIosCloseCircle } from "react-icons/io";
import { MdClose } from "react-icons/md";
import { RiTimerFlashFill } from "react-icons/ri";
import { DPMembershipApplicationStatus } from "../../../actions/dp_membership_application";
import { GetSubSectorsInterface } from "../../../actions/training_settings_create_edit";
import { CountryCodeInterface } from "./data";
import { EditFiles } from "./EditFiles";
import FilePreview from "./FilePreview";
import { DPMembershipApplicationStepsList } from "./Steps";

interface PreviewApplicationProps {
  application_id: string;
  loading: boolean;
  organization_category_id: string;
  organization_name: string;
  phone_number: string;
  email: string;
  location: string;
  rgb_certificate: File | null;
  rgb_certificate_filename: string;
  implementation_plan: File | null;
  implementation_plan_filename: string;
  selected_country_code: CountryCodeInterface;
  added_sub_sectors: string[];
  organization_category_name: string;
  website_link: string;
  sub_sectors: GetSubSectorsInterface[];
  status: DPMembershipApplicationStatus;
  comment: string;
  tin_number: string;
  setStep: (step: DPMembershipApplicationStepsList) => void;
  onClose: () => void;
  action: "create" | "edit";
  onUpdateFile: (file: File) => void;
}
interface PreviewApplicationState {
  view_files: boolean;
}

export class PreviewApplication extends Component<
  PreviewApplicationProps,
  PreviewApplicationState
> {
  constructor(props: PreviewApplicationProps) {
    super(props);

    this.state = {
      view_files: false,
    };
  }
  GetStatusIcon = (status: DPMembershipApplicationStatus) => {
    if (status === DPMembershipApplicationStatus.PENDING) {
      return <RiTimerFlashFill className="text-5xl" />;
    }
    if (status === DPMembershipApplicationStatus.APPROVED) {
      return <HiShieldCheck className="text-5xl" />;
    }
    if (status === DPMembershipApplicationStatus.REJECTED) {
      return <IoIosCloseCircle className="text-5xl" />;
    } else {
      return <BsFillInfoCircleFill className="text-5xl" />;
    }
  };
  GetStatusColor = (status: DPMembershipApplicationStatus) => {
    if (status === DPMembershipApplicationStatus.PENDING) {
      return "gray-500";
    }
    if (status === DPMembershipApplicationStatus.APPROVED) {
      return "green-600";
    }
    if (status === DPMembershipApplicationStatus.REJECTED) {
      return "red-600";
    } else {
      return "accent-900";
    }
  };
  GetFeedBackMsg = (status: DPMembershipApplicationStatus) => {
    return (
      <div
        className={`bg-accent-400 rounded-md my-5 border border-${this.GetStatusColor(
          status
        )} flex flex-col md:flex-row items-center gap-4 p-3 animate__animated animate__zoomIn`}
      >
        <div className={`text-${this.GetStatusColor(status)}`}>
          {this.GetStatusIcon(status)}
        </div>
        <div className="flex flex-col">
          <div className="flex flex-cols items-center gap-2 mt-2">
            <span className="text-gray-600 text-lg font-bold">
              Current status
            </span>
            <div className="flex flex-row items-center">
              <div
                className={`bg-${this.GetStatusColor(
                  status
                )} text-white px-2 py-1 font-extrabold rounded-md text-sm`}
              >
                {this.props.status}
              </div>
            </div>
          </div>
          {this.props.comment !== "" && this.props.comment !== null && (
            <>
              <div className="text-gray-500">Feedback:</div>
              <div className="font-bold -mt-1">{this.props.comment}</div>
            </>
          )}
        </div>
      </div>
    );
  };
  render() {
    return (
      <div
        className="p-3 relative"
        style={{ height: "80%", overflowY: "auto" }}
      >
        <div className="text-xl font-extrabold bg-primary-100 text-primary-800 w-full mb-3 p-2 pl-3 rounded">
          <div className="flex flex-row items-center justify-between gap-2">
            <div>Registration preview</div>
            <div>
              <div
                onClick={this.props.onClose}
                className="bg-white flex items-center justify-center h-10 w-10 rounded-full cursor-pointer"
              >
                <MdClose className="text-3xl" />
              </div>
            </div>
          </div>
        </div>
        {/* Comment from the validator */}
        {this.props.action === "edit" && this.GetFeedBackMsg(this.props.status)}
        {/* Step 1 */}
        <div className="mb-5">
          <div className="flex flex-row items-center justify-between gap-2">
            <div className="flex flex-row items-center gap-2">
              <div className="h-8 w-8 rounded-full bg-accent-400 border border-yellow-400 text-xl flex items-center justify-center font-light">
                1
              </div>
              <div className="text-xl font-light">Organization category</div>
            </div>
            <div>
              <div
                onClick={() =>
                  this.props.setStep(DPMembershipApplicationStepsList.Step1)
                }
                className="bg-accent-400 text-accent-900 hover:text-white hover:bg-accent-900 px-3 py-1 text-base rounded cursor-pointer flex flex-row items-center justify-center gap-2"
              >
                <div>
                  <FaRegEdit className="text-xl" />
                </div>
                <span>Edit</span>
              </div>
            </div>
          </div>
          <div className="p-3 bg-gray-100 rounded-md mt-2">
            <div className="flex flex-row items-center gap-2">
              <span className="text-gray-500">Organization</span>
              <span className="font-bold">{this.props.organization_name}</span>
            </div>
            <div className="flex flex-row items-center gap-2">
              <span className="text-gray-500">Category</span>
              <span className="font-bold">
                {this.props.organization_category_name}
              </span>
            </div>
          </div>
        </div>
        {/* Step 2 */}
        <div className="mb-5">
          <div className="flex flex-row items-center justify-between gap-2">
            <div className="flex flex-row items-center gap-2">
              <div className="h-8 w-8 rounded-full bg-accent-400 border border-yellow-400 text-xl flex items-center justify-center font-light">
                2
              </div>
              <div className="text-xl font-light">Membership requirements</div>
            </div>
            <div>
              <div
                onClick={() =>
                  this.props.setStep(DPMembershipApplicationStepsList.Step2)
                }
                className="bg-accent-400 text-accent-900 hover:text-white hover:bg-accent-900 px-3 py-1 text-base rounded cursor-pointer flex flex-row items-center justify-center gap-2"
              >
                <div>
                  <FaRegEdit className="text-xl" />
                </div>
                <span>Edit</span>
              </div>
            </div>
          </div>
          <div className="p-3 bg-gray-100 rounded-md mt-2">
            <div className="flex flex-row items-center gap-2">
              <span className="text-gray-500">Organization Phone number</span>
              <span className="font-bold">{this.props.phone_number}</span>
            </div>
            <div className="flex flex-row items-center gap-2">
              <span className="text-gray-500">Organization TIN number</span>
              <span className="font-bold">{this.props.tin_number}</span>
            </div>
            <div className="flex flex-row items-center gap-2">
              <span className="text-gray-500">Organization Email</span>
              <span className="font-bold">{this.props.email}</span>
            </div>
            <div className="flex flex-row items-center gap-2">
              <span className="text-gray-500">Organization website</span>
              <span className="font-bold">{this.props.website_link}</span>
            </div>
            <div className="flex flex-row items-center gap-2">
              <span className="text-gray-500">Organization location</span>
              <span className="font-bold">{this.props.location}</span>
            </div>
            <div
              onClick={() =>
                this.setState({ view_files: !this.state.view_files })
              }
              className="bg-accent-400 border border-yellow-500 mt-3 text-sm font-bold rounded-md px-2 py-1 w-max cursor-pointer hover:bg-accent-800 hover:text-black"
            >
              {this.state.view_files === true
                ? "Hide attached files"
                : "Open attached files"}
            </div>
            {this.state.view_files === true && (
              <div className="flex flex-row items-center gap-2 mt-3 bg-white p-2 rounded-md">
                <div className="w-full">
                  <div className="mb-2 font-bold">RGB Certificate</div>
                  {this.props.rgb_certificate === null ? (
                    this.props.action === "edit" &&
                    this.props.application_id !== "" ? (
                      <div
                        className="border rounded-md"
                        style={{ height: "300px", overflowY: "auto" }}
                      >
                        <EditFiles
                          file_path={this.props.rgb_certificate_filename}
                          title={"RGB Certificate"}
                          onClose={() => {}}
                          onUpdate={this.props.onUpdateFile}
                          type={"RGB"}
                          application_id={this.props.application_id}
                          preview_only={true}
                        />
                      </div>
                    ) : (
                      <div className="font-light">File is not valid</div>
                    )
                  ) : (
                    <div style={{ height: "300px", overflowY: "auto" }}>
                      <FilePreview
                        selectedFile={this.props.rgb_certificate}
                        onClose={() => {}}
                        isComponent={true}
                      />
                    </div>
                  )}
                </div>
                <div className="w-full">
                  <div className="mb-2 font-bold">Implementation plan</div>
                  {this.props.implementation_plan === null ? (
                    this.props.action === "edit" &&
                    this.props.application_id !== "" ? (
                      <div
                        className="border rounded-md"
                        style={{ height: "300px", overflowY: "auto" }}
                      >
                        <EditFiles
                          file_path={this.props.implementation_plan_filename}
                          title={"Implementation plan"}
                          onClose={() => {}}
                          onUpdate={this.props.onUpdateFile}
                          type={"IMPLEMENTATION"}
                          application_id={this.props.application_id}
                          preview_only={true}
                        />
                      </div>
                    ) : (
                      <div className="font-light">File is not valid</div>
                    )
                  ) : (
                    <div style={{ height: "300px", overflowY: "auto" }}>
                      <FilePreview
                        isComponent={true}
                        selectedFile={this.props.implementation_plan}
                        onClose={() => {}}
                      />
                    </div>
                  )}
                </div>
              </div>
            )}
          </div>
        </div>
        {/* Step 1 */}
        <div className="mb-5">
          <div className="flex flex-row items-center justify-between gap-2">
            <div className="flex flex-row items-center gap-2">
              <div className="h-8 w-8 rounded-full bg-accent-400 border border-yellow-400 text-xl flex items-center justify-center font-light">
                3
              </div>
              <div className="text-xl font-light">
                Sub areas of intervention
              </div>
            </div>
            <div>
              <div
                onClick={() =>
                  this.props.setStep(DPMembershipApplicationStepsList.Step3)
                }
                className="bg-accent-400 text-accent-900 hover:text-white hover:bg-accent-900 px-3 py-1 text-base rounded cursor-pointer flex flex-row items-center justify-center gap-2"
              >
                <div>
                  <FaRegEdit className="text-xl" />
                </div>
                <span>Edit</span>
              </div>
            </div>
          </div>
          <div className="p-3 bg-gray-100 rounded-md mt-2 mb-24">
            {/* Sub areas here */}
            {this.props.sub_sectors.length === 0 ? (
              <div className="font-light">No sub areas added!</div>
            ) : (
              this.props.sub_sectors.map((item, i) => (
                <div
                  key={i + 1}
                  className={`flex flex-row items-center justify-between bg-white text-black rounded-md p-2 animate__animated animate__fadeIn mb-2`}
                >
                  <div className="flex flex-row items-center gap-2">
                    <div>
                      <div className="h-8 w-8 rounded-full bg-white font-light text-2xl flex items-center justify-center">
                        {i + 1}
                      </div>
                    </div>
                    <div className="flex flex-col">
                      <div className="text-base font-bold">{item.name}</div>
                      <div className="text-xs -mt-1">{item.description}</div>
                    </div>
                  </div>
                  <div></div>
                </div>
              ))
            )}
          </div>
        </div>
      </div>
    );
  }
}
