import axios from "axios";
import React, { Component, ReactNode } from "react";
import { IoMdLocate } from "react-icons/io";
import { MdSupportAgent } from "react-icons/md";
import { RiErrorWarningFill, RiSearchLine } from "react-icons/ri";
import { Link } from "react-router-dom";
import Loading from "../../components/Loading/Loading";
import { SERVER_URL } from "../../config/app.config";
import { returnErrorToText, search } from "../../utils/functions";

const CallTo = (props: { phone: string; children: ReactNode }) => {
  return <a href={`tel:${props.phone}`}>{props.children}</a>;
};

interface SupportStaffInterface {
  names: string;
  phone: string;
  districts: string[];
  category: "SUPPORT" | "SUPERVISOR";
}

interface SupportStaffPageProps {}
interface SupportStaffPageState {
  loading: boolean;
  supportStaff: SupportStaffInterface[] | null;
  error: string;
  search_data: string;
}

class SupportStaffPage extends Component<
  SupportStaffPageProps,
  SupportStaffPageState
> {
  constructor(props: SupportStaffPageProps) {
    super(props);

    this.state = {
      loading: true,
      supportStaff: null,
      error: "",
      search_data: "",
    };
  }
  GetSupportStaff = async () => {
    this.setState({ loading: true });
    try {
      const response = await axios.get<SupportStaffInterface[]>(
        `${SERVER_URL}/support_staff.json`
      );
      if (response) {
        this.setState({ loading: false, supportStaff: response.data });
      } else {
        this.setState({
          loading: false,
          supportStaff: [],
          error: "Can not load support staff, please try again",
        });
      }
    } catch (error: any) {
      this.setState({
        loading: false,
        supportStaff: [],
        error: returnErrorToText(error),
      });
      console.log("Err: ", { ...error });
    }
  };
  GetFilteredData = (
    temp_data: SupportStaffInterface[]
  ): SupportStaffInterface[] => {
    var response: SupportStaffInterface[] = [];
    response = temp_data.filter((itm) => itm.category !== "SUPERVISOR");
    return search(response, this.state.search_data);
  };

  componentDidMount = () => {
    this.state.supportStaff === null && this.GetSupportStaff();
  };
  render() {
    if (this.state.loading === true || this.state.supportStaff === null) {
      return (
        <div className="container mx-auto px-4">
          <div className="w-full h-full flex items-center justify-center pt-5 md:pt-32">
            <div className="w-full md:w-2/3 bg-white rounded-md p-6 py-16">
              <div className="flex flex-col items-center justify-center">
                <div>
                  <Loading />
                </div>
                <div className="text-xl text-gray-400 font-bold mt-3 animate__animated animate__fadeIn animate__infinite">
                  Loading, please wait...
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    }
    if (this.state.error !== "") {
      return (
        <div className="container mx-auto px-4">
          <div className="w-full h-full flex items-center justify-center pt-5 md:pt-32">
            <div className="w-full md:w-2/3 bg-white rounded-md p-6">
              <div className="flex flex-col items-center justify-center">
                <div>
                  <div className="text-7xl text-red-600">
                    <RiErrorWarningFill />
                  </div>
                </div>
                <div className="text-2xl font-bold mt-3">
                  {this.state.error}
                </div>
                <div className="text-gray-500 mt-2">
                  Click the following button to try again
                </div>
                <div className="flex flex-row items-center gap-2 w-full justify-center mt-6">
                  <Link
                    to={"/"}
                    className="bg-gray-100 hover:bg-gray-200 px-4 py-2 rounded w-max font-semibold cursor-pointer"
                  >
                    Back to home
                  </Link>
                  <div
                    onClick={() => this.GetSupportStaff()}
                    className="bg-green-500 hover:bg-green-700 text-white px-4 py-2 rounded w-max font-semibold cursor-pointer"
                  >
                    Reload the page
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    }
    if (this.state.supportStaff.length === 0) {
      return (
        <div className="container mx-auto px-4">
          <div className="w-full h-full flex items-center justify-center pt-5 md:pt-32">
            <div className="w-full md:w-2/3 bg-white rounded-md p-6">
              <div className="flex flex-col items-center justify-center">
                <div>
                  <div className="text-7xl text-gray-300">
                    <RiSearchLine />
                  </div>
                </div>
                <div className="text-2xl font-bold">No support staff found</div>

                <div className="flex flex-row items-center gap-2 w-full justify-center mt-6">
                  <Link
                    to={"/"}
                    className="bg-gray-100 hover:bg-gray-200 px-4 py-2 rounded w-max font-semibold cursor-pointer"
                  >
                    Back to home
                  </Link>
                  <div
                    onClick={() => this.GetSupportStaff()}
                    className="bg-green-500 hover:bg-green-700 text-white px-4 py-2 rounded w-max font-semibold cursor-pointer"
                  >
                    Reload the page
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    }
    return (
      <div className="container mx-auto px-4 pt-4">
        <div className="mb-3 bg-white rounded-md p-3">
          <div className="flex flex-row items-center justify-between gap-2">
            <div className="flex flex-row items-center gap-2 w-full">
              <div className="hidden md:block">
                <div className="flex items-center justify-center h-14 w-14 rounded-md bg-accent-400 text-accent-900">
                  <IoMdLocate className="text-4xl" />
                </div>
              </div>
              <div className="text-center md:text-left">
                <div className="font-bold text-xl md:text-2xl">
                  Allocation of TMIS support staff
                </div>
                <div className="text-gray-500 text-sm md:text-base">
                  Check for your assigned district support staff
                </div>
              </div>
            </div>
            <Link
              to={"/"}
              className="hidden md:block bg-gray-100 rounded px-3 py-2 text-gray-600 font-semibold hover:text-primary-800 hover:bg-primary-100"
            >
              <span className="truncate">Bak to home</span>
            </Link>
          </div>
          <div className="flex flex-row items-center w-full mt-3">
            <div className="w-full relative">
              <input
                placeholder="Search by district name"
                type="search"
                className="w-full border border-gray-300 bg-gray-100 rounded-md px-3 py-2 pl-10 text-primary-800"
                value={this.state.search_data}
                onChange={(e) => this.setState({ search_data: e.target.value })}
              />
              <RiSearchLine className="text-2xl absolute top-2 left-2" />
            </div>
          </div>
        </div>
        <div>
          {this.GetFilteredData(this.state.supportStaff).length === 0 ? (
            <div className="py-6 px-2 bg-white text-xl text-center rounded-md animate__animated animate__shakeX">
              No result found for{" "}
              <span className="font-bold text-accent-900">
                {this.state.search_data}
              </span>
            </div>
          ) : (
            this.GetFilteredData(this.state.supportStaff).map((item, i) => (
              <div
                key={i + 1}
                className="grid grid-cols-12 gap-3 w-full bg-white border border-primary-600 md:border-white hover:border-primary-300 rounded-md mb-3 p-2 hover:text-primary-800 group"
              >
                <div className="col-span-12 md:col-span-6 lg:col-span-5">
                  {item.districts.map((district, d) => (
                    <div
                      key={d + 1}
                      className="rounded-md bg-gray-100 group-hover:bg-primary-100 px-3 py-2 mb-2"
                    >
                      {district}
                    </div>
                  ))}
                </div>
                <div className="col-span-12 md:col-span-6 lg:col-span-7">
                  <div className="flex flex-row items-center h-full">
                    <div className="flex flex-row gap-3 md:pl-10">
                      <div>
                        <div className="flex flex-row items-center justify-center h-16 w-16 bg-gray-100 group-hover:bg-primary-100 text-gray-300 group-hover:text-primary-800 rounded-full">
                          <MdSupportAgent className="text-4xl" />
                        </div>
                      </div>
                      <div>
                        <div className="text-xl text-gray-600 group-hover:text-primary-800">
                          {item.names}
                        </div>
                        <div className="flex flex-row text-lg gap-2 group-hover:text-green-600 font-bold">
                          <CallTo phone={item.phone}>
                            Contact: {item.phone}
                          </CallTo>
                        </div>
                        <div className="mt-2 mb-3 md:mb-0">
                          <CallTo phone={item.phone}>
                            <div className="bg-primary-100 md:bg-gray-50 text-primary-800 md:text-gray-400 px-3 py-2 rounded-md text-sm font-bold cursor-pointer group-hover:text-white group-hover:bg-primary-800 w-max">
                              <span>Call me now</span>
                            </div>
                          </CallTo>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))
          )}
          {/* Supervisor */}
          <div className="bg-white mt-3 rounded-md p-3 md:p-6 mb-20">
            {this.state.supportStaff
              .filter((itm) => itm.category === "SUPERVISOR")
              .map((supervisor, s) => (
                <div className="h-full">
                  <div className="font-extrabold text-2xl border-b pb-2 mb-5">
                    The supervisor
                  </div>
                  <div className="flex flex-row gap-3 md:pl-10">
                    <div>
                      <div className="flex flex-row items-center justify-center h-16 w-16 bg-primary-700 text-white rounded-full">
                        <MdSupportAgent className="text-4xl" />
                      </div>
                    </div>
                    <div>
                      <div className="text-xl">{supervisor.names}</div>
                      <div className="flex flex-row text-lg gap-2 hover:text-green-600 font-bold">
                        <CallTo phone={supervisor.phone}>
                          Contact: {supervisor.phone}
                        </CallTo>
                      </div>
                      <div className="mt-2 mb-3 md:mb-0">
                        <CallTo phone={supervisor.phone}>
                          <div className="bg-primary-100 md:bg-primary-100 text-primary-800 md:text-primary-800 px-3 py-2 rounded-md text-sm font-bold cursor-pointer w-max">
                            <span>Call me now</span>
                          </div>
                        </CallTo>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
          </div>
        </div>
      </div>
    );
  }
}

export default SupportStaffPage;
