// import { Dispatch } from "redux";
import { ActionTypes } from "./types";

export interface App {
  side_menu: boolean;
  search: string;
}

export interface ToggleSideMenu {
  type: ActionTypes.TOGGLE_MENU;
  payload: boolean;
}

export type FT_SideNav = (status: boolean) => void;
export const FC_SideNav = (status: boolean) => {
  return {
    type: ActionTypes.TOGGLE_MENU,
    payload: status,
  } as ToggleSideMenu;
};

export interface MainSearch {
  type: ActionTypes.MAIN_SEARCH;
  payload: string;
}

export type FT_SearchBar = (searches: string) => void;
export const FC_SearchBar = (searches: string) => {
  return {
    type: ActionTypes.MAIN_SEARCH,
    payload: searches,
  } as MainSearch;
};
