import React, { Component } from "react";
import { MdClose } from "react-icons/md";
import { UpdateDPMembershipApplicationFiles } from "../../../actions/dp_membership_application";
import Button from "../../../components/Buttons/Button";
import InputFile, { acceptAllEnum } from "../../../components/Inputs/InputFile";
import Loading from "../../../components/Loading/Loading";
import PdfViewer from "../../../components/PdfViewer/PdfViewer";
import FilePreview from "./FilePreview";
import { ValidateFile } from "./MembershipRequirements";

interface EditFilesProps {
  file_path: string;
  title: string;
  onClose: () => void;
  onUpdate: (file: File) => void;
  type: "RGB" | "IMPLEMENTATION";
  application_id: string;
  preview_only: boolean;
}
interface EditFilesState {
  error: string;
  success: string;
  loading: boolean;
  file: File | null;
}

export class EditFiles extends Component<EditFilesProps, EditFilesState> {
  constructor(props: EditFilesProps) {
    super(props);

    this.state = {
      error: "",
      success: "",
      loading: false,
      file: null,
    };
  }
  SubmitEditFile = () => {
    if (this.state.file === null) {
      return this.setState({ error: "Please select a file" });
    }
    if (ValidateFile(this.state.file, this.props.type) !== true) {
      return this.setState({
        error: ValidateFile(this.state.file, this.props.type).toString(),
      });
    }
    UpdateDPMembershipApplicationFiles(
      {
        application_id: this.props.application_id,
        file: this.state.file,
        type: this.props.type,
      },
      (
        loading: boolean,
        res: { type: "success" | "error"; msg: string } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.type === "success") {
          this.setState({ success: res.msg, error: "", loading: false });
          this.state.file !== null && this.props.onUpdate(this.state.file);
        }
        if (res !== null && res.type === "error") {
          return this.setState({ error: res.msg, success: "", loading: false });
        }
      }
    );
  };
  render() {
    if (this.state.loading === true) {
      return (
        <div className="flex flex-col items-center justify-center w-full bg-white p-5 rounded-md">
          <Loading />
          <div className="text-center text-lg font-light">
            Loading, please wait...
          </div>
        </div>
      );
    }
    return (
      <div>
        <div className="bg-white">
          {this.props.preview_only === false && (
            <div className="flex flex-row items-center justify-between gap-2">
              <div className="text-xl font-bold">{this.props.title}</div>
              <div>
                <div
                  onClick={
                    this.state.loading === false ? this.props.onClose : () => {}
                  }
                  className="h-9 w-9 rounded-full bg-red-100 text-red-700 flex items-center justify-center cursor-pointer"
                >
                  <MdClose className="text-2xl" />
                </div>
              </div>
            </div>
          )}
          {this.state.file === null ? (
            <div
              className="mt-2 rounded-md bg-gray-200 animate__animated animate__fadeIn"
              style={
                this.props.preview_only === true
                  ? {}
                  : { height: "calc(100vh - 180px)", overflowY: "auto" }
              }
            >
              {this.props.file_path.slice(-3) === "pdf" ? (
                <PdfViewer
                  file_url={this.props.file_path}
                  class_name={"h-screen w-full"}
                  frame_title={"Document"}
                  setLoadingFile={(state: boolean) => {}}
                />
              ) : (
                <img
                  src={this.props.file_path}
                  alt={this.props.title}
                  className="w-full h-auto"
                />
              )}
            </div>
          ) : (
            <div
              className="mt-2 rounded-md bg-gray-200 border border-gray-300 animate__animated animate__fadeIn"
              style={
                this.props.preview_only === true
                  ? {}
                  : { height: "calc(100vh - 230px)", overflowY: "auto" }
              }
            >
              <FilePreview
                selectedFile={this.state.file}
                onClose={() => {}}
                isComponent={true}
              />
            </div>
          )}
        </div>
        {/* Form here */}
        {this.props.preview_only === false && (
          <div>
            {this.state.error !== "" && (
              <div
                onClick={() => this.setState({ error: "", success: "" })}
                className="bg-red-100 text-red-700 font-bold px-3 py-2 rounded cursor-pointer mt-2"
              >
                {this.state.error}
              </div>
            )}
            {this.state.success !== "" && (
              <div
                onClick={() => this.setState({ error: "", success: "" })}
                className="bg-green-100 text-green-700 font-bold px-3 py-2 rounded cursor-pointer mt-2"
              >
                {this.state.success}
              </div>
            )}
            <div className="flex flex-row justify-between items-end gap-2">
              <InputFile
                onChange={(e) => {
                  if (
                    e.target.files !== null &&
                    e.target.files[0] !== undefined &&
                    e.target.files[0] !== null
                  ) {
                    this.setState({ file: e.target.files[0], error: "" });
                  }
                }}
                error={""}
                label={"Select a file"}
                type={"file"}
                name={"file"}
                Input_className={"py-1 text-sm"}
                accept={
                  this.props.type === "RGB"
                    ? acceptAllEnum.ALL
                    : acceptAllEnum.PDF
                }
              />
              <div>
                <Button
                  onClick={() => this.SubmitEditFile()}
                  theme="accent-light"
                  className="truncate mb-3"
                >
                  Update file
                </Button>
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }
}
