import React, { Component } from "react";
import { BsInfoCircle } from "react-icons/bs";
import { Link } from "react-router-dom";
import { browserClearCacheData } from "../../utils/functions";

interface PlacementAnnouncementProps {
  user_type_name: string;
}
interface PlacementAnnouncementState {
  showMessage: boolean;
}

export class PlacementAnnouncement extends Component<
  PlacementAnnouncementProps,
  PlacementAnnouncementState
> {
  constructor(props: PlacementAnnouncementProps) {
    super(props);
    this.state = {
      showMessage: true,
    };
  }
  componentDidMount(): void {
    browserClearCacheData();
  }
  render() {
    if (this.state.showMessage === false) {
      return <div></div>;
    }
    return (
      <div className="fixed top-0 left-0 right-0 bottom-0 bg-black bg-opacity-30 flex items-center justify-center overflow-y-auto">
        <div className="bg-white rounded-xl p-5 px-3 w-1/2 text-center shadow-xl animate__animated animate__bounceIn">
          <div className="flex flex-col items-center justify-center mb-3">
            <div>
              <BsInfoCircle className="text-8xl text-yellow-500 animate__animated animate__fadeIn animate__slower" />
            </div>
          </div>
          <div className="font-bold text-xl mb-3 border-b pb-3 border-gray-300">
            Announcement related to Placement Report
          </div>
          <div className="">
            Dear {this.props.user_type_name}, the placement of May has been
            moved back,
            {this.props.user_type_name === "Headteacher" && (
              <div>
                Please you are required to verify and correct teachers
                information before resubmission.
              </div>
            )}
            {this.props.user_type_name === "DDE" && (
              <div>
                Please inform all headteachers to verify and correct teachers
                information before resubmission and approval of new reports frm
                headteachers.
              </div>
            )}
            {this.props.user_type_name === "Headteacher" ? (
              <div> Click the following button to resubmit the placement</div>
            ) : (
              <div> Click the following button to check schools placements</div>
            )}
          </div>
          <div className="w-full flex flex-row items-center justify-center gap-3 pt-4">
            <div
              onClick={() => this.setState({ showMessage: false })}
              className="bg-red-100 hover:bg-red-600 hover:text-white cursor-pointer font-bold text-red-700 px-3 py-2 rounded-md w-max"
            >
              Close
            </div>
            <Link
              onClick={() => this.setState({ showMessage: false })}
              to={
                this.props.user_type_name === "DDE"
                  ? "/placement-list-dde"
                  : "/placement-list-ht"
              }
              className="bg-green-600 hover:bg-green-700 cursor-pointer font-bold text-white px-3 py-2 rounded-md w-max"
            >
              {this.props.user_type_name === "Headteacher"
                ? "Resubmit Placement Report"
                : "Verify Placement Reports"}
            </Link>
          </div>
        </div>
      </div>
    );
  }
}
