import React, { Component, Fragment } from "react";
import {
  BsArrowLeft,
  BsArrowRight,
  BsArrowUpRightSquare,
} from "react-icons/bs";
import { IoMdInformationCircleOutline } from "react-icons/io";
import {
  MdOutlineImageNotSupported,
  MdOutlineKeyboardArrowDown,
  MdOutlineKeyboardArrowUp,
} from "react-icons/md";
import Button from "../../../components/Buttons/Button";
import InputFile, { acceptAllEnum } from "../../../components/Inputs/InputFile";
import TextInput from "../../../components/Inputs/TextInput";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import SelectCustom from "../../../components/SelectCustom/SelectCustom";
import { containsOnlyNumbers, FileTypes } from "../../../utils/functions";
import { validateEmail } from "../../../utils/validation";
import { CountryCodeInterface, COUNTRY_CODES } from "./data";
import { EditFiles } from "./EditFiles";
import FilePreview from "./FilePreview";

interface requirementsProps {
  requirementsData: {
    phone_number: string;
    email: string;
    location: string;
    rgb_certificate: File | null;
    implementation_plan: File | null;
    selected_country_code: CountryCodeInterface;
    website_link: string;
    rgb_certificate_filename: string;
    implementation_plan_filename: string;
    application_id: string;
    tin_number: string;
  };
  onUpdateFile: (file: File) => void;
  loading: boolean;
  GoToStep3: () => void;
  setRequirementDetails: (
    email: string,
    implementation_plan: File | null,
    rgb_certificate: File | null,
    location: string,
    selected_country_code: CountryCodeInterface,
    phone_number: string,
    website_link: string,
    tin_number: string
  ) => void;
  GoBack: () => void;
  action: "create" | "edit";
}
interface requirementsState {
  phone_number: string;
  email: string;
  location: string;
  rgb_certificate: File | null;
  implementation_plan: File | null;
  website_link: string;
  error: {
    target:
      | "phone_number"
      | "email"
      | "location"
      | "rgb_certificate"
      | "implementation_plan"
      | "website_link"
      | "tin_number";
    msg: string;
  } | null;
  preview_file: {
    type: "RGB" | "IMPLEMENTATION";
    file: File | null;
  } | null;
  selected_country_code: CountryCodeInterface;
  tin_number: string;
  open_select_country: boolean;
  confirmModal: boolean;
  editFile: {
    title: string;
    path: string;
    type: "RGB" | "IMPLEMENTATION";
  } | null;
}

export const ValidateFile = (
  uploadFile: File,
  type: "RGB" | "IMPLEMENTATION"
): string | true => {
  // Check length
  if (uploadFile.size <= 0) {
    return "The file selected has been corrupted! Please check your file";
  }
  if (type === "RGB") {
    if (
      uploadFile.type !== FileTypes.PDF &&
      uploadFile.type !== FileTypes.JPEG &&
      uploadFile.type !== FileTypes.PNG &&
      uploadFile.type !== FileTypes.GIF
    ) {
      return "File should be an image or PDF!";
    }
    if (uploadFile.size > 2006466) {
      // 1MB
      return "File size should not be greater than 1MB";
    }
  }
  if (type === "IMPLEMENTATION") {
    if (uploadFile.type !== FileTypes.PDF) {
      return "File should be PDF!";
    }
    if (uploadFile.size > 2006466) {
      //500KBs
      return "File size should not be greater than 1MB";
    }
  }
  return true;
};

export class MembershipRequirements extends Component<
  requirementsProps,
  requirementsState
> {
  constructor(props: requirementsProps) {
    super(props);

    this.state = {
      error: null,
      phone_number: this.props.requirementsData.phone_number,
      tin_number: this.props.requirementsData.tin_number,
      email: this.props.requirementsData.email,
      location: this.props.requirementsData.location,
      rgb_certificate: this.props.requirementsData.rgb_certificate,
      implementation_plan: this.props.requirementsData.implementation_plan,
      website_link: this.props.requirementsData.website_link,
      preview_file: null,
      selected_country_code: this.props.requirementsData.selected_country_code,
      open_select_country: false,
      confirmModal: false,
      editFile: null,
    };
  }
  validateInputs = () => {
    if (
      this.state.email !== "" &&
      this.state.phone_number !== "" &&
      this.state.location !== "" &&
      this.state.rgb_certificate !== null &&
      this.state.implementation_plan !== null
    ) {
      return true;
    }
    return false;
  };
  ValidateForm = () => {
    if (this.state.phone_number === "") {
      return this.setState({
        error: {
          target: "phone_number",
          msg: "Please fill organization phone number",
        },
      });
    }
    if (this.state.tin_number === "") {
      return this.setState({
        error: {
          target: "tin_number",
          msg: "Please fill organization TIN number number",
        },
      });
    }
    if (
      this.state.selected_country_code.name.toLowerCase() ===
        "Rwanda".toLowerCase() &&
      this.state.phone_number.length !== 10
    ) {
      return this.setState({
        error: {
          target: "phone_number",
          msg: "This phone is invalid, it should be having 10 digits",
        },
      });
    }
    if (
      this.state.selected_country_code.name.toLowerCase() ===
        "Rwanda".toLowerCase() &&
      containsOnlyNumbers(this.state.phone_number) !== true
    ) {
      return this.setState({
        error: {
          target: "phone_number",
          msg: "This phone should be numeric",
        },
      });
    }
    if (this.state.email === "") {
      return this.setState({
        error: { target: "email", msg: "Please fill organization email" },
      });
    }
    if (validateEmail(this.state.email) !== true) {
      return this.setState({
        error: { target: "email", msg: "Please fill the valid email" },
      });
    }
    if (this.state.location === "") {
      return this.setState({
        error: {
          target: "location",
          msg: "Please fill organization location",
        },
      });
    }
    if (
      this.state.rgb_certificate === null &&
      this.props.requirementsData.rgb_certificate_filename === ""
    ) {
      return this.setState({
        error: {
          target: "rgb_certificate",
          msg: "Please select RGB certificate",
        },
      });
    }
    if (
      this.state.rgb_certificate !== null &&
      ValidateFile(this.state.rgb_certificate, "RGB") !== true
    ) {
      return this.setState({
        error: {
          target: "rgb_certificate",
          msg: ValidateFile(this.state.rgb_certificate, "RGB").toString(),
        },
      });
    }
    if (
      this.state.implementation_plan === null &&
      this.props.requirementsData.implementation_plan_filename === ""
    ) {
      return this.setState({
        error: {
          target: "implementation_plan",
          msg: "Please select implementation plan",
        },
      });
    }
    if (
      this.state.implementation_plan !== null &&
      ValidateFile(this.state.implementation_plan, "IMPLEMENTATION") !== true
    ) {
      return this.setState({
        error: {
          target: "implementation_plan",
          msg: ValidateFile(
            this.state.implementation_plan,
            "IMPLEMENTATION"
          ).toString(),
        },
      });
    }
    if (this.state.website_link === "") {
      return this.setState({
        error: {
          target: "website_link",
          msg: "Please you are required to add website link",
        },
      });
    }
    return true;
  };
  SaveAndContinue = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // Validation
    if (this.ValidateForm() !== true) {
      return;
    }
    // Finally!
    this.props.setRequirementDetails(
      this.state.email,
      this.state.implementation_plan,
      this.state.rgb_certificate,
      this.state.location,
      this.state.selected_country_code,
      this.state.phone_number,
      this.state.website_link,
      this.state.tin_number
    );
    this.props.GoToStep3();
  };
  render() {
    return (
      <Fragment>
        <form
          onSubmit={this.SaveAndContinue}
          className="w-full p-2 md:p-4 animate__animated animate__fadeIn"
        >
          <div className="font-bold text-xl mb-3">Membership requirements</div>
          <div className="grid grid-cols-12 gap-4">
            <div className="col-span-12 lg:col-span-6">
              <div className="flex flex-row items-start justify-start gap-2">
                <div className="">
                  <div className="mb-3 mt-2">
                    <div>Country Code</div>
                    <div>
                      <div
                        onClick={() =>
                          this.setState({
                            open_select_country:
                              !this.state.open_select_country,
                          })
                        }
                        className="border border-primary-600 rounded -mr-1 h-11 w-40 flex flex-row items-center justify-between truncate px-2 font-bold cursor-pointer hover:bg-accent-400 hover:border-accent-900 hover:text-accent-900"
                      >
                        <div className="w-52 truncate">
                          ({this.state.selected_country_code.dial_code}){" "}
                          {this.state.selected_country_code.name}
                        </div>
                        <div>
                          {this.state.open_select_country === true ? (
                            <MdOutlineKeyboardArrowUp className="text-2xl" />
                          ) : (
                            <MdOutlineKeyboardArrowDown className="text-2xl" />
                          )}
                        </div>
                      </div>
                    </div>
                  </div>
                  {this.state.open_select_country === true && (
                    <div className="absolute z-50 animate__animated animated__fadeIn">
                      <SelectCustom
                        loading={this.props.loading}
                        id={"id"}
                        title={"name"}
                        close={() =>
                          this.setState({ open_select_country: false })
                        }
                        data={COUNTRY_CODES.map((item) => ({
                          id: item.name,
                          name: `(${item.dial_code}) ${item.name}`,
                        }))}
                        click={(data: { id: string; name: string }) => {
                          const selectedItem = COUNTRY_CODES.find(
                            (itm) =>
                              itm.name.toLocaleLowerCase() ===
                              data.id.toLocaleLowerCase()
                          );
                          if (selectedItem !== undefined) {
                            this.setState({
                              selected_country_code: selectedItem,
                            });
                          }
                          this.setState({ open_select_country: false });
                        }}
                      />
                    </div>
                  )}
                </div>
                <div className="w-full">
                  <TextInput
                    onChange={(e) => {
                      if (
                        this.state.selected_country_code.name.toLowerCase() ===
                          "Rwanda".toLowerCase() &&
                        e.target.value.length > 10
                      ) {
                        this.setState({
                          error: {
                            target: "phone_number",
                            msg: "This phone is invalid, it should be having 10 digits",
                          },
                        });
                      }
                      this.setState({
                        phone_number: e.target.value,
                      });
                      if (
                        (this.state.selected_country_code.name.toLowerCase() ===
                          "Rwanda".toLowerCase() &&
                          e.target.value.length <= 10) ||
                        this.state.selected_country_code.name.toLowerCase() !==
                          "Rwanda".toLowerCase()
                      ) {
                        this.setState({
                          error: null,
                        });
                      }
                    }}
                    value={this.state.phone_number}
                    error={
                      this.state.error !== null &&
                      this.state.error.target === "phone_number"
                        ? this.state.error.msg
                        : ""
                    }
                    label={"Organization phone number"}
                    placeholder={""}
                    name={"phone_number"}
                    MainContainer_className="mb-0"
                    Label_className="truncate"
                  />
                </div>
              </div>
              <TextInput
                onChange={(e) =>
                  this.setState({ email: e.target.value, error: null })
                }
                value={this.state.email}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "email"
                    ? this.state.error.msg
                    : ""
                }
                label={"Organization email"}
                placeholder={""}
                name={"email"}
                MainContainer_className="mb-3"
              />

              <TextInput
                onChange={(e) =>
                  this.setState({ tin_number: e.target.value, error: null })
                }
                value={this.state.tin_number}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "tin_number"
                    ? this.state.error.msg
                    : ""
                }
                label={"Organization TIN number"}
                placeholder={""}
                name={"tin_number"}
                MainContainer_className="mb-3"
              />

              <TextInput
                onChange={(e) =>
                  this.setState({ location: e.target.value, error: null })
                }
                value={this.state.location}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "location"
                    ? this.state.error.msg
                    : ""
                }
                label={"Organization location"}
                placeholder={""}
                name={"location"}
                MainContainer_className="mb-3"
              />
            </div>
            <div className="col-span-12 lg:col-span-6">
              <TextInput
                onChange={(e) =>
                  this.setState({ website_link: e.target.value, error: null })
                }
                value={this.state.website_link}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "website_link"
                    ? this.state.error.msg
                    : ""
                }
                label={"Organization website link"}
                placeholder={""}
                name={"website_link"}
                MainContainer_className="mb-3"
              />
              {this.props.requirementsData.rgb_certificate_filename === "" && (
                <InputFile
                  onChange={(e) => {
                    console.log("Selected file: ", e.target.files);
                    this.setState({
                      rgb_certificate:
                        e.target.files === null || e.target.files.length <= 0
                          ? this.state.rgb_certificate
                          : e.target.files[0],
                      error: null,
                    });
                  }}
                  error={
                    this.state.error !== null &&
                    this.state.error.target === "rgb_certificate"
                      ? this.state.error.msg
                      : ""
                  }
                  label={"RGB certificate (PDF or Image) 500KBs"}
                  name={"rgb_certificate"}
                  className="col-span-12 lg:col-span-6"
                  type={"file"}
                  MainContainer_className="mb-3"
                  accept={acceptAllEnum.ALL}
                />
              )}
              {this.state.rgb_certificate !== null ? (
                <div
                  onClick={() =>
                    this.setState({
                      preview_file: {
                        type: "RGB",
                        file: this.state.rgb_certificate,
                      },
                    })
                  }
                  className="bg-accent-400 cursor-pointer text-accent-900 hover:bg-accent-800 hover:text-black text-sm p-1 pr-3 rounded-md w-max flex flex-row items-center justify-center gap-2 animate__animated animate__zoomIn"
                >
                  <div>
                    <BsArrowUpRightSquare className="text-xl" />
                  </div>
                  <div className="truncate w-52">
                    Preview file: {this.state.rgb_certificate.name}
                  </div>
                </div>
              ) : (
                ""
              )}

              {this.props.requirementsData.implementation_plan_filename ===
                "" && (
                <InputFile
                  onChange={(e) =>
                    this.setState({
                      implementation_plan:
                        e.target.files === null || e.target.files.length <= 0
                          ? this.state.implementation_plan
                          : e.target.files[0],
                      error: null,
                    })
                  }
                  error={
                    this.state.error !== null &&
                    this.state.error.target === "implementation_plan"
                      ? this.state.error.msg
                      : ""
                  }
                  label={"Implementation plan (PDF only) <= 1MB"}
                  name={"implementation_plan"}
                  className="col-span-12 lg:col-span-6"
                  type={"file"}
                  MainContainer_className="mb-3"
                  accept={acceptAllEnum.PDF}
                />
              )}
              {this.state.implementation_plan !== null ? (
                <div
                  onClick={() =>
                    this.setState({
                      preview_file: {
                        type: "IMPLEMENTATION",
                        file: this.state.implementation_plan,
                      },
                    })
                  }
                  className="bg-accent-400 cursor-pointer text-accent-900 hover:bg-accent-800 hover:text-black text-sm p-1 pr-3 rounded-md w-max flex flex-row items-center justify-center gap-2 animate__animated animate__zoomIn"
                >
                  <div>
                    <BsArrowUpRightSquare className="text-xl" />
                  </div>
                  <div className="truncate w-52">
                    Preview file: {this.state.implementation_plan.name}
                  </div>
                </div>
              ) : (
                ""
              )}
              {/* Update files */}
              {this.props.action === "edit" && (
                <div className="font-bold mt-6">Uploaded files</div>
              )}
              <div className="col-span-12 flex flex-row gap-3 mt-1">
                {this.props.action === "edit" && (
                  <div
                    onClick={() =>
                      this.setState({
                        editFile: {
                          title: "Edit RGB Certificate",
                          type: "RGB",
                          path: this.props.requirementsData
                            .rgb_certificate_filename,
                        },
                      })
                    }
                    className="p-4 h-36 bg-accent-400 cursor-pointer text-accent-900 hover:text-black text-sm pr-3 rounded-md w-full flex flex-col items-center justify-center gap-2 animate__animated animate__zoomIn mb-2"
                  >
                    <div>
                      <BsArrowUpRightSquare className="text-5xl" />
                    </div>
                    <div className="truncate text-base">
                      Preview the RGB Certificate
                    </div>
                  </div>
                )}
                {this.props.action === "edit" && (
                  <div
                    onClick={() =>
                      this.setState({
                        editFile: {
                          title: "Edit implementation plan",
                          type: "IMPLEMENTATION",
                          path: this.props.requirementsData
                            .implementation_plan_filename,
                        },
                      })
                    }
                    className="p-4 h-36 bg-accent-400 cursor-pointer text-accent-900 hover:text-black text-sm pr-3 rounded-md w-full flex flex-col items-center justify-center gap-2 animate__animated animate__zoomIn"
                  >
                    <div>
                      <BsArrowUpRightSquare className="text-5xl" />
                    </div>
                    <div className="truncate text-base">
                      Preview the implementation plan
                    </div>
                  </div>
                )}
              </div>
              <div className="col-span-12 flex flex-row items-end justify-end gap-2 mt-6">
                <Button
                  onClick={() => {
                    this.setState({ confirmModal: true });
                  }}
                  theme="default"
                  type="button"
                  className="flex flex-row items-center justify-end gap-2"
                >
                  <div>
                    <BsArrowLeft className="text-xl" />
                  </div>
                  <span>Go Back</span>
                </Button>
                <Button
                  theme={`${
                    this.validateInputs() === true ? "accent" : "accent-light"
                  }`}
                  className={`flex flex-row items-center justify-center gap-2 ${
                    this.validateInputs() === true
                      ? "animate__animated animate__bounceIn"
                      : ""
                  }`}
                  type={"submit"}
                  // onClick={() => this.GoToStep3()}
                >
                  <span>Continue to step 3</span>
                  <div>
                    <BsArrowRight className="text-xl" />
                  </div>
                </Button>
              </div>
            </div>
          </div>
        </form>
        {this.state.preview_file !== null &&
          this.state.preview_file.file !== null && (
            <Modal
              backDrop={true}
              theme={Themes.default}
              close={() => this.setState({ preview_file: null })}
              backDropClose={true}
              widthSizeClass={
                ValidateFile(
                  this.state.preview_file.file,
                  this.state.preview_file.type
                ) === true
                  ? ModalSize.extraLarge
                  : ModalSize.small
              }
              displayClose={false}
              padding={{
                title: undefined,
                body: undefined,
                footer: undefined,
              }}
            >
              {ValidateFile(
                this.state.preview_file.file,
                this.state.preview_file.type
              ) !== true ? (
                <div className="flex flex-col w-full items-center justify-center p-5">
                  <div className="flex flex-col items-center justify-center bg-red-100 text-red-700 rounded-xl w-max px-5 py-6">
                    <div>
                      <MdOutlineImageNotSupported className="text-7xl" />
                    </div>
                    <div className="text-xl font-light">
                      {ValidateFile(
                        this.state.preview_file.file,
                        this.state.preview_file.type
                      )}
                    </div>
                  </div>
                  <Button
                    onClick={() => this.setState({ preview_file: null })}
                    theme="accent-light"
                    className="mt-4"
                  >
                    Go Back to form
                  </Button>
                </div>
              ) : (
                <FilePreview
                  selectedFile={this.state.preview_file.file}
                  onClose={() => this.setState({ preview_file: null })}
                  isComponent={false}
                />
              )}
            </Modal>
          )}
        {this.state.confirmModal === true && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ confirmModal: false })}
            backDropClose={true}
            widthSizeClass={ModalSize.medium}
            displayClose={false}
            padding={{
              title: undefined,
              body: true,
              footer: undefined,
            }}
          >
            <div className="flex flex-col items-center justify-center">
              <div>
                <IoMdInformationCircleOutline className="text-7xl" />
              </div>
              <div className="text-xl font-extrabold">
                Are you sure do you want to go back?
              </div>
              <div className="text-sm text-gray-500">
                Make sure you are able to loose your recent changes before
                confirmation
              </div>
              <div className="flex flex-row items-center justify-center gap-2 mt-3">
                <Button
                  onClick={() => this.setState({ confirmModal: false })}
                  theme="default"
                >
                  Cancel
                </Button>
                <Button
                  onClick={() => {
                    this.setState({ confirmModal: false });
                    this.props.GoBack();
                  }}
                  theme="accent"
                >
                  Yes, Go back
                </Button>
              </div>
            </div>
          </Modal>
        )}
        {this.state.editFile !== null && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ editFile: null })}
            backDropClose={true}
            widthSizeClass={ModalSize.extraLarge}
            displayClose={false}
            padding={{
              title: undefined,
              body: true,
              footer: undefined,
            }}
          >
            <EditFiles
              file_path={this.state.editFile.path}
              title={this.state.editFile.title}
              type={this.state.editFile.type}
              onClose={() => this.setState({ editFile: null })}
              onUpdate={this.props.onUpdateFile}
              application_id={this.props.requirementsData.application_id}
              preview_only={false}
            />
          </Modal>
        )}
      </Fragment>
    );
  }
}
