import React, { Component } from "react";
import { BsArrowRight, BsCheckCircleFill } from "react-icons/bs";
import { GetOrganizationCategoriesInterface } from "../../../actions/training_settings_create_edit";
import Button from "../../../components/Buttons/Button";
import TextInput from "../../../components/Inputs/TextInput";
import Loading from "../../../components/Loading/Loading";
import { DPMembershipApplicationStepsList } from "./Steps";

interface OrganizationCategoryProps {
  loading: boolean;
  selected_step: DPMembershipApplicationStepsList;
  setStep: (step: DPMembershipApplicationStepsList) => void;
  organizationCategories: GetOrganizationCategoriesInterface[];
  organization_details: {
    category_id: string;
    category_name: string;
    organization_category_name: string;
  };
  setOrganizationDetails: (
    category_id: string,
    organization_name: string,
    organization_category_name: string
  ) => void;
}
interface OrganizationCategoryState {
  organization_name: string;
  organization_category_id: string;
  organization_category_name: string;
  error: {
    target: "organization_category_id" | "organization_name";
    msg: string;
  } | null;
}

export class OrganizationCategory extends Component<
  OrganizationCategoryProps,
  OrganizationCategoryState
> {
  constructor(props: OrganizationCategoryProps) {
    super(props);

    this.state = {
      organization_name: this.props.organization_details.category_name,
      organization_category_id: this.props.organization_details.category_id,
      organization_category_name:
        this.props.organization_details.organization_category_name,
      error: null,
    };
  }
  GoToStep2 = () => {
    if (this.state.organization_name === "") {
      return this.setState({
        error: {
          target: "organization_name",
          msg: "Organization name is required!",
        },
      });
    }
    if (
      this.state.organization_category_id === "" ||
      this.state.organization_category_name === ""
    ) {
      return this.setState({
        error: {
          target: "organization_category_id",
          msg: "Organization category is required!",
        },
      });
    }
    this.props.setOrganizationDetails(
      this.state.organization_category_id,
      this.state.organization_name,
      this.state.organization_category_name
    );
    this.props.setStep(DPMembershipApplicationStepsList.Step2);
  };
  render() {
    if (this.props.loading === true) {
      return (
        <div className="flex flex-col items-center justify-center text-center p-3">
          <Loading />
          <div>Loading, please wait...</div>
        </div>
      );
    }
    return (
      <div className="px-0 md:px-4">
        <div>
          <TextInput
            onChange={(e) =>
              this.setState({
                organization_name: e.target.value,
                error: null,
              })
            }
            value={this.state.organization_name}
            error={
              this.state.error !== null &&
              this.state.error.target === "organization_name"
                ? this.state.error.msg
                : ""
            }
            label={"Organization name"}
            placeholder={"Type organization name in full"}
            name={"organization_name"}
            className={`${
              this.state.error !== null &&
              this.state.error.target === "organization_name"
                ? "animate__animated animate__shakeX"
                : ""
            }`}
          />
        </div>
        <div className="my-3">
          <div>Choose organization category</div>
          <div className="mt-2">
            {this.props.organizationCategories.length === 0 ? (
              <div>No data found!</div>
            ) : (
              this.props.organizationCategories.map((item, i) => (
                <div
                  key={i + 1}
                  onClick={() =>
                    this.setState({
                      organization_category_id:
                        item.id.toString() ===
                        this.state.organization_category_id
                          ? ""
                          : item.id.toString(),
                      organization_category_name:
                        this.state.organization_category_name === item.name
                          ? ""
                          : item.name,
                      error: null,
                    })
                  }
                  className={`flex flex-row items-center justify-between gap-2 group ${
                    item.id.toString() === this.state.organization_category_id
                      ? "text-accent-900 bg-accent-400"
                      : "bg-gray-100"
                  } cursor-pointer mb-2 hover:bg-accent-400 hover:text-accent-900 rounded-lg px-4 py-4 pr-5 animate__animated animate__fadeIn`}
                >
                  <div className="flex flex-row items-center gap-2">
                    <div>
                      {item.id.toString() ===
                      this.state.organization_category_id ? (
                        <BsCheckCircleFill
                          className={`text-4xl ${
                            item.id.toString() ===
                            this.state.organization_category_id
                              ? "text-accent-900"
                              : "text-gray-400"
                          } group-hover:text-accent-900 animate__animated animate__bounceIn`}
                        />
                      ) : (
                        <div className="h-9 w-9 rounded-full bg-gray-100 border-4 border-gray-400 group-hover:bg-accent-400 group-hover:border-accent-900"></div>
                      )}
                    </div>
                    <div className="flex flex-col">
                      <div className="text-xl font-bold text-black">
                        {item.name}
                      </div>
                      <div className="text-xs text-gray-600">
                        {item.description}
                      </div>
                    </div>
                  </div>
                </div>
              ))
            )}
          </div>
        </div>
        <div className="flex flex-row items-center justify-end gap-2">
          {this.state.error !== null &&
            this.state.error.target === "organization_category_id" && (
              <div className="rounded px-3 py-2 bg-red-100 text-red-700 font-bold animate__animated animate__shakeX">
                {this.state.error?.msg}
              </div>
            )}
          <Button
            theme={`${
              this.state.organization_category_id !== "" &&
              this.state.organization_name !== ""
                ? "accent"
                : "accent-light"
            }`}
            className={`flex flex-row items-center justify-center gap-2 ${
              this.state.organization_category_id !== "" &&
              this.state.organization_name !== ""
                ? "animate__animated animate__bounceIn"
                : ""
            }`}
            onClick={() => this.GoToStep2()}
          >
            <span>Continue to step 2</span>
            <div>
              <BsArrowRight className="text-xl" />
            </div>
          </Button>
        </div>
      </div>
    );
  }
}
