import ReactPDF, {
  Document,
  PDFViewer,
  Font,
  pdf,
  PDFDownloadLink,
} from "@react-pdf/renderer";
import saveAs from "file-saver";
import React, { Component, Suspense } from "react";
import { ImFilePdf } from "react-icons/im";
import { API_GetPlacementBySchoolUpdated, Auth, Basic } from "../../../actions";
import ExportToExcel from "../../../components/GenerateReport/ExportToExcel";
import LazyLoading from "../../../components/LazyLoading/LazyLoading";
import Loading from "../../../components/Loading/Loading";
import { ReportAcademicYearType } from "../../../types/PlacementReportTypes";
import { PDFgenerator } from "./PDFgenerator";
import { ReportPageFormat } from "./ReportPageFormat";

export enum TEACHING_LEVEL {
  "pre-primary" = "pre-primary",
  "primary" = "primary",
  "secondary" = "secondary",
}
export interface UserInfoGet {
  bank_account: string;
  bank_name: string;
  dob: string;
  email: string;
  first_name: string;
  full_name: string;
  hired_date: string;
  last_name: string;
  location?: {
    cell_code: string;
    cell_name: string;
    district_code: string;
    district_name: string;
    province_code: string;
    province_name: string;
    sector_code: string;
    sector_name: string;
    village_id: string;
    village_name: string;
  };
  marital_status: string;
  nationality: string;
  nationality_id: string;
  nid: string;
  phone_numbers: string;
  position: string;
  position_code: string;
  qualification: string;
  role: string;
  role_id: string;
  rssb_number: string;
  school: {
    school_id: string;
    school_code: string;
    school_name: string;
    school_category: string;
    school_status: string;
  };
  sex: string;
  specialization: string;
  staff_category: string | null;
  staff_code: string;
  start_date_in_the_school: string | null;
  status: string;
  teacher_hierarch:
    | {
        combination_code: string;
        combination_name: string;
        course_code: string;
        course_name: string;
        grade_code: string;
        grade_group: string;
        grade_name: string;
        teaching_period: string;
      }[]
    | null;
  user_id: string;
  teaching_level_id: TEACHING_LEVEL;
}

interface SchoolPlacementProps {
  data: API_GetPlacementBySchoolUpdated | null;
  district_data: API_GetPlacementBySchoolUpdated[];
  basic: Basic;
  selected_academic: ReportAcademicYearType | undefined;
  headTeacherNames: string;
  schoolName: string;
  districtName: string;
  ddeName: string;
  mayorName: string;
  auth: Auth;
  reportType: {
    key: TEACHING_LEVEL;
    value: string;
  };
  selected_school: string;
  setLoading: (status: boolean) => void;
  FC_GetSchoolHighestLevel: (school_code: string) => TEACHING_LEVEL | null;
}
interface SchoolPlacementState {
  levels: {
    id: string;
    level_name: string;
  }[];
  loading: boolean;
  documentGenerated: boolean;
  selectedDocuments: JSX.Element | null;
  generatePDF: boolean;
  downloadPDF: JSX.Element | null;
}

export const GetTotalPeriods = (
  hierarchy: {
    combination_code: string;
    combination_name: string;
    course_code: string;
    course_name: string;
    grade_code: string;
    grade_group: string;
    grade_name: string;
    teaching_period: string;
  }[]
) => {
  let total: number = 0;
  for (let item of hierarchy) {
    const grade_groups = JSON.parse(item.grade_group) as string[];
    total = total + parseInt(item.teaching_period) * grade_groups.length;
  }
  return total;
};

export const PlacementReportTypes: {
  key: TEACHING_LEVEL;
  value: string;
}[] = [
  {
    key: TEACHING_LEVEL["pre-primary"],
    value: "PRE-PRIMARY",
  },
  {
    key: TEACHING_LEVEL.primary,
    value: "PRIMARY",
  },
  {
    key: TEACHING_LEVEL.secondary,
    value: "SECONDARY",
  },
];

export const GetStaffsFiltered = (
  data: API_GetPlacementBySchoolUpdated,
  type: TEACHING_LEVEL | "ADMINISTRATIVE"
) => {
  let new_list: UserInfoGet[] = [];
  switch (type) {
    case TEACHING_LEVEL["pre-primary"]:
      new_list = (JSON.parse(data.list) as UserInfoGet[]).filter(
        (itm) =>
          // itm.teacher_hierarch !== null &&
          itm.teaching_level_id === TEACHING_LEVEL["pre-primary"] &&
          // itm.staff_category !== "ADMINISTRATIVE"
          itm.role_id.toString() === "1"
      );
      break;
    case TEACHING_LEVEL.primary:
      new_list = (JSON.parse(data.list) as UserInfoGet[]).filter(
        (itm) =>
          // itm.teacher_hierarch !== null &&
          itm.teaching_level_id === TEACHING_LEVEL["primary"] &&
          // itm.staff_category !== "ADMINISTRATIVE"
          itm.role_id.toString() === "1"
      );
      break;
    case TEACHING_LEVEL.secondary:
      new_list = (JSON.parse(data.list) as UserInfoGet[]).filter(
        (itm) =>
          // itm.teacher_hierarch !== null &&
          itm.teaching_level_id === TEACHING_LEVEL["secondary"] &&
          // itm.staff_category !== "ADMINISTRATIVE"
          itm.role_id.toString() === "1"
      );
      break;
    default:
      new_list = (JSON.parse(data.list) as UserInfoGet[]).filter(
        (itm) => itm.role_id.toString() !== "1"
        // itm.staff_category === "ADMINISTRATIVE"
      );
  }
  // Sort by names Asc
  new_list = new_list.sort(function (a, b) {
    if (a.full_name < b.full_name) {
      return -1;
    }
    if (a.full_name > b.full_name) {
      return 1;
    }
    return 0;
  });
  return {
    list: JSON.stringify(
      data.list === null || data.list === "" || data.list === undefined
        ? []
        : new_list
    ),
    placement_list_approval_id: data.placement_list_approval_id,
    school_code: data.school_code,
    district_code: data.district_code,
    academic_year_id: data.academic_year_id,
    dde_approval_status: data.dde_approval_status,
    approved_date: data.approved_date,
    month: data.month,
    created_by: data.created_by,
    created_at: data.created_at,
    updated_by: data.updated_by,
    updated_at: data.updated_at,
    school_name: data.school_name,
    district_name: data.district_name,
    ht_comment: data.ht_comment,
    dde_comment: data.dde_comment,
    head_teacher_id: data.head_teacher_id,
    dde_id: data.dde_id,
    users: data.users,
  };
};

export class SchoolPlacement extends Component<
  SchoolPlacementProps,
  SchoolPlacementState
> {
  constructor(props: SchoolPlacementProps) {
    super(props);

    this.state = {
      levels: [
        {
          id: "001",
          level_name: "PRE-PRIMARY",
        },
        {
          id: "101",
          level_name: "PRIMARY",
        },
      ],
      loading: false,
      documentGenerated: false,
      selectedDocuments: null,
      generatePDF: false,
      downloadPDF: null,
    };
  }

  getDocument = async () => {
    this.setState({ loading: true, selectedDocuments: null });
    try {
      const res: JSX.Element = await this.MyDocument(this.props.data);
      if (res) {
        this.setState({
          selectedDocuments: res,
          loading: false,
          generatePDF: true,
        });
      }
    } catch (error) {
      this.setState({
        loading: true,
        selectedDocuments: null,
        generatePDF: false,
      });
      console.log("Error occured!");
    }
  };

  MyDocument = (data: API_GetPlacementBySchoolUpdated | null): JSX.Element => {
    return (
      <Document
        title="Placement report"
        onRender={(props: ReactPDF.OnRenderProps) => {}}
      >
        {this.props.auth.role?.role_id !== "2" &&
        this.props.selected_school === ""
          ? this.props.district_data.map((item, i) => (
              <ReportPageFormat
                key={i + 1}
                data={item}
                basic={this.props.basic}
                selected_academic={this.props.selected_academic}
                headTeacherNames={this.props.headTeacherNames}
                schoolName={this.props.schoolName}
                districtName={this.props.districtName}
                ddeName={this.props.ddeName}
                mayorName={this.props.mayorName}
                reportType={this.props.reportType}
                auth={this.props.auth}
                selected_school={this.props.selected_school}
              />
            ))
          : data !== null && (
              <ReportPageFormat
                data={data}
                basic={this.props.basic}
                selected_academic={this.props.selected_academic}
                headTeacherNames={this.props.headTeacherNames}
                schoolName={this.props.schoolName}
                districtName={this.props.districtName}
                ddeName={this.props.ddeName}
                mayorName={this.props.mayorName}
                reportType={this.props.reportType}
                auth={this.props.auth}
                selected_school={this.props.selected_school}
              />
            )}
      </Document>
    );
  };

  getDownloadPDF = async () => {
    const selectedDocuments = this.state.selectedDocuments;
    if (selectedDocuments !== null) {
      this.setState({ loading: true });
      try {
        const res = (
          <PDFViewer
            showToolbar={false}
            style={{ width: "100%", height: "100vh" }}
            {...this.state}
          >
            {this.state.selectedDocuments === null
              ? this.MyDocument(this.props.data)
              : this.state.selectedDocuments}
          </PDFViewer>
        );
        this.setState({ downloadPDF: res });
        this.setState({ loading: false });
      } catch (error) {
        this.setState({ loading: false });
        console.log("error: ", error);
      }
    }
  };

  generatePdfDocument = async (fileName: string) => {
    if (this.state.selectedDocuments !== null) {
      this.setState({ loading: true });
      try {
        const blob = await pdf(this.state.selectedDocuments).toBlob();
        saveAs(blob, fileName);
        this.setState({ loading: false });
      } catch (error) {
        this.setState({ loading: false });
      }
    }
  };

  renderUrl = async (selectedDocuments: JSX.Element) =>
    new Promise(async (resolve, reject) => {
      const blob = await ReactPDF.pdf(selectedDocuments).toBlob();
      const url = URL.createObjectURL(blob);
      if (url && url.length > 0) {
        resolve(url);
      }
    })
      .then((res) => res)
      .catch((err) => console.log(err));

  confirmDownload = async () => {
    this.setState({ loading: true });
    let sampleTab = window.open();
    if (sampleTab && this.state.selectedDocuments !== null) {
      this.renderUrl(this.state.selectedDocuments)
        .then((generatedUrl) => {
          if (generatedUrl) {
            let aTag: HTMLAnchorElement = document.createElement("a");
            aTag.href = generatedUrl as string;
            // aTag.style = "display: none";
            aTag.download = "FeeAcceptance.pdf";
            document.body.appendChild(aTag);
            aTag.click();
          } // else -> means something went wrong during pdf generation
        })
        .catch((err) => console.log(err));
    } // else -> means new tab can't be opened ... (some Adblock issue)
    this.setState({ loading: false });
  };

  getReportForExcel = (data: API_GetPlacementBySchoolUpdated[]) => {
    var response = [];
    for (const mainItem of data) {
      for (const item of JSON.parse(mainItem.list) as UserInfoGet[]) {
        response.push({
          ...item,
          district_code: mainItem.district_code,
          district_name: mainItem.district_name,
          school_code: mainItem.school_code,
          school_name: mainItem.school_name,
          month: mainItem.month,
        });
      }
    }
    return response;
  };

  render() {
    if (this.state.loading === true) {
      return <Loading />;
    }
    return (
      <div>
        <div
          className={`w-full mt-3 border rounded h-auto overflow-y-auto ${
            this.props.auth.role?.role_id === "2"
              ? "border-gray-500 bg-gray-500"
              : "bg-gray-200"
          }`}
        >
          {this.props.auth.role?.role_id === "2" ? (
            <div className="">
              <div className="flex flex-row items-center justify-between w-full">
                <div className="px-3 font-extrabold text-white text-2xl">
                  School placement report
                </div>
                <div className="px-3 py-2 rounded-md bg-yellow-700 w-max text-white font-extrabold cursor-pointer m-2 border-2 border-white">
                  <PDFDownloadLink
                    fileName={`Placement report`}
                    document={this.MyDocument(this.props.data)}
                    className="w-full"
                  >
                    {({ blob, url, loading, error }) =>
                      loading ? (
                        <div className="animate__animated animate__flash animate__faster px-3 truncate">
                          Loading document...
                        </div>
                      ) : (
                        <div className="flex flex-row items-center gap-2 w-max font-semilight truncate">
                          <div>
                            <ImFilePdf className="text-2xl" />
                          </div>
                          <div>Download</div>
                        </div>
                      )
                    }
                  </PDFDownloadLink>
                </div>
              </div>
              <Suspense fallback={<LazyLoading />}>
                <PDFViewer
                  showToolbar={true}
                  style={{ width: "100%", height: "100vh" }}
                  {...this.state}
                >
                  {this.state.selectedDocuments === null
                    ? this.MyDocument(this.props.data)
                    : this.state.selectedDocuments}
                </PDFViewer>
              </Suspense>
            </div>
          ) : this.state.generatePDF === true &&
            this.state.selectedDocuments !== null ? (
            <div>
              {
                <PDFgenerator
                  data={this.props.district_data}
                  basic={this.props.basic}
                  selected_academic={this.props.selected_academic}
                  reportType={this.props.reportType}
                  auth={this.props.auth}
                  selected_school={this.props.selected_school}
                  FC_GetSchoolHighestLevel={this.props.FC_GetSchoolHighestLevel}
                />
              }
            </div>
          ) : (
            <div className="px-5 py-10 rounded-md flex flex-col items-center justify-center gap-3 bg-gray-100">
              <div>
                <ImFilePdf className="text-8xl text-gray-400" />
              </div>
              <div className="">
                Click the button below to download generated PDF file
              </div>
              <div className="text-center bg-accent-400 rounded-full border border-accent-800 w-max px-2 text-base font-extrabold">
                {this.props.reportType.value}
              </div>
              <div className="flex flex-row items-center justify-center gap-2">
                <ExportToExcel
                  fileData={this.getReportForExcel(this.props.district_data)
                    .filter(
                      (itm) =>
                        this.props.selected_school === "" ||
                        itm.school_code === this.props.selected_school
                    )
                    .map((item, i) => ({
                      No: `${i + 1}`,
                      "Staff code": item.staff_code,
                      "First name": item.first_name,
                      "Last name": item.last_name,
                      NID: item.nid,
                      Gender: item.sex,
                      "Date of birth": item.dob,
                      "Martial status": item.marital_status,
                      "RSSB number": item.rssb_number,
                      Nationality: item.nationality,
                      "Bank name": item.bank_name,
                      "Bank account": item.bank_account,
                      "Phone number": item.phone_numbers,
                      Email: item.email,
                      "Position code": item.position_code,
                      "Position name": item.position,
                      Specialization: item.specialization,
                      Qualification: item.qualification,
                      "Hired date": item.hired_date,
                      "District code": item.district_code,
                      "District name": item.district_name,
                      "School code": item.school_code,
                      "Staff category": item.staff_category,
                      Role: item.role,
                      Month: item.month,
                    }))}
                  fileName={`${
                    this.props.district_data.length > 0
                      ? this.props.district_data[0].district_name
                      : ""
                  } ${
                    this.props.selected_school !== ""
                      ? this.props.district_data.find(
                          (itm) =>
                            itm.school_code === this.props.selected_school
                        )?.school_name
                      : ""
                  } Placement report`}
                  btnName="Download Excel"
                  className="p-3 flex flex-row items-center gap-2 justify-center bg-primary-700 text-white hover:bg-primary-800 rounded cursor-pointer"
                />
                <PDFgenerator
                  data={this.props.district_data}
                  basic={this.props.basic}
                  selected_academic={this.props.selected_academic}
                  reportType={this.props.reportType}
                  auth={this.props.auth}
                  selected_school={this.props.selected_school}
                  FC_GetSchoolHighestLevel={this.props.FC_GetSchoolHighestLevel}
                />
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}
