import { Action, ActionTypes } from "../actions";
import { AcademicCalenderInterface } from "../types/AcademicTypes";

export interface AcademicCalenderReducerInterface {
  allAcademicPlans: AcademicCalenderInterface[] | null;
}

// default state
const defaultState: AcademicCalenderReducerInterface = {
  allAcademicPlans: null,
};
export const academicAcademicReducer = (
  state: AcademicCalenderReducerInterface = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.LOGOUT:
      return defaultState;
    case ActionTypes.GET_ALL_ACADEMIC_CALENDER:
      return {
        ...state,
        allAcademicPlans: action.payload,
      };
    case ActionTypes.CREATE_ACADEMIC_CALENDER:
      if (state.allAcademicPlans) {
        return {
          ...state,
          allAcademicPlans: [action.payload, ...state.allAcademicPlans],
        };
      }
      return {
        ...state,
        allAcademicPlans: [action.payload],
      };
    case ActionTypes.UPDATE_ACADEMIC_CALENDER:
      if (state.allAcademicPlans) {
        let _allAcademicPlans: AcademicCalenderInterface[] = [
          ...state.allAcademicPlans,
        ];
        let index: number = _allAcademicPlans.findIndex(
          (plan) => plan.academic_year_id === action.payload.academic_year_id
        ); //finding index of the item
        if (index >= 0) {
          _allAcademicPlans[index] = action.payload;
        }
        return {
          ...state,
          allAcademicPlans: _allAcademicPlans,
        };
      }
      return state;
    case ActionTypes.ACADEMIC_CALENDER_ACTIVATION:
      if (state.allAcademicPlans) {
        let _allAcademicPlans: AcademicCalenderInterface[] = [
          ...state.allAcademicPlans,
        ];
        let index: number = _allAcademicPlans.findIndex(
          (plan) => plan.academic_year_id === action.payload.academic_year_id
        ); //finding index of the item
        if (index >= 0) {
          _allAcademicPlans[index] = action.payload;
        }
        let oldIndex: number = _allAcademicPlans.findIndex(
          (plan) =>
            plan.academic_year_id !== action.payload.academic_year_id &&
            plan.academic_year_status === "true"
        );
        if (oldIndex && action.payload.academic_year_status === "true") {
          _allAcademicPlans[oldIndex].academic_year_status = "false";
        }
        return {
          ...state,
          allAcademicPlans: _allAcademicPlans,
        };
      }
      return state;
    default:
      return state;
  }
};
