import React, { FC, ReactElement, useEffect, useState } from "react";
import { IconType } from "react-icons";
import {
  // MdAccountCircle,
  // MdAccountTree,
  // MdSchool,
  MdGroup,
} from "react-icons/md";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import {
  CheckSchools_Interface,
  FC_CheckUserEducationStatus,
  UniversityItem,
} from "../../actions";
import { Auth } from "../../actions/auth.action";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
// import Alert from "../../components/Alert/Alert";
// import PdfViewer from "../../components/PdfViewer/PdfViewer";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import { checkAccess, UsersAccess } from "../../utils/access";
// import { validateAuthUser } from "../../utils/validatedStaff";
import AcademicCalendar from "../HomePage/AcademicCalendar";
import UpdateSchoolUniversity from "./UpdateSchoolUniversity";

type DashboardProps = {
  auth: Auth;
};
interface DashboardNavigation {
  icon: IconType;
  title: string;
  description: string;
  access: UsersAccess;
  to: string;
}

const accessNavigationList: DashboardNavigation[] = [
  {
    icon: MdGroup,
    title: "Post limits",
    description: "Post limits",
    to: "/post-limits",
    access: UsersAccess.ADD_COUNTRY_POST_LIMITATION,
  },
  {
    icon: MdGroup,
    to: "",
    title: "School staff",
    description: "Post limits",
    access: UsersAccess.ADD_COUNTRY_POST_LIMITATION,
  },
  {
    to: "",
    icon: MdGroup,
    title: "Schools",
    description: "Post limits",
    access: UsersAccess.ADD_COUNTRY_POST_LIMITATION,
  },
  {
    to: "",
    icon: MdGroup,
    title: "School staff",
    description: "Post limits",
    access: UsersAccess.ADD_COUNTRY_POST_LIMITATION,
  },
  {
    to: "",
    icon: MdGroup,
    title: "Schools",
    description: "Post limits",
    access: UsersAccess.ADD_COUNTRY_POST_LIMITATION,
  },
  {
    to: "",
    icon: MdGroup,
    title: "School staff",
    description: "Post limits",
    access: UsersAccess.ADD_COUNTRY_POST_LIMITATION,
  },
  {
    to: "",
    icon: MdGroup,
    title: "Schools",
    description: "Post limits",
    access: UsersAccess.ADD_COUNTRY_POST_LIMITATION,
  },
];
const _Dashboard: FC<DashboardProps> = ({ auth }): ReactElement => {
  const [loading, setLoading] = useState(true);
  const [modalOpen, setModalOpen] = useState(false);
  const [error, setError] = useState("");
  const [data, setData] = useState<CheckSchools_Interface | null>(null);

  useEffect(() => {
    // console.log(validateAuthUser(auth));
    // console.log({
    //   user: auth.user, position: auth.position,
    //   role: auth.role?.role_id
    // });
    if (loading === true) {
      if (auth.user && auth.position) {
        FC_CheckUserEducationStatus(
          auth.user?.user_id,
          auth.position?.qualification_id + "",
          // "3",
          (status, data, err) => {
            setLoading(false);
            setData(data);
            // console.log(data)
            if (err !== "" || status === false) setError(`Error: ${err}`);
          }
        );
      } else {
        if (auth.role?.role_id + "" === "1")
          setError(
            "Your profile is not completed, please request your Headteacher to update your info"
          );
        setLoading(false);
      }
    }
  }, [auth.position, auth.user, loading, auth.role]);

  if (!auth.user)
    return <div className="p-4 text-center">No profile found!</div>;

  let accessList = auth.role?.access;
  // const { role, school, sector, stakeholder, district, user } = auth;

  const successData = (data_send: UniversityItem) => {
    if (data) setData({ ...data, selected_values: data_send });
  };

  return (
    <>
      {}
      {checkAccess(accessList, UsersAccess.CHOOSE_A_UNIVERSITY_SCHOOL) &&
        modalOpen && (
          <UpdateSchoolUniversity
            user_id={auth.user.user_id}
            close={() => setModalOpen(false)}
            data={data}
            successFunc={successData}
          />
        )}

      <DashboardContainer
        className="animate__animated animate__zoomIn "
        transparent
      >
        {error !== "" && <Alert theme="danger">{error}</Alert>}

        <div className="pt-2">
          <Heading className="text-center font-light p-3">
            Welcome <b>{auth.user.full_name}</b>
          </Heading>
        </div>

        <div className="  ">
          {/* {checkAccess(accessList, UsersAccess.MY_TRANSFERS_CANDIDATE) &&
          auth.is_transfer_open === true && (
            <div className="mb-2 p-1  bg-white rounded">
              <PdfViewer
                file_url="/TransferManual.pdf"
                class_name="w-full h-100vh"
                frame_title="Manual"
                setLoadingFile={(status) => {
                  console.log("Loading");
                }}
              />
            </div>
          )} */}

          <h2 className="text-2xl text-center text-gray-400">
            Rwanda Basic Education Board
          </h2>
          <AcademicCalendar headerSm={false} />
        </div>
        <div className="mt-2  flex-wrap hidden ">
          {accessNavigationList.map((itm: DashboardNavigation, i) => {
            let Icon = itm.icon;
            if (!checkAccess(accessList, itm.access)) return null;
            return (
              <div className={`w-1/3 lg:w-1/5  animate__faster `} key={i}>
                <Link to={itm.to}>
                  <div
                    key={i}
                    className="rounded dark:bg-accent-900 bg-accent-900 bg-opacity-50 dark:text-accent-800 m-2 text-black hover:bg-opacity-70 py-4 text-center dark:bg-opacity-20 p-2"
                  >
                    <Icon className="text-6xl inline-block" />
                    <h4 className="font-bold text-lg">{itm.title}</h4>
                    {itm.description && (
                      <h5 className="font-bold text-xs mt-1">
                        {itm.description}
                      </h5>
                    )}
                  </div>
                </Link>
              </div>
            );
          })}
        </div>

        {checkAccess(accessList, UsersAccess.CHOOSE_A_UNIVERSITY_SCHOOL) && (
          <>
            {loading === true && (
              <div className="text-center pt-6">
                <Loading />
              </div>
            )}

            {data && data.allowed === true && (
              <>
                {data.selected_values === null ? (
                  <>
                    <div className="mt-4 border p-4 rounded bg-white max-w-screen-md mx-auto">
                      <div className="flex-1">
                        <h4 className="text-2xl font-bold">
                          Information about where in-service teachers wish to
                          obtain a teaching "Certificate".
                        </h4>
                        <p className="m-0 text-gray-500 pt-2 ">
                          You are working but you have not studied education,
                          you are requested to fill out this form in order to be
                          prepared for the "Certificate" of teaching in
                          kindergartens and primary schools, as well as the
                          "Postgraduate" of teaching in secondary schools
                          (Postgraduate in education). You are also required to
                          choose the location where you want to study near you.{" "}
                          <br /> The information you provide is important to
                          fill in correctly.
                        </p>
                      </div>
                      <section className="pt-2 text-right border-t mt-3">
                        <Button
                          onClick={() => setModalOpen(true)}
                          className="text-white"
                        >
                          Apply
                        </Button>
                      </section>
                    </div>
                  </>
                ) : (
                  <div className="mt-4 border p-4 rounded bg-white max-w-screen-sm mx-auto  text-center">
                    <div className="pb-1 text-gray-500">
                      You have decided that you are going to get a "Certificate"
                      to teach at:
                    </div>
                    <b className="text-blue-700">
                      {data.selected_values?.university}{" "}
                      {`(${data.selected_values?.qualification_name})`}
                    </b>
                  </div>
                )}
              </>
            )}
          </>
        )}
      </DashboardContainer>
    </>
  );
};

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

const Dashboard = connect(mapStateToProps, {})(_Dashboard);

export default Dashboard;
