import React, { Component } from "react";
import { Auth, DistrictList, Qualifications } from "../../../actions";
import { ReportAcademicYearType } from "../../../types/PlacementReportTypes";
import { Annex1ReportInterface, Annex1ReportItem } from "../PlacementBooks";
import { DataFormat } from "./DataFormat";

interface Annex1Props {
  auth: Auth;
  data: Annex1ReportInterface;
  selected_district: DistrictList | undefined;
  qualifications: Qualifications[];
  selected_academic?: ReportAcademicYearType;
}
interface Annex1State {
  loading: boolean;
}

export const FindTotalByQualification = (
  data: Annex1ReportInterface,
  qualification_id: string,
  level: "primary" | "pre-primary" | "secondary",
  filterStatus: "Active" | "New" | "Vacant" | "Total" | string
) => {
  const findTotalSel = (
    teaching_level: string | null,
    qualification_id: string,
    new_data: Annex1ReportItem[]
  ) => {
    const res = new_data.find(
      (itm) =>
        itm.qualification_id === qualification_id &&
        itm.teaching_level !== null &&
        itm.teaching_level.toLocaleLowerCase() ===
          teaching_level?.toLocaleLowerCase()
    );
    if (res !== undefined) {
      return res.total;
    }
    // return filterStatus === "Total" ? "1" : "0";
    return "0";
  };
  const FindTotalField = () => {
    let response: Annex1ReportItem[] = [];
    let myLoop: Annex1ReportItem[] = [];
    myLoop =
      data.vacants !== undefined && data.vacants.length > 0
        ? data.vacants
        : data.serving_teachers !== undefined &&
          data.serving_teachers.length > 0
        ? data.serving_teachers
        : data.new_teachers !== undefined
        ? data.new_teachers
        : [];
    for (const item of myLoop) {
      response.push({
        qualification_id: item.qualification_id,
        qualification_name: item.qualification_name,
        teaching_level_id: item.teaching_level_id,
        teaching_level: item.teaching_level,
        total: (
          parseInt(
            findTotalSel(
              item.teaching_level,
              item.qualification_id,
              data.serving_teachers === undefined ? [] : data.serving_teachers
            )
          ) +
          parseInt(
            findTotalSel(
              item.teaching_level,
              item.qualification_id,
              data.new_teachers === undefined ? [] : data.new_teachers
            )
          ) +
          parseInt(
            findTotalSel(
              item.teaching_level,
              item.qualification_id,
              data.vacants === undefined ? [] : data.vacants
            )
          )
        ).toString(),
      });
    }
    return response;
  };
  let total = 0;
  let search_array =
    filterStatus === "Active"
      ? data.serving_teachers
      : filterStatus === "New"
      ? data.new_teachers
      : filterStatus === "Vacant"
      ? data.vacants
      : FindTotalField();
  if (search_array === undefined) {
    search_array = [];
  }
  for (const temp_data of search_array) {
    if (
      temp_data.qualification_id === qualification_id &&
      temp_data.teaching_level?.toLocaleLowerCase() ===
        level.toLocaleLowerCase()
    ) {
      total += parseInt(temp_data.total);
    }
  }
  return total;
};

export class Annex1 extends Component<Annex1Props, Annex1State> {
  constructor(props: Annex1Props) {
    super(props);

    this.state = {
      loading: false,
    };
  }
  render() {
    return (
      <div>
        {this.props.selected_district !== undefined &&
          this.props.selected_academic !== undefined && (
            <DataFormat
              auth={this.props.auth}
              data={this.props.data}
              selected_district={this.props.selected_district}
              qualifications={this.props.qualifications}
              selected_academic={this.props.selected_academic}
            />
          )}
      </div>
    );
  }
}
