import { Auth, Action, ActionTypes } from "../actions";

// default state
const defaultState: Auth = {
  isAuthenticated: false,
  user: null,
  jwt: "",
  role: null,
  country: null,
  school: null,
  sector: null,
  stakeholder: null,
  save_code: false,
  is_post_request_open: false,
  is_transfer_open: false,
  district: null,
  position: null,
  is_permutation_request_open: false,
  has_one_year: false,
};

export const authReducer = (state: Auth = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.LOGOUT:
      return defaultState;
    case ActionTypes.LOGIN_DETAILS:
      return {
        ...state,
        isAuthenticated: true,
        user: action.payload.user_info,
        role: action.payload.role,
        jwt: action.payload.jwt,
        school: action.payload.school,
        sector: action.payload.sector,
        country: action.payload.country,
        stakeholder: action.payload.stakeholder,
        district: action.payload.district,
        // save_code: action.payload?.save_code
        //   ? action.payload?.save_code
        //   : false,
        save_code: false,
        is_post_request_open: action.payload.is_post_request_open,
        is_transfer_open: action.payload.is_transfer_open,
        // is_transfer_open: true,
        has_one_year: action.payload.has_one_year
          ? action.payload.has_one_year
          : false,
        is_permutation_request_open: action.payload.is_post_request_open,
        // is_permutation_request_open: true,
        position: action.payload.position,
      } as Auth;
    case ActionTypes.SET_AUTH_CHANGE_PASS_CODE:
      return { ...state, save_code: action.payload } as Auth;
    case ActionTypes.UPDATE_USER_DOCUMENTS:
      return {
        ...state,
        user: {
          ...state.user,
          resident_district_id:
            action.payload.resident_district_id === null
              ? state.user?.resident_district_id
              : action.payload.resident_district_id,
          disability:
            action.payload.disability === null
              ? state.user?.disability
              : action.payload.disability,
          disability_surpoting_document:
            action.payload.disability_surpoting_document === null
              ? state.user?.disability_surpoting_document
              : action.payload.disability_surpoting_document,
          resident_surpoting_document:
            action.payload.resident_surpoting_document === null
              ? state.user?.resident_surpoting_document
              : action.payload.resident_surpoting_document,
        },
      } as Auth;
    default:
      return state;
  }
};
