import React, { FC, ReactElement } from "react";

type TextInputProps = {
  children: React.ReactNode;
  className?: string;
  container?: boolean;
  transparent?: boolean;
  noPadding?: boolean;
};

const DashboardContainer: FC<TextInputProps> = ({
  children,
  className = "",
  container = true,
  transparent = false,
  noPadding,
}): ReactElement => {
  return (
    <div
      className={
        container
          ? `container mx-auto  ${noPadding === undefined ? "p-2 lg:p-2" : ""}`
          : ` ${noPadding === undefined ? "p-2 lg:p-3" : ""}`
      }
    >
      <div
        className={`${
          transparent === true
            ? ""
            : "bg-white dark:bg-dark-800 dark:text-white border border-gray-200 dark:border-gray-600"
        }  rounded   ${noPadding === undefined ? "p-2 px-3" : ""} ${className}`}
      >
        {children}
      </div>
    </div>
  );
};
export default DashboardContainer;
