export type FT_SubmitPlacementList = (
  data_send: PlacementDataCreate,
  callBack: (
    status: boolean,
    data: { message: string } | null,
    msg: string
  ) => void
) => void;
export const FC_SubmitPlacementList = async (
  data_send: PlacementDataCreate,
  callBack: (
    status: boolean,
    data: { message: string } | null,
    msg: string
  ) => void
) => {
  try {
    setAxiosToken();
    let res = await axios.post<{ message: string }>(
      `${url}/placementapproval/save`,
      data_send
    );

    callBack(true, res.data, "");
  } catch (error) {
    console.log(error);
    callBack(false, null, errorToText(error));
  }
};

