export interface GetPlacementBySchool {
  type: ActionTypes.SET_PLACEMENT_LIST;
  payload: API_GetPlacementBySchool[];
}
export type FT_GetPlacementBySchool = (
  { school_code, academic_id }: { school_code: string; academic_id: string },
  callBack: (status: boolean, msg: string) => void
) => void;

export const FC_GetPlacementBySchool = (
  { school_code, academic_id }: { school_code: string; academic_id: string },
  callBack: (status: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    setAxiosToken();

    try {
      const res = await axios.get<API_GetPlacementBySchool[]>(
        `${url}/placementapproval/slist/school/${school_code}/${academic_id}`
      );

      dispatch<GetPlacementBySchool>({
        type: ActionTypes.SET_PLACEMENT_LIST,
        payload: res.data,
      });

      callBack(true, "");
    } catch (error) {
      console.log({ error });
      callBack(false, errorToText(error));
    }
  };
};

