// report //////
export const FC_GetReportByDistrict = async (
  district: string,
  academic: string,
  month: string,
  callBack: (
    status: boolean,
    msg: string,
    data: API_GetPlacementBySchoolUpdated[] | null
  ) => void
) => {
  callBack(true, "", null);
  try {
    setAxiosToken();
    const res = await axios.get<API_GetPlacementBySchoolUpdated[]>(
      `${url}/placementapproval/monthlylist/district/${district}/${academic}/${month.toLocaleLowerCase()}`
    );
    // * save data
    const result = res.data.filter(
      (item) => Number(item.dde_approval_status) === 1
    );
    callBack(false, "", result);
  } catch (error) {
    callBack(
      false,
      errorToText(error, () => {}),
      null
    );
  }
};

