export interface DistrictTotalsSummarySubmitted {
  district_total_summary: {
    district_code: string;
    district_name: string;
    total: string;
  }[];
  pla_district_total_summary: {
    district_code: string;
    district_name: string;
    total: string;
  }[];
}

export const validatedDistrictPlacement = (
  data: DistrictTotalsSummarySubmitted,
  district_code: string
) => {
  const district_summary = data.district_total_summary.find(
    (itm) => itm.district_code === district_code
  );
  const district_placement_reports = data.pla_district_total_summary.find(
    (itm) => itm.district_code === district_code
  );
  if (district_summary !== undefined) {
    return {
      district_summary: district_summary,
      district_placement_reports:
        district_placement_reports === undefined
          ? null
          : district_placement_reports,
    };
  }
  return {
    district_summary: null,
    district_placement_reports: null,
  };
};

