export type InputSizeType = "lg" | "sm" | "md" | "xs";

export const InputSize = (key: InputSizeType): string => {
  switch (key) {
    case "sm":
      return "p-1 text-sm";
    case "md":
      return "p-2 px-4 text-md";
    case "lg":
      return "p-3 px-5 text-lg";
    case "xs":
      return "p-0.5 text-xs";
    default:
      return "";
  }
};
