import React, { Component } from "react";
import { AiFillQuestionCircle } from "react-icons/ai";
import { FaTimes } from "react-icons/fa";
import { MdCheckCircle } from "react-icons/md";
import Button from "../../../components/Buttons/Button";

interface ApplicationAlertProps {
  alert_type: "confirm" | "danger" | "success";
  msg: string;
  onConfirm: () => void;
  onCancel: () => void;
}
interface ApplicationAlertState {}

export class ApplicationAlert extends Component<
  ApplicationAlertProps,
  ApplicationAlertState
> {
  render() {
    return (
      <div className="p-6 animate__animated animate__fadeIn bg-white rounded text-center">
        <div className="flex flex-col items-center justify-center">
          <div>
            {this.props.alert_type === "confirm" && (
              <AiFillQuestionCircle className="text-7xl text-accent-900 animate-pulse" />
            )}
            {this.props.alert_type === "success" && (
              <MdCheckCircle className="text-6xl text-green-600" />
            )}
            {this.props.alert_type === "danger" && (
              <div className="h-20 w-20 bg-red-100 text-red-600 rounded-md flex items-center justify-center animate__animated animate__shakeX">
                <FaTimes className="text-7xl" />
              </div>
            )}
          </div>
          <div className="text-2xl font-bold mt-2">
            {this.props.alert_type === "confirm" ? "Confirmation required" : ""}
            {this.props.alert_type === "success" ? "Action Succeeded" : ""}
            {this.props.alert_type === "danger" ? "Action Failed" : ""}
          </div>
          <div className="text-base font-light mb-4">
            {this.props.alert_type === "confirm"
              ? "Are you sure do you want to submit?"
              : this.props.msg}
          </div>
          <div className="mt-5 flex flex-row items-center justify-center gap-3">
            <Button onClick={this.props.onCancel} theme="default">
              Cancel
            </Button>
            <Button
              onClick={this.props.onConfirm}
              theme={this.props.alert_type === "confirm" ? "accent" : "accent"}
            >
              {this.props.alert_type === "confirm"
                ? "Yes, submit"
                : this.props.alert_type === "danger"
                ? "Ok"
                : "Ok"}
            </Button>
          </div>
        </div>
      </div>
    );
  }
}
