import React, { Component } from "react";
import { ImFilePdf } from "react-icons/im";
import { MdInfoOutline } from "react-icons/md";
import { Auth, Basic, DistrictList } from "../../../actions";
import Button from "../../../components/Buttons/Button";
import { ReportAcademicYearType } from "../../../types/PlacementReportTypes";
import { PDFgenerator } from "./PDFgenerator";

export interface DistrictNewTeachersInterface {
  user_id: string;
  school_code: string;
  school_name: string;
  province_code: string;
  province_name: string;
  district_code: string;
  district_name: string;
  sector_code: string;
  sector_name: string;
  first_name: string;
  middle_name: string;
  last_name: string;
  full_name: string;
  nid: string;
  dob: string;
  email: string;
  phone_numbers: string;
  sex: string;
  marital_status: string;
  bank: string;
  bank_account: string;
  rssb_number: string;
  role_id: string;
  role: string;
  specialization_name: string;
  staff_code: string;
  start_date_in_the_school: string;
  position_code: string;
  position_name: string;
  qualification_id: string;
  qualification_name: string;
  teaching_level: string;
}

interface NewTeachersProps {
  data: DistrictNewTeachersInterface[] | null;
  loadData: () => void;
  auth: Auth;
  basic: Basic;
  selected_academic?: ReportAcademicYearType;
  districtList: DistrictList[] | null;
  selected_district_code: string;
}
interface NewTeachersState {
  loading: boolean;
}

export class NewTeachers extends Component<NewTeachersProps, NewTeachersState> {
  constructor(props: NewTeachersProps) {
    super(props);

    this.state = {
      loading: false,
    };
  }
  selectedDistrict = () => {
    const res = this.props.districtList?.find(
      (itm) => itm.district_code === this.props.selected_district_code
    );
    if (res === undefined) {
      return null;
    }
    return res;
  };
  render() {
    if (this.props.data === null) {
      return (
        <div className="bg-gray-200 rounded-md flex flex-col items-center justify-center gap-2 p-10 mt-10">
          <div>
            No data loaded for new teachers, please request for the report
          </div>
          <Button
            theme="accent"
            onClick={this.props.loadData}
            className="animate__animated animate__zoomIn"
          >
            Load Annex 2 report
          </Button>
        </div>
      );
    }
    if (this.props.data.length === 0) {
      return (
        <div className="bg-gray-200 rounded-md flex flex-col items-center justify-center gap-2 p-10 mt-10">
          <div>
            <MdInfoOutline className="text-8xl animate__animated animate__shakeX text-gray-500" />
          </div>
          <div className="text-2xl font-light">
            No new teachers found for current time
          </div>
        </div>
      );
    }
    return (
      <div>
        <div className="px-5 py-10 mt-10 rounded-md flex flex-col items-center justify-center gap-3 bg-gray-100">
          <div>
            <ImFilePdf className="text-8xl text-gray-400" />
          </div>
          <div className="">
            Click the button below to download generated PDF file
          </div>
          {this.selectedDistrict() === null ? (
            <div className="text-xl font-light">No district selected</div>
          ) : (
            <PDFgenerator
              data={this.props.data}
              basic={this.props.basic}
              selected_academic={this.props.selected_academic}
              auth={this.props.auth}
              selected_school={""}
              selected_district={this.selectedDistrict()!}
            />
          )}
        </div>
      </div>
    );
  }
}
