import {
  Document,
  StyleSheet,
  PDFViewer,
  PDFDownloadLink,
  Font,
  Page,
  View,
  Text,
} from "@react-pdf/renderer";
import { Component } from "react";
import { ImFilePdf } from "react-icons/im";
import {
  API_GetPlacementBySchoolUpdated,
  Auth,
  DistrictList,
  Qualifications,
} from "../../../actions";
import { ReportAcademicYearType } from "../../../types/PlacementReportTypes";
import { DATE, mayors_list } from "../../../utils/functions";
import { Annex1ReportInterface } from "../PlacementBooks";
import { Annex1Footer, getProvinceFromCode } from "./Annex1Footer";
import { ReportData } from "./ReportData";

interface DataFormatProps {
  auth: Auth;
  data: Annex1ReportInterface;
  selected_district: DistrictList;
  qualifications: Qualifications[];
  selected_academic: ReportAcademicYearType;
}
interface DataFormatState {}

export const AnnexReportTypes = [
  {
    key: "Active",
    value: "NUMBER OF SERVING TEACHERS",
  },
  {
    key: "New",
    value: "NUMBER OF NEW TEACHERS",
  },
  {
    key: "Vacant",
    value: "NUMBER OF VACANT POSTS",
  },
  {
    key: "Total",
    value: "GENERAL TOTAL OF NUMBER OF TEACHERS",
  },
];

export class DataFormat extends Component<DataFormatProps, DataFormatState> {
  constructor(props: DataFormatProps) {
    super(props);

    this.state = {};
  }
  MyDocument = (data: API_GetPlacementBySchoolUpdated | null) => {
    return (
      <Document
        title={`${this.props.selected_district.district_name} Placement report`}
      >
        <Page
          break={true}
          size="A4"
          style={styles.page}
          orientation="landscape"
          wrap={false}
        >
          {/* Heading */}
          <View>
            {/* General info */}
            <View
              style={{ flexDirection: "row", justifyContent: "space-between" }}
            >
              <View style={styles.section}>
                <Text style={styles.mb1}>REPUBLIC OF RWANDA</Text>
                <Text style={styles.mb1}>
                  {getProvinceFromCode(
                    this.props.selected_district.district_code
                  )}{" "}
                </Text>
                <Text style={[styles.mb1, { textTransform: "capitalize" }]}>
                  {this.props.selected_district.district_name} District
                </Text>
              </View>
              <View style={{ marginRight: "20px", marginTop: "5px" }}>
                <Text>Report date: {DATE(new Date(), "DD/MM/YYYY")}</Text>
              </View>
            </View>
            <View
              style={{
                flexDirection: "column",
                alignItems: "center",
                width: "100%",
                marginBottom: "60px",
                marginTop: "30px",
              }}
            >
              <Text
                style={[
                  styles.title,
                  { fontSize: 16, textDecoration: "underline" },
                ]}
              >
                ANNEX 1: District summary{" "}
                {this.props.selected_academic.academic_year_name}
              </Text>
            </View>
          </View>
          {/* Details here */}
          <View style={{ flexDirection: "row" }}>
            {AnnexReportTypes.map((item, i) => (
              <ReportData
                key={i + 1}
                data={this.props.data}
                qualifications={this.props.qualifications}
                dataType={item}
                selected_academic={this.props.selected_academic}
              />
            ))}
          </View>
          <Annex1Footer
            districtName={this.props.selected_district.district_name}
            ddeName={this.props.data.users.dde_name}
            mayorName={`${
              mayors_list.find(
                (itm) =>
                  itm.district_code ===
                  this.props.selected_district.district_code
              )?.mayor_family_name
            } ${
              mayors_list.find(
                (itm) =>
                  itm.district_code ===
                  this.props.selected_district.district_code
              )?.mayor_first_name
            }`}
            districtCode={this.props.selected_district.district_code}
          />
        </Page>
      </Document>
    );
  };
  render() {
    return (
      <div>
        <div className="flex flex-row items-center justify-between w-full">
          <div></div>
          <PDFDownloadLink
            fileName={`Placement report`}
            document={this.MyDocument(null)}
            className=" bg-accent-800 px-2 py-2 rounded hover:bg-accent-900 w-max"
          >
            {({ blob, url, loading, error }) =>
              loading ? (
                <div className="animate__animated animate__flash animate__faster px-3">
                  Loading document...
                </div>
              ) : (
                <div className="flex flex-row items-center gap-2 w-full font-semilight">
                  <div>
                    <ImFilePdf className="text-2xl" />
                  </div>
                  <div>Download</div>
                </div>
              )
            }
          </PDFDownloadLink>
        </div>
        <div
          className={`w-full mt-3 border rounded h-auto ${
            this.props.auth.role?.role_id === "2"
              ? "border-gray-500 bg-gray-500"
              : "bg-gray-200"
          }`}
        >
          <PDFViewer
            showToolbar={false}
            style={{ width: "100%", height: "100vh" }}
          >
            {this.MyDocument(null)}
          </PDFViewer>
        </div>
      </div>
    );
  }
}
// Create styles
Font.register({
  family: "myFont",
  src: "https://cdn.jsdelivr.net/npm/react-native-text-weight@0.6.1/Fonts/Roboto-Bold.ttf",
});

