import React, { Component, Fragment } from "react";
import { connect } from "react-redux";
import { Basic } from "../../actions";
import {
  GetOrganizationCategories,
  GetOrganizationCategoriesInterface,
  GetSubSectorsInterface,
} from "../../actions/training_settings_create_edit";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import { OrganizationCategory } from "./fragments/OrganizationCategory";
import { MembershipRequirements } from "./fragments/MembershipRequirements";
import { DPMembershipApplicationStepsList, Steps } from "./fragments/Steps";
import { CountryCodeInterface } from "./fragments/data";
import { OrganizationAreasSubSectors } from "./fragments/OrganizationAreasSubSectors";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { PreviewApplication } from "./fragments/PreviewApplication";
import Button from "../../components/Buttons/Button";
import { ApplicationAlert } from "./fragments/ApplicationAlert";
import {
  DPMembershipApplicationStatus,
  SubmitDPMembershipApplication,
  UpdateDPMembershipApplicationInterface,
} from "../../actions/dp_membership_application";
import { FaTimes } from "react-icons/fa";
import { FormatDataToEdit } from "./UpdateDPMembershipApplication";
import {
  IMPLEMENTATION_PLANS_PATH,
  RGB_CERTIFICATES_PATH,
} from "../../config/app.config";

interface DPMembershipApplicationProps {
  editApplication?: FormatDataToEdit;
  onUpdate?: (file: File) => void;
  onSubmitUpdates?: (data: UpdateDPMembershipApplicationInterface) => void;
}
interface DPMembershipApplicationState {
  application_id: string;
  organizationCategories: GetOrganizationCategoriesInterface[] | null;
  loading: boolean;
  selected_step: DPMembershipApplicationStepsList;
  //   Form data
  organization_category_id: string;
  organization_category_name: string;
  organization_name: string;
  phone_number: string;
  email: string;
  location: string;
  rgb_certificate: File | null;
  rgb_certificate_filename: string;
  implementation_plan: File | null;
  implementation_plan_filename: string;
  selected_country_code: CountryCodeInterface;
  website_link: string;
  sub_sectors: GetSubSectorsInterface[];
  added_sub_sectors: string[];
  previewApplication: boolean;
  submit_application: boolean;
  success: string;
  error: string;
  action: "edit" | "create";
  status: DPMembershipApplicationStatus;
  comment: string;
  tin_number: string;
}

class _DPMembershipApplication extends Component<
  DPMembershipApplicationProps,
  DPMembershipApplicationState
> {
  constructor(props: DPMembershipApplicationProps) {
    super(props);

    this.state = {
      application_id:
        this.props.editApplication !== undefined
          ? this.props.editApplication.application_id
          : "",
      organizationCategories: null,
      loading: false,
      selected_step: DPMembershipApplicationStepsList.Step1,
      //   Step one form data
      organization_category_id:
        this.props.editApplication !== undefined
          ? this.props.editApplication.organization_category_id
          : "",
      organization_name:
        this.props.editApplication !== undefined
          ? this.props.editApplication.organization_name
          : "",
      //   Step two form data
      email:
        this.props.editApplication !== undefined
          ? this.props.editApplication.email
          : "",
      phone_number:
        this.props.editApplication !== undefined
          ? this.props.editApplication.phone_number
          : "",
      location:
        this.props.editApplication !== undefined
          ? this.props.editApplication.location
          : "",
      rgb_certificate: null,
      implementation_plan: null,
      selected_country_code:
        this.props.editApplication !== undefined
          ? this.props.editApplication.selected_country_code
          : {
              name: "Rwanda",
              dial_code: "+250",
              code: "RW",
            },
      website_link:
        this.props.editApplication !== undefined
          ? this.props.editApplication.website_link
          : "",
      sub_sectors:
        this.props.editApplication !== undefined
          ? this.props.editApplication.sub_sectors
          : [],
      added_sub_sectors:
        this.props.editApplication !== undefined
          ? this.props.editApplication.added_sub_sectors
          : [],
      previewApplication:
        this.props.editApplication !== undefined ? true : false,
      organization_category_name:
        this.props.editApplication !== undefined
          ? this.props.editApplication.organization_category_name
          : "",
      submit_application: false,
      success: "",
      error: "",
      rgb_certificate_filename:
        this.props.editApplication !== undefined
          ? RGB_CERTIFICATES_PATH + this.props.editApplication.rgb_certificate
          : "",
      implementation_plan_filename:
        this.props.editApplication !== undefined
          ? IMPLEMENTATION_PLANS_PATH +
            this.props.editApplication.implementation_plan
          : "",
      action: this.props.editApplication === undefined ? "create" : "edit",
      status:
        this.props.editApplication === undefined
          ? DPMembershipApplicationStatus.PENDING
          : this.props.editApplication.status,
      comment:
        this.props.editApplication === undefined
          ? ""
          : this.props.editApplication.comment,

      tin_number:
        this.props.editApplication === undefined
          ? ""
          : this.props.editApplication.tin_number,
    };
  }
  cleanApplication = () => {
    this.setState({
      loading: false,
      selected_step: DPMembershipApplicationStepsList.Step1,
      organization_category_id: "",
      organization_name: "",
      email: "",
      phone_number: "",
      location: "",
      rgb_certificate: null,
      implementation_plan: null,
      implementation_plan_filename: "",
      rgb_certificate_filename: "",
      application_id: "",
      selected_country_code: {
        name: "Rwanda",
        dial_code: "+250",
        code: "RW",
      },
      website_link: "",
      sub_sectors: [],
      added_sub_sectors: [],
      previewApplication: false,
      organization_category_name: "",
      submit_application: false,
      success: "",
      error: "",
    });
  };
  setStep = (step: DPMembershipApplicationStepsList) => {
    this.setState({ selected_step: step });
  };
  componentDidMount(): void {
    if (
      this.state.organizationCategories === null ||
      this.state.organizationCategories.length === 0
    ) {
      GetOrganizationCategories(
        (
          loading: boolean,
          res: {
            status: "success" | "error";
            msg: string;
            data: GetOrganizationCategoriesInterface[];
          } | null
        ) => {
          this.setState({ loading: loading });
          if (res !== null) {
            if (res.status === "success") {
              this.setState({ organizationCategories: res.data });
            } else {
              this.setState({ organizationCategories: [] });
            }
          }
        }
      );
    }
  }
  setOrganizationDetails = (
    category_id: string,
    organization_name: string,
    organization_category_name: string
  ) => {
    this.setState({
      organization_category_id: category_id,
      organization_name: organization_name,
      organization_category_name: organization_category_name,
    });
  };
  checkStepIfCompleted = (step: DPMembershipApplicationStepsList) => {
    if (step === DPMembershipApplicationStepsList.Step1) {
      return this.state.organization_category_id !== "" &&
        this.state.organization_name !== ""
        ? true
        : false;
    }
    if (step === DPMembershipApplicationStepsList.Step2) {
      if (
        this.state.email !== "" &&
        (this.state.implementation_plan !== null ||
          this.state.implementation_plan_filename !== "") &&
        (this.state.rgb_certificate !== null ||
          this.state.rgb_certificate_filename !== "") &&
        this.state.location !== "" &&
        this.state.organization_category_id !== "" &&
        this.state.organization_name !== "" &&
        this.state.website_link !== ""
      ) {
        return true;
      }
    }
    if (step === DPMembershipApplicationStepsList.Step3) {
      if (this.state.added_sub_sectors.length > 0) {
        return true;
      }
    }
    return false;
  };
  setRequirementDetails = (
    email: string,
    implementation_plan: File | null,
    rgb_certificate: File | null,
    location: string,
    selected_country_code: CountryCodeInterface,
    phone_number: string,
    website_link: string,
    tin_number: string
  ) => {
    this.setState({
      email: email,
      phone_number: phone_number,
      location: location,
      rgb_certificate: rgb_certificate,
      implementation_plan: implementation_plan,
      selected_country_code: selected_country_code,
      website_link: website_link,
      tin_number: tin_number,
    });
  };
  removeSubSector = (id: string) => {
    this.setState({
      added_sub_sectors: this.state.added_sub_sectors.filter(
        (itm) => itm.toString() !== id.toString()
      ),
      sub_sectors: this.state.sub_sectors.filter(
        (itm) => itm.id.toString() !== id.toString()
      ),
    });
  };
  addSubSector = (id: string) => {
    this.setState({
      added_sub_sectors: [
        ...this.state.added_sub_sectors.filter((itm) => itm !== id),
        id,
      ],
    });
  };
  setSubAreas = (sub_areas: GetSubSectorsInterface) =>
    this.setState({
      sub_sectors: [
        ...this.state.sub_sectors.filter(
          (itm) => itm.id.toString() !== sub_areas.id.toString()
        ),
        sub_areas,
      ],
    });
  render() {
    return (
      <Fragment>
        <div className="h-screen">
          <div className="bg-primary-800" style={{ height: "145px" }}></div>
          <div className="container mx-auto absolute top-20 pt-1 left-0 right-0 px-2 md:px-4 lg:px-12">
            <div className="flex flex-row items-center justify-between gap-2">
              <div>
                <div className="text-3xl font-extrabold text-white">
                  {this.state.action === "create"
                    ? "Training provider Registration"
                    : this.state.organization_name === ""
                    ? "Training provider Update registration"
                    : this.state.organization_name}
                </div>
                <div className="text-sm font-light text-white mt-1 mb-3">
                  Complete the following steps to{" "}
                  {this.state.action === "create" ? "submit" : "update"}{" "}
                  training provider membership registration
                </div>
              </div>
              <div>
                {this.state.organization_category_id !== "" &&
                  this.state.organization_name !== "" &&
                  this.state.action === "create" && (
                    <div
                      onClick={() => {
                        if (
                          window.confirm(
                            "Are you sure do you want to cancel the process?"
                          ) === true &&
                          this.state.loading === false
                        ) {
                          this.cleanApplication();
                        }
                      }}
                      className="bg-primary-700 px-2 pr-3 py-2 rounded-md text-white hover:text-white hover:bg-red-700 animate__animated animate__fadeIn cursor-pointer flex flex-row items-center justify-center gap-2"
                    >
                      <div>
                        <div className="h-6 w-6 rounded-md bg-red-100 text-red-600 flex items-center justify-center">
                          <FaTimes />
                        </div>
                      </div>
                      <span>Cancel process</span>
                    </div>
                  )}
              </div>
            </div>
            <Steps
              selected_step={this.state.selected_step}
              setStep={this.setStep}
              checkStepIfCompleted={this.checkStepIfCompleted}
            />
            {this.state.organizationCategories === null ||
            this.state.loading === true ? (
              <div className="bg-white p-3 py-10 mt-3 rounded-md flex flex-col items-center justify-center shadow-md">
                <Loading />
                <div className="text-center">Loading, please wait...</div>
              </div>
            ) : (
              <div className="bg-white p-2 mt-3 rounded-md">
                {this.state.selected_step ===
                  DPMembershipApplicationStepsList.Step1 && (
                  <OrganizationCategory
                    loading={false}
                    selected_step={this.state.selected_step}
                    setStep={this.setStep}
                    organizationCategories={this.state.organizationCategories}
                    setOrganizationDetails={this.setOrganizationDetails}
                    organization_details={{
                      category_id: this.state.organization_category_id,
                      category_name: this.state.organization_name,
                      organization_category_name:
                        this.state.organization_category_name,
                    }}
                  />
                )}
                {this.state.selected_step ===
                  DPMembershipApplicationStepsList.Step2 && (
                  <MembershipRequirements
                    requirementsData={{
                      application_id: this.state.application_id,
                      phone_number: this.state.phone_number,
                      email: this.state.email,
                      location: this.state.location,
                      rgb_certificate: this.state.rgb_certificate,
                      implementation_plan: this.state.implementation_plan,
                      selected_country_code: this.state.selected_country_code,
                      website_link: this.state.website_link,
                      rgb_certificate_filename:
                        this.state.rgb_certificate_filename,
                      implementation_plan_filename:
                        this.state.implementation_plan_filename,
                      tin_number: this.state.tin_number,
                    }}
                    loading={this.state.loading}
                    GoToStep3={() =>
                      this.setStep(DPMembershipApplicationStepsList.Step3)
                    }
                    setRequirementDetails={this.setRequirementDetails}
                    GoBack={() =>
                      this.setState({
                        selected_step: DPMembershipApplicationStepsList.Step1,
                      })
                    }
                    action={this.state.action}
                    onUpdateFile={(file: File) =>
                      this.props.onUpdate && this.props.onUpdate(file)
                    }
                  />
                )}
                {this.state.selected_step ===
                  DPMembershipApplicationStepsList.Step3 && (
                  <OrganizationAreasSubSectors
                    GoBack={() =>
                      this.setState({
                        selected_step: DPMembershipApplicationStepsList.Step2,
                      })
                    }
                    added_sub_sectors={this.state.added_sub_sectors}
                    removeSubSector={this.removeSubSector}
                    addSubSector={this.addSubSector}
                    previewApplication={() => {
                      this.setState({ previewApplication: true });
                    }}
                    setSubAreas={this.setSubAreas}
                  />
                )}
              </div>
            )}
          </div>
        </div>
        {this.state.previewApplication === true && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() =>
              this.state.loading === false &&
              this.setState({ previewApplication: false })
            }
            backDropClose={false}
            widthSizeClass={
              this.state.success !== "" ||
              this.state.error !== "" ||
              this.state.submit_application === true
                ? ModalSize.small
                : ModalSize.extraLarge
            }
            displayClose={false}
            padding={{
              title: undefined,
              body: undefined,
              footer: undefined,
            }}
          >
            {/* Confirm submit */}
            {this.state.submit_application === true && (
              <ApplicationAlert
                alert_type="confirm"
                onCancel={() =>
                  this.setState({
                    submit_application: false,
                    previewApplication: false,
                  })
                }
                onConfirm={() => {
                  this.setState({
                    loading: true,
                    submit_application: false,
                    previewApplication: false,
                    error:
                      this.state.implementation_plan === null
                        ? "Implementation plan is missing"
                        : this.state.rgb_certificate === null
                        ? "RGB Certificate is missing"
                        : "",
                  });
                  // Update function here
                  if (this.state.action === "edit") {
                    this.props.onSubmitUpdates &&
                      this.props.onSubmitUpdates({
                        application_id: this.state.application_id,
                        country_code: JSON.stringify(
                          this.state.selected_country_code
                        ),
                        email: this.state.email,
                        location: this.state.location,
                        name: this.state.organization_name,
                        organization_category_id:
                          this.state.organization_category_id,
                        phone: this.state.phone_number,
                        subareas: this.state.sub_sectors.map((itm) =>
                          itm.id.toString()
                        ),
                        website_link: this.state.website_link,
                      });
                  } else {
                    this.state.implementation_plan !== null &&
                      this.state.rgb_certificate !== null &&
                      SubmitDPMembershipApplication(
                        {
                          email: this.state.email,
                          implementation_plan_file_name:
                            this.state.implementation_plan,
                          location: this.state.location,
                          name: this.state.organization_name,
                          organization_category_id:
                            this.state.organization_category_id,
                          phone: this.state.phone_number,
                          rgb_certificate_file_name: this.state.rgb_certificate,
                          subareas: this.state.added_sub_sectors,
                          website_link: this.state.website_link,
                          country_code: JSON.stringify(
                            this.state.selected_country_code
                          ),
                          tin_number: this.state.tin_number,
                        },
                        (
                          loading: boolean,
                          res: { type: "success" | "error"; msg: string } | null
                        ) => {
                          this.setState({ loading: loading });
                          if (loading === false && res !== null) {
                            if (res.type === "success") {
                              this.setState({
                                error: "",
                                success: "Registration done successfully!",
                                previewApplication: true,
                              });
                            } else {
                              this.setState({
                                error: res.msg,
                                previewApplication: true,
                              });
                            }
                          }
                        }
                      );
                  }
                }}
                msg=""
              />
            )}
            {/* Confirm success */}
            {this.state.success !== "" && (
              <ApplicationAlert
                alert_type="success"
                onCancel={() => this.cleanApplication()}
                onConfirm={() => this.cleanApplication()}
                msg={this.state.success}
              />
            )}
            {/* Failed error */}
            {this.state.error !== "" && (
              <ApplicationAlert
                alert_type="danger"
                onCancel={() =>
                  this.setState({
                    error: "",
                    previewApplication: false,
                  })
                }
                onConfirm={() =>
                  this.setState({
                    error: "",
                    previewApplication: false,
                  })
                }
                msg={this.state.error}
              />
            )}
            {this.state.success === "" &&
              this.state.error === "" &&
              this.state.submit_application === false && (
                <>
                  <div
                    style={{ height: "calc(100vh - 150px)", overflowY: "auto" }}
                  >
                    <div>
                      <PreviewApplication
                        added_sub_sectors={this.state.added_sub_sectors}
                        email={this.state.email}
                        implementation_plan={this.state.implementation_plan}
                        loading={this.state.loading}
                        location={this.state.location}
                        organization_category_id={
                          this.state.organization_category_id
                        }
                        organization_name={this.state.organization_name}
                        phone_number={
                          this.state.selected_country_code.name.toLocaleLowerCase() ===
                          "Rwanda".toLocaleLowerCase()
                            ? `${
                                this.state.selected_country_code.dial_code
                              }${this.state.phone_number.substring(1)}`
                            : this.state.phone_number
                        }
                        rgb_certificate={this.state.rgb_certificate}
                        website_link={this.state.website_link}
                        selected_country_code={this.state.selected_country_code}
                        sub_sectors={this.state.sub_sectors}
                        setStep={(step: DPMembershipApplicationStepsList) =>
                          this.setState({
                            selected_step: step,
                            previewApplication: false,
                          })
                        }
                        organization_category_name={
                          this.state.organization_category_name
                        }
                        onClose={() =>
                          this.setState({ previewApplication: false })
                        }
                        rgb_certificate_filename={
                          this.state.rgb_certificate_filename
                        }
                        implementation_plan_filename={
                          this.state.implementation_plan_filename
                        }
                        action={this.state.action}
                        application_id={this.state.application_id}
                        onUpdateFile={(file: File) => {
                          this.cleanApplication();
                          this.props.onUpdate && this.props.onUpdate(file);
                        }}
                        status={this.state.status}
                        comment={this.state.comment}
                        tin_number={this.state.tin_number}
                      />
                    </div>
                  </div>
                  <div className="bg-white border-t border-gray-400 p-2 pt-3 pb-5 flex flex-col md:flex-row items-center justify-between gap-2">
                    <div className="text-lg font-light pl-3 w-full">
                      Verify the above details for the application before submit
                    </div>
                    <div className="flex flex-row items-center justify-end gap-2 w-full">
                      <Button
                        theme="default"
                        onClick={() =>
                          this.setState({ previewApplication: false })
                        }
                      >
                        Go back
                      </Button>
                      {(this.state.action === "create" ||
                        (this.state.action === "edit" &&
                          this.props.editApplication?.status !==
                            DPMembershipApplicationStatus.APPROVED &&
                          this.props.editApplication?.status !==
                            DPMembershipApplicationStatus.REJECTED)) && (
                        <Button
                          onClick={() =>
                            this.setState({ submit_application: true })
                          }
                          theme="accent"
                        >
                          {this.state.action === "create" ? "Submit" : "Update"}
                        </Button>
                      )}
                    </div>
                  </div>
                </>
              )}
          </Modal>
        )}
      </Fragment>
    );
  }
}
const mapStateToProps = ({ basic }: StoreState): { basic: Basic } => {
  return { basic: basic };
};

export const DPMembershipApplication = connect(
  mapStateToProps,
  {}
)(_DPMembershipApplication);
