import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from ".";
import { APP_URL } from "../config/app.config";
import { GetPermutations } from "../containers/PermutationsReports/PermutationsReports";
import { setAxiosToken } from "../utils/AxiosToken";

export interface PermutationsStore {
  permutations: PermutationsByPage[] | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export interface PermutationsByPage {
  page: number;
  limit: number;
  permutations_data: GetPermutations[];
}

export interface GetPermutationsAction {
  type: ActionTypes.GET_PERMUTATIONS;
  payload: PermutationsByPage;
}

export const FC_GetPermutationsByDistrict = (
  district_code: string,
  page: number,
  limit: number,
  setLoading: (status: boolean) => void
) => {
  setLoading(true);
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<GetPermutations[]>(
        `${APP_URL}/permutation/district/${district_code}`
      );
      console.log("Res: ", res.data);
      dispatch<GetPermutationsAction>({
        type: ActionTypes.GET_PERMUTATIONS,
        payload: {
          page: page,
          limit: limit,
          permutations_data: res.data, //improve here
        },
      });
      setLoading(false);
    } catch (error: any) {
      // console.log("error: ", { ...error });
      setLoading(false);
    }
  };
};

export const FC_GetNationalPermutations = (
  page: number,
  limit: number,
  setLoading: (status: boolean) => void
) => {
  setLoading(true);
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<{
        next: number | null;
        previous: number | null;
        total_users: number;
        permutations: GetPermutations[];
      }>(`${APP_URL}/permutation/allusers/Rejected/${page}/${limit}`);
      console.log("Res: ", res.data);
      dispatch<GetPermutationsAction>({
        type: ActionTypes.GET_PERMUTATIONS,
        payload: {
          page: page,
          limit: limit,
          permutations_data: res.data.permutations, //improve here
        },
      });
      setLoading(false);
    } catch (error: any) {
      console.log("error: ", error);
      console.log("error: ", { ...error });
      setLoading(false);
    }
  };
};
