import React, { Component } from "react";

export enum DPMembershipApplicationStepsList {
  Step1 = "Step1",
  Step2 = "Step2",
  Step3 = "Step3",
}

const StepItem = (props: {
  numbering: number;
  step: DPMembershipApplicationStepsList;
  title: string;
  description: string;
  selected: boolean;
  completed: boolean;
  selectStep: (step: DPMembershipApplicationStepsList) => void;
}) => {
  return (
    <div className="col-span-4">
      <div className="flex flex-col items-center">
        <div className="flex flex-row justify-between items-center w-full">
          <div
            className={`h-3 ${
              props.step === DPMembershipApplicationStepsList.Step1
                ? "bg-transparent"
                : props.completed === true
                ? "bg-white"
                : (props.step === DPMembershipApplicationStepsList.Step2 ||
                    props.step === DPMembershipApplicationStepsList.Step3) &&
                  props.completed === false &&
                  props.selected === true
                ? "bg-white"
                : "bg-primary-600"
            } w-full`}
          ></div>
          <div className="-mx-3" style={{ zIndex: 9 }}>
            <div
              className={`h-14 w-14 ${
                props.selected === true
                  ? "border-4 border-yellow-500 bg-white text-yellow-600 animate__animated animate__bounceIn"
                  : props.completed === true
                  ? "bg-white text-primary-700 animate__animated animated__fadeIn"
                  : "bg-primary-500 text-white"
              } rounded-full flex items-center justify-center text-4xl font-extrabold cursor-pointer shadow-lg`}
            >
              <div>{props.numbering}</div>
            </div>
          </div>
          <div
            className={`h-3 ${
              props.step === DPMembershipApplicationStepsList.Step3
                ? "bg-transparent"
                : props.completed === true
                ? "bg-white"
                : "bg-primary-600"
            } w-full`}
          ></div>
        </div>
        <div className="text-xs md:text-base text-white font-bold text-center">
          {props.title}
        </div>
        <div className="hidden lg:block text-xs text-blue-100 -mt-1 font-light text-center">
          {props.description}
        </div>
      </div>
    </div>
  );
};

interface StepsProps {
  selected_step: DPMembershipApplicationStepsList;
  setStep: (step: DPMembershipApplicationStepsList) => void;
  checkStepIfCompleted: (step: DPMembershipApplicationStepsList) => boolean;
}
interface StepsState {}

export class Steps extends Component<StepsProps, StepsState> {
  constructor(props: StepsProps) {
    super(props);

    this.state = {};
  }
  selectStep = (step: DPMembershipApplicationStepsList) =>
    this.setState({ selected_step: step });
  render() {
    return (
      <div>
        <div className="p-3 bg-primary-700 rounded-lg pt-6">
          <div className="grid grid-cols-12">
            <StepItem
              step={DPMembershipApplicationStepsList.Step1}
              numbering={1}
              title={"Organization category"}
              description={"Category for your business where it belongs"}
              selected={
                this.props.selected_step ===
                DPMembershipApplicationStepsList.Step1
                  ? true
                  : false
              }
              completed={this.props.checkStepIfCompleted(
                DPMembershipApplicationStepsList.Step1
              )}
              selectStep={this.selectStep}
            />
            <StepItem
              step={DPMembershipApplicationStepsList.Step2}
              numbering={2}
              title={"Registration requirements"}
              description={"Provide requirements for selected category"}
              selected={
                this.props.selected_step ===
                DPMembershipApplicationStepsList.Step2
                  ? true
                  : false
              }
              completed={this.props.checkStepIfCompleted(
                DPMembershipApplicationStepsList.Step2
              )}
              selectStep={this.selectStep}
            />
            <StepItem
              step={DPMembershipApplicationStepsList.Step3}
              numbering={3}
              title={"Training areas"}
              description={"Choose training areas of intervention"}
              selected={
                this.props.selected_step ===
                DPMembershipApplicationStepsList.Step3
                  ? true
                  : false
              }
              completed={this.props.checkStepIfCompleted(
                DPMembershipApplicationStepsList.Step3
              )}
              selectStep={this.selectStep}
            />
          </div>
        </div>
      </div>
    );
  }
}
