import {
  Annex3ToBePlaced,
  PlacementReportLastPB,
  PlacementReportLastPBItem,
  PlacementReportLastPBStaffs,
} from "../actions";
import { TEACHING_LEVEL } from "../containers/PlacementBooks/SchoolPlacement/SchoolPlacement";

interface LevelsCountInterface {
  teaching_level_id: string;
  total: string;
}

const initializeLevels = (
  data: PlacementReportLastPBItem[]
): LevelsCountInterface[] => {
  const temp: LevelsCountInterface[] = [];

  for (const item of data) {
    if (
      temp.find(
        (itm) =>
          itm.teaching_level_id.toLowerCase() ===
          item.teaching_level_id.toLowerCase()
      ) === undefined
    ) {
      temp.push({
        teaching_level_id: item.teaching_level_id,
        total: (0).toString(),
      });
    }
  }
  return temp;
};

const GetTotalDistrictTeacherByLevel = (
  data: PlacementReportLastPBItem[],
  level: TEACHING_LEVEL
): number => {
  var total = 0;
  for (const item of data) {
    if (item.teaching_level_id.toLowerCase() === level.toLowerCase()) {
      total += parseInt(item.total_teacher);
    }
  }
  return total;
};

const GetSchoolHighestLevel = (
  data: PlacementReportLastPBItem[],
  school_code: string
): TEACHING_LEVEL | null => {
  const test = data.filter(
    (itm) =>
      itm.school_code.toString() === school_code.toString() &&
      parseInt(itm.total_teacher) > 0
  );
  if (test.length > 0) {
    if (
      test.find(
        (itm) =>
          itm.teaching_level_id.toLowerCase() ===
          TEACHING_LEVEL.secondary.toLowerCase()
      ) !== undefined
    ) {
      return TEACHING_LEVEL.secondary;
    }
    if (
      test.find(
        (itm) =>
          itm.teaching_level_id.toLowerCase() ===
          TEACHING_LEVEL.primary.toLowerCase()
      ) !== undefined
    ) {
      return TEACHING_LEVEL.primary;
    }
    if (
      test.find(
        (itm) =>
          itm.teaching_level_id.toLowerCase() ===
          TEACHING_LEVEL["pre-primary"].toLowerCase()
      ) !== undefined
    ) {
      return TEACHING_LEVEL["pre-primary"];
    }
  }
  return null;
};

const GetTotalDistrictAdminStaffByLevel = (
  teacherData: PlacementReportLastPBItem[],
  adminStaff: PlacementReportLastPBStaffs[],
  level: TEACHING_LEVEL
) => {
  var total: number = 0;
  for (const item of adminStaff) {
    if (
      GetSchoolHighestLevel(teacherData, item.school_code)?.toLowerCase() ===
      level.toLowerCase()
    ) {
      total += parseInt(item.total_teacher);
    }
  }
  return total;
};

export const FC_FormatAnnex3Report = (
  data: PlacementReportLastPB
): Annex3ToBePlaced => {
  // Manipulation step 1: initialize levels
  var levels_counts: LevelsCountInterface[] = [];

  //   Calculating totals
  for (const item of initializeLevels(data.total_teach_level)) {
    levels_counts.push({
      teaching_level_id: item.teaching_level_id,
      total: (
        GetTotalDistrictTeacherByLevel(
          data.total_teach_level,
          item.teaching_level_id as TEACHING_LEVEL
        ) +
        GetTotalDistrictAdminStaffByLevel(
          data.total_teach_level,
          data.total_staff,
          item.teaching_level_id as TEACHING_LEVEL
        )
      ).toString(),
    });
  }

  return {
    district_needed_posts: data.district_needed_posts,
    total_teach_level: levels_counts,
  };
};
