export interface API_RequestAuth_Login extends API_RequestAuth {
  jwt: string;
  is_account_verified: boolean;
  message: string;
  verification_code?: string;
}

/**
 * * ****************************** ACTIONS *****************************
 */
export interface SaveAuthData {
  type: ActionTypes.LOGIN_DETAILS;
  payload: API_RequestAuth_Login;
}
export type FT_Login = (
  data: { username: string; password: string },
  callBack: (
    status: boolean,
    data: { is_account_verified: boolean; message: string } | null,
    msg: string
  ) => void
) => void;
export const FC_Login = (
  data: { username: string; password: string },
  callBack: (
    status: boolean,
    data: { is_account_verified: boolean; message: string } | null,
    msg: string
  ) => void
) => {
  return async (dispatch: Dispatch) => {
    try {
      const res = await axios.post<API_RequestAuth_Login>(
        `${url}/account/login`,
        data
      );

      // console.log({ LOGIN_INFO: res.data });
      // console.log("CODE: ", res.data?.verification_code);

      if (res.data.is_account_verified === true) {
        // * save data
        localStorage.setItem(APP_TOKEN_NAME, res.data.jwt);
        // console.log({ jwt: res.data.jwt });

        dispatch<SaveAuthData>({
          type: ActionTypes.LOGIN_DETAILS,
          payload: res.data,
        });

        setAxiosToken();
        callBack(true, null, "");
      } else {
        callBack(
          true,
          {
            is_account_verified: res.data.is_account_verified,
            message: res.data.message,
          },
          ""
        );
      }
    } catch (error) {
      console.log({ error });
      callBack(false, null, errorToText(error));
    }
  };
};

export interface SaveAuthData {
  type: ActionTypes.LOGIN_DETAILS;
  payload: API_RequestAuth_Login;
}
export type FT_CheckUserIsAuthenticated = (
  callBack: (status: boolean, msg: string) => void
) => void;
export const FC_CheckUserIsAuthenticated = (
  callBack: (status: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    setAxiosToken();

    try {
      const res = await axios.get<API_RequestAuth_Login>(`${url}/current/info`);

      dispatch<SaveAuthData>({
        type: ActionTypes.LOGIN_DETAILS,
        payload: res.data,
      });
      callBack(true, "");
    } catch (error) {
      console.log({ error });
      callBack(false, errorToText(error));
    }
  };
};

// *** Logout
export interface Logout {
  type: ActionTypes.LOGOUT;
}
export type FT_Logout = () => void;
export const FC_Logout = () => {
  delete axios.defaults.headers.common["Tmis-Token"];
  localStorage.removeItem(APP_TOKEN_NAME);
  localStorage.clear();
  return {
    type: ActionTypes.LOGOUT,
  } as Logout;
};

