import React, { FC, ReactElement, useState } from "react";
import { IconType } from "react-icons";
import { MdChevronRight, MdOutlineExpandMore } from "react-icons/md";
import { NavLink } from "react-router-dom";
import { checkAccess, UsersAccess } from "../../utils/access";

type ExpandableSideNavigationItemProps = {
  title: string;
  icon: IconType;
  onSelect: () => void;
  links: {
    title: string;
    url: string;
    access: UsersAccess | null | "ALL";
    icon?: IconType;
    and?: boolean;
  }[];
  access_list: string | undefined;
};

const ExpandableSideNavigationItem: FC<ExpandableSideNavigationItemProps> = ({
  title,
  icon,
  links,
  access_list,
  onSelect,
}): ReactElement | null => {
  const [open, setOpen] = useState(false);
  let Icon = icon;
  let Links = links.filter((el) =>
    el.access === "ALL" ? true : checkAccess(access_list, el.access)
  );
  // let Links = links.filter((el) =>
  //   el.access === null ? false : checkAccess(access_list, el.access)
  // );

  if (Links.length <= 0) return null;

  if (Links.length === 1) {
    let IconData = Links[0].icon ? Links[0].icon : Icon;
    return (
      <>
        <NavLink
          to={Links[0].url}
          className={`navigation-item `}
          exact
          onClick={onSelect}
        >
          <div className="mr-1">
            <IconData />
          </div>
          <span className="flex-1">{Links[0].title}</span>
        </NavLink>
      </>
    );
  }

  return (
    <>
      <span
        className={`navigation-item cursor-pointer  ${
          open ? "bg-gray-100 border" : "bg-gray-50"
        }`}
        onClick={setOpen.bind(this, !open)}
      >
        <div className="mr-1">
          <Icon className="text-2xl" />
        </div>
        <span className="flex-1">{title}</span>
        <span className="block pr-4 opacity-40">
          {open ? <MdOutlineExpandMore /> : <MdChevronRight />}
        </span>
      </span>
      {open && (
        <div className="sub-navigation-container animate__animated animate__fadeIn animate__faster">
          {links.map((el, i) => {
            let IconDisplay = el.icon ? el.icon : Icon;
            if (el.and !== undefined && el.and === false) return null;

            if (el.access !== "ALL") {
              if (!el.access) return null;
              if (!checkAccess(access_list, el.access)) return null;
            }
            return (
              <NavLink
                key={i}
                to={el.url}
                className="sub-navigation-item"
                onClick={onSelect}
              >
                <span className="text-accent-900 text-xl">
                  <IconDisplay />
                </span>

                <span className="pl-2">{el.title}</span>
              </NavLink>
            );
          })}
        </div>
      )}
    </>
  );
};
export default ExpandableSideNavigationItem;
