/**
 * * ****************************** ACTIONS *****************************
 */
export type FT_GetDistributionDistrict = (
  academic_id: string,
  callBack: (
    status: boolean,
    data: API_DistributedDistricts | null,
    msg: string
  ) => void
) => void;
export const FC_GetDistributionDistrict = async (
  academic_id: string,
  callBack: (
    status: boolean,
    data: API_DistributedDistricts | null,
    msg: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.get<API_DistributedDistricts>(
      `${url}/rebdistribution/academic/${academic_id}`
    );

    console.log({ red_distribution_data: res.data });

    callBack(true, res.data, "");
  } catch (error) {
    callBack(false, null, errorToText(error));
  }
};

