// report distribution
export interface DistributionVacant_Qualification {
  qualification_id: string;
  qualification_name: string;
  total: number;
  limits: number;
  distributed: number;
  remaining: number;
}
export interface API_DistributedVacantPosts {
  district_code: string;
  district_name: string;
  teachers_on_qualification: DistributionVacant_Qualification[];
}

export interface DistributedVacantPostsReport__Interface {
  district_code: string;
  district_name: string;
  // A0
  A0_distributed: number;
  A0_limits: number;
  A0_remaining: number;
  A0_total: number;
  // A1
  A1_distributed: number;
  A1_limits: number;
  A1_remaining: number;
  A1_total: number;
  // A2
  A2_distributed: number;
  A2_limits: number;
  A2_remaining: number;
  A2_total: number;
  // Totals
  district_total_distributed: number;
  district_total_limits: number;
  district_total_remaining: number;
  district_total: number;
}

export const FC_DistributedVacantPosts = async (
  academic_id: string,
  callBack: (
    status: boolean,
    data: DistributedVacantPostsReport__Interface[],
    msg: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.get<API_DistributedVacantPosts[]>(
      `${url}/report/district/qualteacher/${academic_id}`
    );

    // formatting the data
    let formatted_data: DistributedVacantPostsReport__Interface[] = [];

    console.log({ non_fromated: res.data });

    if (res.data.length >= 1)
      formatted_data = res.data.map((el) => {
        let A0 = el.teachers_on_qualification.find(
          (itm) => itm.qualification_name === "A0"
        );
        let A1 = el.teachers_on_qualification.find(
          (itm) => itm.qualification_name === "A1"
        );
        let A2 = el.teachers_on_qualification.find(
          (itm) => itm.qualification_name === "A2"
        );

        return {
          district_code: el.district_code,
          district_name: el.district_name,
          // A0
          A0_distributed: A0 ? A0.distributed : 0,
          A0_limits: A0 ? A0.limits : 0,
          A0_remaining: A0 ? A0.remaining : 0,
          A0_total: A0 ? A0.total : 0,
          // A1
          A1_distributed: A1 ? A1.distributed : 0,
          A1_limits: A1 ? A1.limits : 0,
          A1_remaining: A1 ? A1.remaining : 0,
          A1_total: A1 ? A1.total : 0,
          // A2
          A2_distributed: A2 ? A2.distributed : 0,
          A2_limits: A2 ? A2.limits : 0,
          A2_remaining: A2 ? A2.remaining : 0,
          A2_total: A2 ? A2.total : 0,
          // Totals
          district_total_distributed:
            (A0 ? A0.distributed : 0) +
            (A1 ? A1.distributed : 0) +
            (A2 ? A2.distributed : 0),
          district_total_limits:
            (A0 ? A0.limits : 0) + (A1 ? A1.limits : 0) + (A2 ? A2.limits : 0),
          district_total_remaining:
            (A0 ? A0.remaining : 0) +
            (A1 ? A1.remaining : 0) +
            (A2 ? A2.remaining : 0),
          district_total:
            (A0 ? A0.total : 0) + (A1 ? A1.total : 0) + (A2 ? A2.total : 0),
        } as DistributedVacantPostsReport__Interface;
      });

    callBack(true, formatted_data, "");
  } catch (error) {
    callBack(false, [], errorToText(error));
  }
};
