import React, { Component } from "react";
import { FaCheckCircle } from "react-icons/fa";
import { ImRadioUnchecked } from "react-icons/im";
import { MdHighlightAlt } from "react-icons/md";
import { API_GetPlacementBySchoolUpdated } from "../../../actions";
import { search } from "../../../utils/functions";

interface SelectSchoolProps {
  getSelectedReportDistrict: API_GetPlacementBySchoolUpdated[] | null;
  selected_school_code: string;
  select_district: (school_code: string) => void;
}

interface SelectSchoolState {
  loading: boolean;
  search_data: string;
}

export class SelectSchool extends Component<
  SelectSchoolProps,
  SelectSchoolState
> {
  constructor(props: SelectSchoolProps) {
    super(props);

    this.state = {
      loading: false,
      search_data: "",
    };
  }
  render() {
    return (
      <div>
        <div>
          <div className="mb-4">
            <div className="text-gray-700">Search school</div>
            <input
              type="text"
              value={this.state.search_data}
              onChange={(e) => this.setState({ search_data: e.target.value })}
              className="px-3 py-2 rounded border border-gray-400 w-full"
            />
          </div>
          {(
            search(
              this.props.getSelectedReportDistrict,
              this.state.search_data
            ) as API_GetPlacementBySchoolUpdated[] | null
          )?.length === 0 ? (
            <div>
              <div className="bg-gray-200 p-5 rounded-md text-2xl font-extrabold text-center text-gray-700">
                No result found!
              </div>
            </div>
          ) : (
            (
              search(
                this.props.getSelectedReportDistrict,
                this.state.search_data
              ) as API_GetPlacementBySchoolUpdated[] | null
            )?.map((item, i) => (
              <div key={i + 1}>
                <div
                  onClick={() => {
                    this.props.select_district(item.school_code);
                  }}
                  className={`flex flex-row items-center gap-2 justify-between ${
                    item.school_code === this.props.selected_school_code
                      ? "bg-accent-400 border border-yellow-600"
                      : "bg-gray-200 hover:bg-accent-400 border border-gray-200 hover:border-yellow-600"
                  } rounded px-3 py-2 mb-2 group hover:text-black cursor-pointer`}
                >
                  <div className="flex flex-row items-center gap-2">
                    <div>
                      <MdHighlightAlt className="text-4xl" />
                    </div>
                    <div className="flex flex-col">
                      <span className="text-base font-semibold">
                        {item.school_name}
                      </span>
                      <span className="text-gray-700 group-hover:text-black text-sm">
                        Code: {item.school_code}
                      </span>
                    </div>
                  </div>
                  <div>
                    {item.school_code === this.props.selected_school_code ? (
                      <FaCheckCircle className="text-3xl text-accent-900" />
                    ) : (
                      <ImRadioUnchecked className="text-3xl text-gray-400 group-hover:text-gray-700" />
                    )}
                  </div>
                </div>
              </div>
            ))
          )}
        </div>
      </div>
    );
  }
}

export default SelectSchool;
