import { Font, View, Text, StyleSheet } from "@react-pdf/renderer";
import React from "react";
import { Qualifications } from "../../../actions";
import { ReportAcademicYearType } from "../../../types/PlacementReportTypes";
import { commaFy } from "../../../utils/functions";
import { Annex1ReportInterface } from "../PlacementBooks";
import { FindTotalByQualification } from "./Annex1";

interface ReportDataProps {
  data: Annex1ReportInterface;
  dataType: {
    key: string;
    value: string;
  };
  qualifications: Qualifications[];
  selected_academic: ReportAcademicYearType;
}

export const ReportData = (props: ReportDataProps) => {
  const FindFindGrandTotalByQualification = (
    level: "primary" | "pre-primary" | "secondary"
  ) => {
    let total = 0;
    for (const item of props.qualifications) {
      total =
        total +
        FindTotalByQualification(
          props.data,
          item.qualification_id,
          level,
          props.dataType.key
        );
    }
    return total;
  };
  return (
    <View style={styles.table}>
      <View
        style={[
          styles.theader,
          {
            borderRightWidth: props.dataType.key === "Total" ? "0.5px" : "0px",
          },
        ]}
      >
        <Text>
          {props.dataType.value} {props.selected_academic.academic_year_name}
        </Text>
      </View>
      <View style={[styles.tr, { fontFamily: "myFont", fontSize: 7 }]}>
        <Text style={styles.th}>Qualification</Text>
        <Text style={styles.th}>Pre-primary</Text>
        <Text style={styles.th}>Primary</Text>
        <Text style={styles.th}>Secondary</Text>
        <Text
          style={[
            styles.th,
            {
              borderRightWidth:
                props.dataType.key === "Total" ? "0.5px" : "0px",
            },
          ]}
        >
          Total
        </Text>
      </View>
      {/* Data counting by levels */}
      {props.qualifications.map((item, i) => (
        <View key={i + 1} style={styles.tr}>
          <Text style={styles.th}>{item.qualification_name}</Text>
          <Text style={styles.th}>
            {commaFy(
              FindTotalByQualification(
                props.data,
                item.qualification_id,
                "pre-primary",
                props.dataType.key
              )
            )}
          </Text>
          <Text style={styles.th}>
            {commaFy(
              FindTotalByQualification(
                props.data,
                item.qualification_id,
                "primary",
                props.dataType.key
              )
            )}
          </Text>
          <Text style={styles.th}>
            {commaFy(
              FindTotalByQualification(
                props.data,
                item.qualification_id,
                "secondary",
                props.dataType.key
              )
            )}
          </Text>
          <Text
            style={[
              styles.th,
              {
                borderRightWidth:
                  props.dataType.key === "Total" ? "0.5px" : "0px",
              },
            ]}
          >
            {commaFy(
              FindTotalByQualification(
                props.data,
                item.qualification_id,
                "pre-primary",
                props.dataType.key
              ) +
                FindTotalByQualification(
                  props.data,
                  item.qualification_id,
                  "primary",
                  props.dataType.key
                ) +
                FindTotalByQualification(
                  props.data,
                  item.qualification_id,
                  "secondary",
                  props.dataType.key
                )
            )}
          </Text>
        </View>
      ))}
      {/* Total count on serving teachers */}
      <View style={[styles.tr, { fontFamily: "myFont", fontSize: 8 }]}>
        <Text style={styles.th}>Total</Text>
        <Text style={styles.th}>
          {commaFy(FindFindGrandTotalByQualification("pre-primary"))}
        </Text>
        <Text style={styles.th}>
          {commaFy(FindFindGrandTotalByQualification("primary"))}
        </Text>
        <Text style={styles.th}>
          {commaFy(FindFindGrandTotalByQualification("secondary"))}
        </Text>
        <Text
          style={[
            styles.th,
            {
              borderRightWidth:
                props.dataType.key === "Total" ? "0.5px" : "0px",
            },
          ]}
        >
          {commaFy(
            FindFindGrandTotalByQualification("pre-primary") +
              FindFindGrandTotalByQualification("primary") +
              FindFindGrandTotalByQualification("secondary")
          )}
        </Text>
      </View>
    </View>
  );
};

// Create styles
Font.register({
  family: "myFont",
  src: "https://cdn.jsdelivr.net/npm/react-native-text-weight@0.6.1/Fonts/Roboto-Bold.ttf",
});

