import React, { Component } from "react";
import { BsCheckCircleFill } from "react-icons/bs";
import { FaBars } from "react-icons/fa";
import { ImRadioChecked2 } from "react-icons/im";
import { IoMdClose } from "react-icons/io";
import { connect } from "react-redux";
import { Auth, Basic, FC_GetAllSystemRoles } from "../../actions";
import {
  GetAreasOfIntervention,
  GetAreasOfInterventionInterface,
} from "../../actions/training_settings_create_edit";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import { CreateSettingsComponent } from "./CreateSettingsComponent";

const SettingsCategorySelect = (props: {
  title: string;
  selected: boolean;
  onClick: () => void;
}) => {
  return (
    <div>
      <div
        className={`${
          props.selected === true
            ? "bg-white text-primary-800 font-bold cursor-not-allowed animate__animated animate__fadeIn border border-blue-200"
            : "font-light hover:bg-white hover:text-primary-800 cursor-pointer"
        } flex flex-row items-center gap-4 px-3 py-2 rounded-md group`}
        onClick={props.onClick}
      >
        <div>
          {props.selected === true ? (
            <BsCheckCircleFill className="text-xl" />
          ) : (
            <ImRadioChecked2 className="text-xl text-blue-300 group-hover:text-blue-400" />
          )}
        </div>
        <span>{props.title}</span>
      </div>
    </div>
  );
};

export enum SettingsRouteType {
  area = "area",
  subarea = "subarea",
  topic = "topic",
  organization_category = "organization/category",
  trainers_category = "trainer/institution",
  trainees_category = "trainee/category",
}

export interface SettingsEditType {
  key: SettingsRouteType;
  title: string;
  hasDescription: boolean;
  hasMandatory: boolean;
  hasTags: boolean;
  hasParentData: boolean;
  hasRoles: boolean;
}

export const SettingsTypesTitles: SettingsEditType[] = [
  {
    key: SettingsRouteType.area,
    title: "Area of intervention",
    hasDescription: true,
    hasMandatory: true,
    hasTags: false,
    hasParentData: false,
    hasRoles: false,
  },
  {
    key: SettingsRouteType.subarea,
    title: "Sub area",
    hasDescription: false,
    hasMandatory: false,
    hasTags: false,
    hasParentData: true,
    hasRoles: false,
  },
  {
    key: SettingsRouteType.organization_category,
    title: "Training Provider category",
    hasDescription: true,
    hasMandatory: false,
    hasTags: false,
    hasParentData: false,
    hasRoles: false,
  },
  {
    key: SettingsRouteType.trainers_category,
    title: "Trainers category",
    hasDescription: false,
    hasMandatory: false,
    hasTags: false,
    hasParentData: false,
    hasRoles: false,
  },
  {
    key: SettingsRouteType.trainees_category,
    title: "Trainee's category",
    hasDescription: false,
    hasMandatory: false,
    hasTags: false,
    hasParentData: false,
    hasRoles: true,
  },
  {
    key: SettingsRouteType.topic,
    title: "Topic",
    hasDescription: false,
    hasMandatory: false,
    hasTags: true,
    hasParentData: true,
    hasRoles: false,
  },
];

interface CreateSettingsProps {
  auth: Auth;
  basic: Basic;
  FC_GetAllSystemRoles: (
    callBack: (status: boolean, msg: string) => void
  ) => void;
}
interface CreateSettingsState {
  loading: boolean;
  name: string;
  description: string;
  formError: { target: "name" | "description" | "main"; msg: string } | null;
  success: string;
  settings_type: SettingsRouteType;
  areas_of_intervention: GetAreasOfInterventionInterface[] | null;
  pageType: "form" | "list";
  sideNav: boolean;
}

export const GetComponentType = (settings_type: SettingsRouteType) => {
  const componentType = SettingsTypesTitles.find(
    (itm) => itm.key === settings_type
  );
  if (componentType !== undefined) {
    return componentType;
  } else {
    return null;
  }
};

export const GetAreaAsParent = (
  areas: GetAreasOfInterventionInterface[] | null,
  selected_component: SettingsEditType | null
) => {
  if (
    areas === null ||
    selected_component === null ||
    selected_component.hasParentData === false
  ) {
    return [];
  }
  return areas.map((item) => ({
    name: item.name,
    id: item.id.toString(),
  }));
};

export class _CreateSettings extends Component<
  CreateSettingsProps,
  CreateSettingsState
> {
  constructor(props: CreateSettingsProps) {
    super(props);

    this.state = {
      areas_of_intervention: null,
      loading: false,
      settings_type: SettingsRouteType.subarea,
      formError: null,
      success: "",
      name: "",
      description: "",
      pageType: "form",
      sideNav: true,
    };
  }

  componentDidMount = () => {
    if (
      this.state.settings_type === SettingsRouteType.subarea ||
      this.state.settings_type === SettingsRouteType.topic
    ) {
      this.FC_GetAreasIntervention(this.state.settings_type);
    }
  };

  FC_GetAreasIntervention = (selected_key: SettingsRouteType) => {
    this.setState({ loading: true, formError: null, success: "" });
    GetAreasOfIntervention(
      (
        loading: boolean,
        res: {
          status: "success" | "error";
          msg: string;
          data: GetAreasOfInterventionInterface[];
        } | null
      ) => {
        this.setState({ loading: loading });
        if (loading === false && res !== null && res.data.length > 0) {
          this.setState({
            areas_of_intervention: res.data,
            loading: false,
            settings_type: selected_key,
          });
        }
        if (loading === false && res !== null && res.status === "error") {
          this.setState({
            success: "",
            formError: { target: "main", msg: res.msg },
          });
        }
      }
    );
  };

  render() {
    const componentType = SettingsTypesTitles.find(
      (itm) => itm.key === this.state.settings_type
    );

    return (
      <div>
        <DashboardContainer>
          <div className="flex flex-row items-center justify-between bg-white w-full m-1">
            <div className="flex flex-col">
              <span className="text-2xl font-extrabold">
                Create training settings
              </span>
              <span className="text-sm text-gray-500">
                Choose settings type you want to create as new
              </span>
            </div>
          </div>
        </DashboardContainer>
        <div className="container mx-auto px-3">
          <div className="flex flex-row items-center gap-0">
            <div
              onClick={() => this.setState({ sideNav: !this.state.sideNav })}
              className="lg:hidden bg-white rounded h-10 w-10 flex items-center justify-center mr-2 cursor-pointer hover:bg-primary-700 group border border-gray-300"
            >
              {this.state.sideNav === false ? (
                <FaBars className="text-2xl text-gray-500 group-hover:text-white" />
              ) : (
                <IoMdClose className="text-3xl text-gray-500 group-hover:text-white" />
              )}
            </div>
            <div
              onClick={() => this.setState({ pageType: "form" })}
              className={`bg-white cursor-pointer rounded-tl-md ${
                this.state.pageType === "form"
                  ? "font-bold text-primary-800 border-b-4 border-blue-400"
                  : "border-b-4 border-white"
              } px-3 py-2 flex flex-row items-center justify-center w-max`}
            >
              <div className="flex flex-row items-center gap-2">
                <span>Create new</span>
              </div>
            </div>
            <div
              onClick={() => this.setState({ pageType: "list" })}
              className={`bg-white cursor-pointer rounded-tr-md ${
                this.state.pageType === "list"
                  ? "font-bold text-primary-800 border-b-4 border-blue-400"
                  : "border-b-4 border-white"
              } px-3 py-2 flex flex-row items-center justify-center w-max`}
            >
              View list
            </div>
          </div>
        </div>
        {this.state.pageType === "list" && (
          <div className="mx-3 px-3 py-2 rounded border border-blue-300 mt-2 bg-blue-50">
            Table of List of {this.state.settings_type}
          </div>
        )}
        {this.state.pageType === "form" && (
          <div className="grid grid-cols-12 gap-2 container mx-auto">
            {this.state.sideNav === true && (
              <div className="absolute lg:relative z-50 mx-3 bg-white lg:bg-transparent col-span-12 lg:col-span-4 px-3 pt-2 mt-2 shadow-xl lg:shadow-none rounded-md pb-4">
                {SettingsTypesTitles.map((item, i) => (
                  <SettingsCategorySelect
                    key={i + 1}
                    title={item.title}
                    selected={
                      this.state.settings_type === item.key ? true : false
                    }
                    onClick={() => {
                      item.key === SettingsRouteType.subarea
                        ? this.FC_GetAreasIntervention(item.key)
                        : this.setState({ settings_type: item.key });
                      // this.setState({ sideNav: !this.state.sideNav });
                      if (
                        item.key === SettingsRouteType.trainees_category &&
                        (this.props.basic.roles === null ||
                          this.props.basic.roles.length === 0)
                      ) {
                        this.setState({ loading: true });
                        this.props.FC_GetAllSystemRoles(
                          (status: boolean, msg: string) => {
                            if (status === true) {
                              this.setState({ loading: false });
                            } else {
                              this.setState({
                                loading: false,
                                formError: { target: "main", msg: msg },
                              });
                            }
                          }
                        );
                      }
                    }}
                  />
                ))}
              </div>
            )}
            <div className="col-span-12 lg:col-span-8 p-3">
              {this.state.loading === true ||
              (this.state.areas_of_intervention === null &&
                (this.state.settings_type === SettingsRouteType.subarea ||
                  this.state.settings_type === SettingsRouteType.topic)) ? (
                <div className="h-full flex flex-col  animate__animated animate__fadeIn">
                  <DashboardContainer className="flex flex-col items-center justify-center py-6">
                    <Loading />
                    <div className="font-light text-lg">
                      Loading, please wait...
                    </div>
                  </DashboardContainer>
                </div>
              ) : (
                <div className="">
                  {componentType !== undefined && (
                    <CreateSettingsComponent
                      component_type={componentType.key}
                      areas={
                        this.state.areas_of_intervention === null
                          ? []
                          : this.state.areas_of_intervention
                      }
                    />
                  )}
                </div>
              )}

              {/* Update Settings */}
              {/* <UpdateSettings
                onSubmit={() => {}}
                component_type={SettingsRouteType.topic}
                id={"7"}
              /> */}
              {/* Create Settings */}
              {/* <CreateSettingsComponent
                component_type={SettingsRouteType.topic}
              /> */}
              {/* <CreateSettingsModal
                route_name={SettingsRouteType.area}
                onClose={() => {
                  console.log("Closed");
                }}
                onSubmit={() => {
                  console.log("submitted");
                }}
              /> */}
              {/* <UpdateSettingsModal
                id="7"
                route_name={SettingsRouteType.area}
                onClose={() => {
                  console.log("Closed");
                }}
                onSubmit={() => {
                  console.log("Updated");
                }}
              /> */}
            </div>
          </div>
        )}
      </div>
    );
  }
}
const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const CreateSettings = connect(mapStateToProps, {
  FC_GetAllSystemRoles,
})(_CreateSettings);
