import React, { Component } from "react";
import { BsArrowLeft, BsFolder2Open } from "react-icons/bs";
import { MdAdd, MdClose, MdError } from "react-icons/md";
import { RiSearchLine } from "react-icons/ri";
import {
  GetAllSubSectors,
  GetSubSectorsInterface,
} from "../../../actions/training_settings_create_edit";
import Button from "../../../components/Buttons/Button";
import Loading from "../../../components/Loading/Loading";
import SelectCustom from "../../../components/SelectCustom/SelectCustom";
import { search } from "../../../utils/functions";

interface OrganizationAreasSubSectorsProps {
  GoBack: () => void;
  added_sub_sectors: string[];
  removeSubSector: (id: string) => void;
  addSubSector: (id: string) => void;
  setSubAreas: (sub_areas: GetSubSectorsInterface) => void;
  previewApplication: () => void;
}
interface OrganizationAreasSubSectorsState {
  loading: boolean;
  error: string;
  sub_sectors: GetSubSectorsInterface[] | null;
  open_add_new: boolean;
  search_added_sub_sectors: string;
}

export class OrganizationAreasSubSectors extends Component<
  OrganizationAreasSubSectorsProps,
  OrganizationAreasSubSectorsState
> {
  constructor(props: OrganizationAreasSubSectorsProps) {
    super(props);

    this.state = {
      error: "",
      loading: false,
      sub_sectors: null,
      open_add_new: false,
      search_added_sub_sectors: "",
    };
  }
  GetSelectedSubSectors = () => {
    const response: GetSubSectorsInterface[] = [];
    const test =
      this.state.sub_sectors === null
        ? []
        : this.state.sub_sectors.filter(
            (itm) =>
              this.props.added_sub_sectors.find(
                (test) => test.toString() === itm.id.toString()
              ) !== undefined
          );
    for (const item of test) {
      if (response.find((itm) => itm.id === item.id) === undefined) {
        response.push(item);
      }
    }
    return response;
  };

  componentDidMount(): void {
    if (this.state.sub_sectors === null) {
      this.setState({ loading: true });
      GetAllSubSectors(
        (
          loading: boolean,
          res: {
            status: "success" | "error";
            msg: string;
            data: GetSubSectorsInterface[];
          } | null
        ) => {
          this.setState({ loading: loading });
          if (res !== null && res.status === "success") {
            this.setState({ sub_sectors: res.data, loading: false, error: "" });
          }
          if (res !== null && res.status === "error") {
            this.setState({ sub_sectors: [], loading: false, error: res.msg });
          }
        }
      );
    }
  }
  render() {
    if (this.state.loading === true || this.state.sub_sectors === null) {
      return (
        <div className="flex flex-col items-center justify-center py-4 px-2 gap-1">
          <Loading />
          <div>Loading, please wait...</div>
        </div>
      );
    }
    return (
      <div>
        {this.props.added_sub_sectors.length > 0 && (
          <div className="flex flex-row items-center justify-between gap-2 mb-4">
            <div className="ml-2">
              <div className="text-xl font-bold">
                List of training areas{" "}
                {this.props.added_sub_sectors.length > 0 && (
                  <span className="px-1 rounded text-base font-bold bg-accent-900 text-white">
                    {this.props.added_sub_sectors.length}
                  </span>
                )}
              </div>

              <div className="text-xs">
                List of all areas that will be focused while providing training
              </div>
            </div>
            <div>
              {/* <Button
                theme={`${
                  this.props.added_sub_sectors.length === 0
                    ? "accent"
                    : "accent-light"
                }`}
                className={`flex flex-row items-center ga-2 ${
                  this.props.added_sub_sectors.length === 0
                    ? "animate__animated animate__zoomIn"
                    : ""
                }`}
                onClick={() =>
                  this.setState({ open_add_new: !this.state.open_add_new })
                }
              >
                <div>
                  <MdAdd className="text-xl" />
                </div>
                <span>Add new area</span>
              </Button> */}
            </div>
          </div>
        )}
        <div>
          <div className="">
            {this.state.open_add_new === true && (
              <div className="relative w-full z-50">
                <div className="absolute right-0 left-0 animate__animated animate__fadeInUp animate__faster p-3 bg-white rounded-b-md">
                  <div className="text-xl font-bold mb-2">
                    Choose area from the list
                  </div>
                  <SelectCustom
                    loading={this.state.loading}
                    id={"id"}
                    title={"name"}
                    close={() => this.setState({ open_add_new: false })}
                    data={this.state.sub_sectors.map((item) => ({
                      id: item.id,
                      name: `${item.name}`,
                    }))}
                    click={(data: { id: string; name: string }) => {
                      const item = this.state.sub_sectors?.find(
                        (itm) => itm.id.toString() === data.id.toString()
                      );
                      this.props.addSubSector(data.id);
                      if (item !== undefined && item !== null) {
                        this.props.setSubAreas(item);
                      }
                      this.setState({
                        open_add_new: false,
                        error: "",
                      });
                    }}
                  />
                </div>
              </div>
            )}
          </div>
          {/* Search data */}
          <div className="mb-3">
            <div className="relative">
              <div className="pl-2">
                <RiSearchLine className="text-3xl absolute top-2 text-gray-400" />
              </div>
              <input
                type={"search"}
                className="px-3 py-3 pl-11 bg-white rounded border border-gray-500 w-full font-bold"
                placeholder="Search for added areas..."
                value={this.state.search_added_sub_sectors}
                onChange={(e) =>
                  this.setState({ search_added_sub_sectors: e.target.value })
                }
              />
            </div>
          </div>
          {/* List here */}
          <div className="" style={{ maxHeight: "350px", overflowY: "auto" }}>
            {this.GetSelectedSubSectors().length === 0 ? (
              <div className="flex flex-col items-center justify-center py-5 bg-gray-100 rounded-md m-2">
                <div className="text-2xl text-center font-extrabold">
                  Training areas
                </div>
                <div className="text-sm mb-4 text-gray-600 text-center">
                  List of all areas that will be focused while providing
                  training
                </div>
                {this.props.added_sub_sectors.length === 0 && (
                  <Button
                    theme={`${
                      this.props.added_sub_sectors.length === 0
                        ? "accent"
                        : "accent-light"
                    }`}
                    className={`flex flex-row items-center ga-3 ${
                      this.props.added_sub_sectors.length === 0
                        ? "animate__animated animate__zoomIn"
                        : ""
                    }`}
                    onClick={() =>
                      this.setState({ open_add_new: !this.state.open_add_new })
                    }
                  >
                    <div>
                      <MdAdd className="text-xl" />
                    </div>
                    <span>{"  "} Add new area</span>
                  </Button>
                )}
              </div>
            ) : search(
                this.GetSelectedSubSectors(),
                this.state.search_added_sub_sectors
              ).length === 0 ? (
              <div className="text-xl font-light px-3">No result found!</div>
            ) : (
              (
                search(
                  this.GetSelectedSubSectors(),
                  this.state.search_added_sub_sectors
                ) as GetSubSectorsInterface[]
              ).map((item, i) => (
                <div
                  key={i + 1}
                  className={`flex flex-row items-center justify-between bg-gray-100 text-black rounded-md p-2 mb-2 animate__animated animate__fadeIn`}
                >
                  <div className="flex flex-row items-center gap-2">
                    <div>
                      <div className="h-8 w-8 rounded-full bg-white font-light text-2xl flex items-center justify-center">
                        {i + 1}
                      </div>
                    </div>
                    <div className="flex flex-col">
                      <div className="text-base font-bold">{item.name}</div>
                      <div className="text-xs -mt-1">{item.description}</div>
                    </div>
                  </div>
                  <div>
                    <div
                      onClick={() =>
                        this.props.removeSubSector(item.id.toString())
                      }
                      className="cursor-pointer px-2 py-1 text-sm bg-white text-accent-900 rounded hover:bg-accent-800 hover:text-black border border-yellow-400"
                    >
                      Remove
                    </div>
                  </div>
                </div>
              ))
            )}
            {this.GetSelectedSubSectors().length > 0 && (
              <div
                onClick={() =>
                  this.setState({ open_add_new: !this.state.open_add_new })
                }
                className="cursor-pointer bg-accent-400 rounded-md p-1 hover:bg-accent-400 hover:text-accent-900 flex flex-row items-center gap-3 group animate__animated animate__slideInDown"
              >
                <div>
                  <div className="rounded-md bg-white border border-white group-hover:border-yellow-300 p-2 flex items-center justify-center">
                    <MdAdd className="text-2xl" />
                  </div>
                </div>
                <span className="text-lg font-light">Click to add an area</span>
              </div>
            )}
          </div>
          {this.state.error !== "" && (
            <div
              onClick={() => this.setState({ error: "" })}
              className="bg-red-100 px-2 py-2 rounded-md text-lg font-light flex flex-row items-center justify-between gap-2 mx-2 animate__animated animate__shakeX cursor-pointer"
            >
              <div className="flex flex-row items-center gap-3">
                <div>
                  <MdError className="text-3xl text-red-700" />
                </div>
                <span>{this.state.error}</span>
              </div>
              <div>
                <MdClose className="text-2xl" />
              </div>
            </div>
          )}
          <div className="flex flex-row items-center justify-end gap-2 mt-5">
            <Button
              theme="default"
              className="flex flex-row items-center gap-2"
              onClick={this.props.GoBack}
            >
              <div>
                <BsArrowLeft className="text-xl" />
              </div>
              <span>Go back</span>
            </Button>
            <Button
              theme={
                this.props.added_sub_sectors.length === 0
                  ? "accent-light"
                  : "accent"
              }
              className={`flex flex-row items-center gap-2 ${
                this.props.added_sub_sectors.length === 0
                  ? ""
                  : "animate__animated animate__bounceIn"
              } `}
              onClick={() => {
                if (this.props.added_sub_sectors.length === 0) {
                  this.setState({ error: "Please add areas!" });
                } else {
                  this.props.previewApplication();
                }
              }}
            >
              <div>
                <BsFolder2Open className="text-2xl" />
              </div>
              <span>Verify and Submit</span>
            </Button>
          </div>
        </div>
      </div>
    );
  }
}
