import React, { Component } from "react";
import { MdClose } from "react-icons/md";
import { Link } from "react-router-dom";
import {
  AccountRoleInterface,
  CreateTrainingProviderAccount,
  CreateTrainingProviderAccountInterface,
  DPMembershipApplicationGetInterface,
} from "../../../actions/dp_membership_application";
import Button from "../../../components/Buttons/Button";
import TextInput from "../../../components/Inputs/TextInput";
import Loading from "../../../components/Loading/Loading";
import { containsOnlyNumbers } from "../../../utils/functions";
import { validateEmail } from "../../../utils/validation";

interface RegisterAccountProps {
  developmentPartner: DPMembershipApplicationGetInterface;
  onCancel: () => void;
}
interface RegisterAccountState {
  loading: boolean;
  loading_data: boolean;
  error: {
    target:
      | "main"
      | "nid"
      | "first_name"
      | "middle_name"
      | "last_name"
      | "full_name"
      | "phone_number"
      | "email"
      | "role_id"
      | "development_partner_id"
      | "password"
      | "confirm_password";
    msg: string;
  } | null;
  success: string;
  nid: string;
  first_name: string;
  middle_name: string;
  last_name: string;
  full_name: string;
  phone_number: string;
  email: string;
  role_id: number | null;
  development_partner_id: string;
  account_levels: AccountRoleInterface[] | null;
  password: string;
  confirm_password: string;
}

export class RegisterAccount extends Component<
  RegisterAccountProps,
  RegisterAccountState
> {
  constructor(props: RegisterAccountProps) {
    super(props);

    this.state = {
      loading: false,
      error: null,
      success: "",
      development_partner_id: this.props.developmentPartner.id,
      email: "",
      first_name: "",
      full_name: "",
      last_name: "",
      middle_name: "",
      nid: "",
      phone_number: "",
      role_id: 33, //Training Provider Role
      loading_data: false,
      account_levels: null,
      password: "",
      confirm_password: "",
    };
  }

  componentDidMount(): void {
    if (
      this.state.account_levels === null ||
      this.state.account_levels.length === 0
    ) {
      this.setState({ loading_data: true });
      setTimeout(() => {
        this.setState({
          loading_data: false,
        });
      }, 2000);
    }
  }
  cleanForm = () => {
    this.setState({
      email: "",
      first_name: "",
      full_name: "",
      last_name: "",
      middle_name: "",
      nid: "",
      phone_number: "",
      role_id: null,
      password: "",
      confirm_password: "",
    });
  };
  SubmitData = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // Validation
    if (this.state.first_name === "") {
      return this.setState({
        error: { target: "first_name", msg: "Please fill the first name" },
      });
    }
    if (this.state.last_name === "") {
      return this.setState({
        error: { target: "last_name", msg: "Please fill the last name" },
      });
    }
    if (this.state.phone_number === "") {
      return this.setState({
        error: { target: "phone_number", msg: "Please fill phone number" },
      });
    }
    if (this.state.phone_number.length !== 10) {
      return this.setState({
        error: {
          target: "phone_number",
          msg: "This phone is invalid, it should be having 10 digits",
        },
      });
    }
    if (containsOnlyNumbers(this.state.phone_number) !== true) {
      return this.setState({
        error: {
          target: "phone_number",
          msg: "This phone should be numeric",
        },
      });
    }
    if (this.state.email === "") {
      return this.setState({
        error: { target: "email", msg: "Please fill the email" },
      });
    }
    if (validateEmail(this.state.email) !== true) {
      return this.setState({
        error: { target: "email", msg: "Please fill the valid email" },
      });
    }
    if (this.state.role_id === null || isNaN(this.state.role_id)) {
      return this.setState({
        error: { target: "role_id", msg: "Please select the role" },
      });
    }
    if (this.state.password === "") {
      return this.setState({
        error: { target: "password", msg: "Please fill the password" },
      });
    }
    if (this.state.confirm_password === "") {
      return this.setState({
        error: { target: "confirm_password", msg: "Please confirm password" },
      });
    }
    if (this.state.confirm_password !== this.state.password) {
      return this.setState({
        error: { target: "confirm_password", msg: "Password does not match" },
      });
    }
    // submit data
    const data: CreateTrainingProviderAccountInterface = {
      development_partner_id: this.state.development_partner_id,
      email: this.state.email,
      first_name: this.state.first_name,
      full_name:
        this.state.first_name +
        " " +
        this.state.middle_name +
        " " +
        this.state.last_name,
      last_name: this.state.last_name,
      middle_name: this.state.middle_name,
      nid: this.state.nid,
      phone_number: this.state.phone_number,
      role_id: this.state.role_id,
      password: this.state.password,
    };
    this.setState({ loading: true });
    CreateTrainingProviderAccount(
      data,
      (
        loading: boolean,
        res: { type: "success" | "error"; msg: string } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.type === "success") {
          this.setState({ success: res.msg, error: null, loading: false });
          this.cleanForm();
        }
        if (res !== null && res.type === "error") {
          this.setState({
            error: { target: "main", msg: res.msg },
            success: "",
            loading: false,
          });
        }
      }
    );
  };
  render() {
    if (this.state.loading_data === true) {
      return (
        <div className="p-8 flex flex-col items-center justify-center bg-white rounded-md my-2">
          <Loading />
          <div className="text-lg font-light text-center">
            Loading, please wait...
          </div>
        </div>
      );
    }
    return (
      <div className="bg-white p-2 rounded">
        <div className="bg-gra-100 py-2 flex flex-row items-center gap-2 rounded px-2 animate__animated animate__fadeIn">
          <div>
            <div
              onClick={() => {
                this.state.loading === false && this.props.onCancel();
              }}
              className="flex items-center justify-center"
            >
              {/* <MdAccountCircle className="text-4xl text-primary-700" /> */}
            </div>
          </div>
          <div className="flex flex-col">
            <div className="text-xl font-bold">
              Register account for authentication
            </div>
            {/* <div className="text-sm font-bold text-accent-900 bg-accent-400 border border-accent-500 rounded-full px-2 w-max">
              {this.props.developmentPartner.development_partner_name}
            </div> */}
          </div>
        </div>
        {/* Body */}
        {this.state.success !== "" ? (
          <div className="px-3 pb-3">
            <div className="cursor-pointer rounded-md px-3 py-2 font-bold bg-green-50 text-green-700 flex flex-row items-center justify-between w-full my-2">
              <div>
                <div>
                  Your account has registered successfully! Now you can login
                </div>
                <div className="mt-3 mb-2">
                  <Link to="/login">
                    <Button
                      theme="success"
                      className="animate__animated animate__zoomIn"
                    >
                      Continue Login
                    </Button>
                  </Link>
                </div>
              </div>
              <div>
                <MdClose className="text-2xl" />
              </div>
            </div>
          </div>
        ) : (
          <form onSubmit={this.SubmitData} className="w-full my-3 px-2 md:px-4">
            <div className="grid grid-cols-12 gap-2">
              <TextInput
                onChange={(e) =>
                  this.setState({ first_name: e.target.value, error: null })
                }
                value={this.state.first_name}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "first_name"
                    ? this.state.error.msg
                    : ""
                }
                label={"First name"}
                placeholder={"Ex: Damascene"}
                name={""}
                MainContainer_className="col-span-6"
                disabled={this.state.loading}
              />
              <TextInput
                onChange={(e) =>
                  this.setState({ last_name: e.target.value, error: null })
                }
                value={this.state.last_name}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "last_name"
                    ? this.state.error.msg
                    : ""
                }
                label={"Last name "}
                placeholder={"Ex: KAMANA"}
                name={""}
                MainContainer_className="col-span-6"
                disabled={this.state.loading}
              />
              <TextInput
                onChange={(e) => {
                  if (e.target.value.length > 10) {
                    this.setState({
                      error: {
                        target: "phone_number",
                        msg: "This phone is invalid, it should be having 10 digits",
                      },
                    });
                  }
                  this.setState({
                    phone_number: e.target.value,
                    error: null,
                  });
                }}
                value={this.state.phone_number}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "phone_number"
                    ? this.state.error.msg
                    : ""
                }
                label={"Phone number"}
                placeholder={"07XXXXXXXX"}
                name={""}
                MainContainer_className="col-span-6"
                disabled={this.state.loading}
              />
              <TextInput
                onChange={(e) =>
                  this.setState({ email: e.target.value, error: null })
                }
                value={this.state.email}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "email"
                    ? this.state.error.msg
                    : ""
                }
                label={"Email"}
                placeholder={"email@gmail.com"}
                name={""}
                MainContainer_className="col-span-6"
                disabled={this.state.loading}
              />
              <TextInput
                onChange={(e) =>
                  this.setState({ password: e.target.value, error: null })
                }
                value={this.state.password}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "password"
                    ? this.state.error.msg
                    : ""
                }
                label={"Password"}
                placeholder={""}
                name={""}
                MainContainer_className="col-span-6"
                disabled={this.state.loading}
                type="password"
              />
              <TextInput
                onChange={(e) =>
                  this.setState({
                    confirm_password: e.target.value,
                    error: null,
                  })
                }
                value={this.state.confirm_password}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "confirm_password"
                    ? this.state.error.msg
                    : ""
                }
                label={"Retype password"}
                placeholder={""}
                name={""}
                MainContainer_className="col-span-6"
                disabled={this.state.loading}
                type="password"
              />
            </div>
            {this.state.error !== null && this.state.error.target === "main" && (
              <div
                onClick={() => this.setState({ error: null, success: "" })}
                className="cursor-pointer px-3 py-2 font-bold bg-red-100 text-red-700 flex flex-row items-center justify-between w-full my-2"
              >
                <span>{this.state.error.msg}</span>
                <div>
                  <MdClose className="text-2xl" />
                </div>
              </div>
            )}

            <div className="flex flex-row items-center justify-end gap-2 mt-3">
              {this.state.loading === true && (
                <div>
                  <Loading />
                </div>
              )}
              <Button
                onClick={this.props.onCancel}
                type="button"
                theme="default"
                disabled={this.state.loading}
              >
                Go Back
              </Button>
              {/* <Button
              onClick={() => this.cleanForm()}
              type="button"
              theme="default"
              disabled={this.state.loading}
            >
              Reset form
            </Button> */}
              <Button
                type="submit"
                theme="accent"
                disabled={this.state.loading}
              >
                {this.state.loading === true
                  ? "Loading, please wait..."
                  : "Register Account"}
              </Button>
            </div>
          </form>
        )}
      </div>
    );
  }
}
