import React, { PureComponent } from "react";

import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";
import Button from "../../../components/Buttons/Button";
import { API_GetPlacementBySchoolUpdated, Auth, Basic } from "../../../actions";
import { ReportAcademicYearType } from "../../../types/PlacementReportTypes";
import {
  GetStaffsFiltered,
  GetTotalPeriods,
  TEACHING_LEVEL,
  UserInfoGet,
} from "./SchoolPlacement";
import { DATE, mayors_list } from "../../../utils/functions";
import { BsDownload } from "react-icons/bs";
import Loading from "../../../components/Loading/Loading";

const getFirstName = (
  data: {
    user_id: string;
    first_name: string;
    last_name: string;
    role_id: string;
    role: string;
  }[],
  role_id: string
) => {
  const test = data?.find((itm) => itm.role_id === role_id);
  const res =
    test === undefined ||
    test === null ||
    test.first_name === null ||
    test.last_name === null
      ? ""
      : test.first_name;
  return res.toString().toUpperCase();
};
const getLastName = (
  data: {
    user_id: string;
    first_name: string;
    last_name: string;
    role_id: string;
    role: string;
  }[],
  role_id: string
) => {
  const test = data?.find((itm) => itm.role_id === role_id);
  const res =
    test === undefined ||
    test === null ||
    test.last_name === null ||
    test.last_name === null
      ? ""
      : test.last_name;
  return res.toString().toUpperCase();
};

interface PDFgeneratorProps {
  data: API_GetPlacementBySchoolUpdated[];
  basic: Basic;
  selected_academic: ReportAcademicYearType | undefined;
  reportType: {
    key: TEACHING_LEVEL;
    value: string;
  };
  auth: Auth;
  selected_school: string;
  FC_GetSchoolHighestLevel: (school_code: string) => TEACHING_LEVEL | null;
}
interface PDFgeneratorState {
  loading: boolean;
  downloading: boolean;
  selected_level: TEACHING_LEVEL;
}

var numbering = 0;
var n_index = 0;

export class PDFgenerator extends PureComponent<
  PDFgeneratorProps,
  PDFgeneratorState
> {
  constructor(props: PDFgeneratorProps) {
    super(props);

    this.state = {
      loading: false,
      downloading: false,
      selected_level: this.props.reportType.key,
    };
  }

  returnStaffs = (data: UserInfoGet[]) => {
    return data.map((item, i) => {
      numbering = numbering + 1;
      n_index += 1;
      return {
        no: `${numbering}`,
        index: `${n_index}`,
        full_name: `${item.full_name}`,
        nid: `${item.nid}`,
        sex: `${item.sex}`,
        qualification: `${item.qualification}`,
        position: `${item.position}`,
        role: `${item.role}`,
        dob: `${item.dob}`,
        marital_status: `${item.marital_status}`,
        specialization: `${item.specialization}`,
        hired_date: `${item.hired_date}`,
        hours: `${
          item.teacher_hierarch === null
            ? 0
            : GetTotalPeriods(item.teacher_hierarch)
        }`,
        rssb_number: `${item.rssb_number}`,
        nationality: `${item.nationality}`,
        bank_name: `${item.bank_name}`,
        bank_account: `${item.bank_account}`,
      };
    });
  };

  createPage = (
    doc: any,
    dataItem: UserInfoGet[] | null,
    marginTop: number
  ) => {
    if (dataItem === null || dataItem.length === 0) {
      return autoTable(doc, {
        theme: "grid",
        showHead: true,
        useCss: true,
        styles: {
          lineColor: "gray",
          cellPadding: 4,
          fontSize: 12,
          fillColor: "#f3f3f3",
          halign: "left",
        },
        startY: marginTop,
        rowPageBreak: "avoid",
        headStyles: {
          fillColor: "white",
          textColor: "black",
          lineColor: "gray",
          fontSize: 6.5,
          lineWidth: 0.1,
        },
        columnStyles: { No: { halign: "left" } },
        columns: [{ header: "", dataKey: "no" }],
        body: [{ no: "No staff found!" }],
      });
    }
    return autoTable(doc, {
      theme: "grid",
      showHead: true,
      useCss: true,
      styles: {
        lineColor: "gray",
        cellPadding: 1,
        fontSize: 7,
        halign: "center",
      },
      startY: marginTop,
      rowPageBreak: "avoid",
      headStyles: {
        fillColor: "white",
        textColor: "black",
        lineColor: "gray",
        fontSize: 6.5,
        lineWidth: 0.1,
      },
      columnStyles: { No: { halign: "left" } },
      columns: [
        { header: "PB", dataKey: "no" },
        { header: "#", dataKey: "index" },
        { header: "Names", dataKey: "full_name" },
        { header: "ID", dataKey: "nid" },
        { header: "Sex", dataKey: "sex" },
        { header: "Qualification", dataKey: "qualification" },
        { header: "Position", dataKey: "position" },
        { header: "Occupation", dataKey: "role" },
        { header: "Date of birth", dataKey: "dob" },
        { header: "Martial status", dataKey: "marital_status" },
        { header: "Specialization", dataKey: "specialization" },
        { header: "Date of hire", dataKey: "hired_date" },
        { header: "Hours taught", dataKey: "hours" },
        { header: "RSSB Number", dataKey: "rssb_number" },
        { header: "Nationality", dataKey: "nationality" },
        { header: "Bank name", dataKey: "bank_name" },
        { header: "Bank account", dataKey: "bank_account" },
      ],
      body:
        dataItem === null
          ? []
          : dataItem === undefined
          ? []
          : dataItem.length === 0
          ? []
          : this.returnStaffs(
              (dataItem as UserInfoGet[]).filter(
                (itm) =>
                  itm.school.school_code === this.props.selected_school ||
                  this.props.selected_school === ""
              )
            ),
    });
  };

  ReturnPageCollection = (
    doc: any,
    data: API_GetPlacementBySchoolUpdated,
    type: TEACHING_LEVEL | "ADMINISTRATIVE",
    marginTop: number
  ) => {
    // Admin
    return this.createPage(
      doc,
      data.list === null
        ? null
        : (JSON.parse(GetStaffsFiltered(data, type).list) as UserInfoGet[]),
      marginTop
    );
  };

  GeneratePdfReport = async (callback: (loading: boolean) => void) => {
    callback(true);
    numbering = 0;
    const doc = new jsPDF("l", "mm", "a4");
    let currentPosition = 10;
    const dataFiltered = this.props.data.filter(
      (itm) =>
        itm.school_code === this.props.selected_school ||
        this.props.selected_school === ""
    );
    // Alphabetical sorting
    dataFiltered.sort(function (a, b) {
      if (a.school_name < b.school_name) {
        return -1;
      }
      if (a.school_name > b.school_name) {
        return 1;
      }
      return 0;
    });
    for (let data of dataFiltered) {
      n_index = 0;
      if (
        GetStaffsFiltered(data, this.props.reportType.key).list !== null &&
        (
          JSON.parse(
            GetStaffsFiltered(data, this.props.reportType.key).list
          ) as UserInfoGet[]
        ).length > 0
      ) {
        doc.setFontSize(10);
        doc.setFont("fontStyle", "normal");

        doc.text("REPUBLIC OF RWANDA", 12, 15);
        doc.text(`Report Date: ${DATE(new Date())}`, 240, 15);

        doc.text("PROVINCE: ", 12, 20);
        doc.setFont("fontStyle", "bold");
        doc.text(
          data.list !== null && data.list.length > 0
            ? (JSON.parse(data.list) as UserInfoGet[])[0] === undefined ||
              (JSON.parse(data.list) as UserInfoGet[])[0].location === undefined
              ? ""
              : `${
                  (JSON.parse(data.list) as UserInfoGet[])[0].location
                    ?.province_name
                } (${
                  (JSON.parse(data.list) as UserInfoGet[])[0].location
                    ?.province_code
                })`
            : "",
          32,
          20
        );

        doc.setFont("fontStyle", "normal");
        doc.text("DISTRICT: ", 12, 25);
        doc.setFont("fontStyle", "bold");
        doc.text(
          data.list !== null && data.list.length > 0
            ? (JSON.parse(data.list) as UserInfoGet[])[0] === undefined
              ? ""
              : `${
                  (JSON.parse(data.list) as UserInfoGet[])[0].location
                    ?.district_name
                } (${
                  (JSON.parse(data.list) as UserInfoGet[])[0].location
                    ?.district_code
                })`
            : "",
          31,
          25
        );

        doc.setFont("fontStyle", "normal");
        doc.text("SECTOR: ", 12, 30);
        doc.setFont("fontStyle", "bold");
        doc.text(
          data.list !== null && data.list.length > 0
            ? (JSON.parse(data.list) as UserInfoGet[])[0] === undefined
              ? ""
              : `${
                  (JSON.parse(data.list) as UserInfoGet[])[0].location
                    ?.sector_name
                }`
            : "",
          30,
          30
        );

        doc.setFont("fontStyle", "normal");
        doc.text("SCHOOL NAME: ", 12, 35);
        doc.setFont("fontStyle", "bold");
        doc.text(data.school_name, 41, 35);

        // Main titles
        doc.setFontSize(16);
        doc.setFont("fontStyle", "bolder");
        doc.setFont("fontStyle", "underline");
        doc.text(
          `Placement Report ${this.props.selected_academic?.academic_year_name}`,
          113,
          60
        );

        doc.setFontSize(18);
        doc.setFont("fontStyle", "bold");
        doc.setFont("fontWeight", "bold");
        doc.setTextColor("#025198");
        doc.text(
          `${this.props.reportType.value} SCHOOL STAFF PLACEMENT`,
          84,
          70
        );
        doc.setTextColor("#000");
        // ADMIN CONTAINER
        if (
          GetStaffsFiltered(data, "ADMINISTRATIVE").list !== null &&
          (
            JSON.parse(
              GetStaffsFiltered(data, "ADMINISTRATIVE").list
            ) as UserInfoGet[]
          ).length > 0 &&
          this.props.FC_GetSchoolHighestLevel(data.school_code) !== null &&
          this.props
            .FC_GetSchoolHighestLevel(data.school_code)
            ?.toLocaleLowerCase() ===
            this.props.reportType.key.toLocaleLowerCase()
        ) {
          doc.setFontSize(12);
          doc.setFont("fontStyle", "bold");
          doc.text(`ADMINISTRATIVE STAFF`, 15, 100);
          this.ReturnPageCollection(doc, data, "ADMINISTRATIVE", 105);
          // if (
          //   (
          //     JSON.parse(
          //       GetStaffsFiltered(data, this.props.reportType.key).list
          //     ) as UserInfoGet[]
          //   ).length > 0
          // ) {
          //   doc.addPage();
          // }
        }
        if (
          GetStaffsFiltered(data, "ADMINISTRATIVE").list !== null &&
          (
            JSON.parse(
              GetStaffsFiltered(data, "ADMINISTRATIVE").list
            ) as UserInfoGet[]
          ).length > 0 &&
          this.props
            .FC_GetSchoolHighestLevel(data.school_code)
            ?.toLocaleLowerCase() ===
            this.props.reportType.key.toLocaleLowerCase()
        ) {
          currentPosition = (doc as any).lastAutoTable.finalY as number;
        } else {
          currentPosition = 75;
        }
        // TEACHERS CONTAINER
        if (
          GetStaffsFiltered(data, this.props.reportType.key).list !== null &&
          (
            JSON.parse(
              GetStaffsFiltered(data, this.props.reportType.key).list
            ) as UserInfoGet[]
          ).length > 0
        ) {
          doc.setFontSize(12);
          doc.setFont("fontStyle", "bold");
          doc.text(
            `TEACHING STAFF`,
            15,
            GetStaffsFiltered(data, "ADMINISTRATIVE").list !== null &&
              (
                JSON.parse(
                  GetStaffsFiltered(data, "ADMINISTRATIVE").list
                ) as UserInfoGet[]
              ).length === 0
              ? 100
              : currentPosition + 30
          );
          this.ReturnPageCollection(
            doc,
            data,
            this.props.reportType.key,
            GetStaffsFiltered(data, "ADMINISTRATIVE").list !== null &&
              (
                JSON.parse(
                  GetStaffsFiltered(data, "ADMINISTRATIVE").list
                ) as UserInfoGet[]
              ).length === 0
              ? 105
              : currentPosition + 35
          );
          currentPosition = (doc as any).lastAutoTable.finalY as number;
        }

        if (currentPosition > 160) {
          doc.addPage();
          currentPosition = 30;
        }

        // Footer
        doc.setFontSize(10);
        // column one
        doc.setFont("fontWeight", "normal");
        doc.text("Prepared by", 15, currentPosition + 15);
        doc.setFont("fontWeight", "bold");
        doc.text(
          data.users?.find((itm) => itm.role_id === "2") === undefined
            ? "Not found!"
            : `${getFirstName(data.users, "2")} ${getLastName(
                data.users,
                "2"
              )}`,
          15,
          currentPosition + 30
        );
        doc.text(
          `Headteacher of ${
            data.school_name.length > 21 ? "" : data.school_name
          }`,
          15,
          currentPosition + 35
        );
        doc.text(
          `${data.school_name.length > 21 ? data.school_name : ""}`,
          15,
          currentPosition + 40
        );
        // column 2
        doc.setFont("fontWeight", "normal");
        doc.text("Verified by", 90, currentPosition + 15);
        doc.setFont("fontWeight", "bold");
        doc.text(
          data.users?.find((itm) => itm.role_id === "3") === undefined
            ? ""
            : `${getFirstName(data.users, "3")} ${getLastName(
                data.users,
                "3"
              )}`,
          90,
          currentPosition + 30
        );
        doc.text(`District Director of Education`, 90, currentPosition + 35);

        // column 3
        doc.setFont("fontWeight", "normal");
        doc.text("Approved by", 160, currentPosition + 15);
        doc.setFont("fontWeight", "bold");
        doc.text(
          `${mayors_list
            .find((itm) => itm.district_code === data.district_code)
            ?.mayor_family_name.toUpperCase()} ${mayors_list
            .find((itm) => itm.district_code === data.district_code)
            ?.mayor_first_name.toUpperCase()}`,
          160,
          currentPosition + 30
        );
        doc.text(
          `${
            data.district_code[0] === "1"
              ? "District Executive Administrator of"
              : "Mayor of "
          } ${
            data.district_code[0] !== "1"
              ? data.district_name + " district"
              : ""
          }`,
          160,
          currentPosition + 35
        );
        doc.text(
          `${
            data.district_code[0] === "1"
              ? data.district_name + " district"
              : ""
          }`,
          160,
          currentPosition + 40
        );

        // column 4
        doc.setFont("fontWeight", "normal");
        doc.text("Confirmed by", 230, currentPosition + 15);
        doc.setFont("fontWeight", "bold");
        doc.text(`${""}`, 230, currentPosition + 30);
        doc.text(`Rwanda Basic Education Board`, 230, currentPosition + 35);
        if (
          dataFiltered.length > 1 &&
          data.school_code !== dataFiltered[dataFiltered.length - 1].school_code
        ) {
          doc.addPage();
        }
      }
    }
    // this.setLoading(true);
    doc.save("Placement report.pdf", { returnPromise: true }).then(() => {
      this.setState({ loading: false });
    });
  };

  setLoading = (status: boolean) => this.setState({ loading: status });

  // DownloadReport = async () => {
  //   this.setLoading(true);
  //   try {
  //     const res = await this.GeneratePdfReport(this.setLoading);
  //     if (res.getPageInfo(1)) {
  //       // this.setLoading(false);
  //     }
  //   } catch (error: any) {
  //     console.log("error: ", { ...error });
  //   }
  // };

  // asyncPDF = () => {
  //   this.setLoading(true);
  //   this.DownloadReport().then(() => {
  //     this.setLoading(false);
  //   });
  // };

  render() {
    if (this.state.loading === true) {
      return (
        <div className="bg-white rounded-md p-4 flex flex-col items-center justify-center w-full">
          <Loading />
          <div className="text-xl font-light text-accent-900">
            Downloading report, please wait...
          </div>
        </div>
      );
    }
    return (
      <div>
        {this.state.downloading === true &&
        this.props.reportType.key === this.state.selected_level ? (
          <div className="text-accent-900">
            Your file is being downloaded, if it is taking long time,{" "}
            <span
              onClick={() => {
                this.setState({ downloading: true });
                this.GeneratePdfReport(this.setLoading);
              }}
              className="font-bold underline text-black cursor-pointer"
            >
              Click to download again!
            </span>
          </div>
        ) : (
          <Button
            theme="success"
            className="animate__animated animate__zoomIn"
            onClick={() => {
              this.setState({ downloading: true });
              this.GeneratePdfReport(this.setLoading);
              this.setState({ selected_level: this.props.reportType.key });
            }}
          >
            <div className="flex flex-row items-center gap-3">
              <div>
                <BsDownload className="text-3xl" />
              </div>
              <span>Download document</span>
            </div>
          </Button>
        )}
      </div>
    );
  }
}
