import axios from "axios";
import { APP_URL } from "../config/app.config";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";

export interface DocumentCandidate {
  techer_id: string | null;
  first_name: string | null;
  middle_name: string | null;
  last_name: string | null;
  full_name: string | null;
  role_id: string | null;
  role: string | null;
  school_from_id: string | null;
  school_name: string | null;
  requested_school_id: string | null;
  teacher_comment: string | null;
  martial_status: string | null;
  dob: string | null;
  hire_date: string | null;
  position_id: string | null;
  position_name: string | null;
  qualification_id: string | null;
  qualification_name: string | null;
  teacher_transfer_id: string | null;
  transfer_issue_status: string | null;
  district_to_id: string | null;
  distrct_from_id: string | null;
  transfer_issued: string | null;
  transfer_issue_comment: string | null;
  transfer_issued_date: string | null;
  disability: string | null;
  disability_surpoting_document: string | null;
  resident_district_id: string | null;
  residence: string | null;
  resident_district_name: string | null;
  resident_surpoting_document: string | null;
  requested_schools: {
    district_name: string | null;
    sector_name: string | null;
    school_name: string | null;
    school_code: string | null;
  }[];
  districts: {
    district_name: string | null;
    district_code: string | null;
  }[];
}

export interface GET_Documents_API {
  teacher_docs: DocumentCandidate[];
  next: null | string;
  previous: string | null;
  total_transfer: number | null;
}
export const FC_GetDocuments = async (
  {
    district_code,
    page,
    limit,
    status = "remain",
  }: {
    district_code: string;
    page: number;
    limit: number;
    status: DocumentsValidationType;
  },
  callBack: (
    status: boolean,
    data: GET_Documents_API | null,
    error: string
  ) => void
) => {
  try {
    setAxiosToken();
    let res = await axios.get<GET_Documents_API>(
      `${APP_URL}/teachertransfers/${status}/usersupportdocs/${district_code}/${page}/${limit}`
    );
    // teachertransfers/remain/usersupportdocs/:district_code/:page/:limit

    callBack(true, res.data, "");
  } catch (error) {
    callBack(false, null, errorToText(error));
  }
};

export const FC_ApproveDocument = async (
  {
    teacher_transfer_id,
    user_id,
    residence,
    disability,
  }: {
    teacher_transfer_id: string;
    user_id: string;
    residence: boolean;
    disability: boolean;
  },
  callBack: (status: boolean, data: any | null, error: string) => void
) => {
  try {
    setAxiosToken();
    let res = await axios.post(`${APP_URL}/teachertransfers/docvalidation`, {
      teacher_transfer_id: teacher_transfer_id,
      user_id: user_id,
      residence: `${residence}`,
      disability: `${disability}`,
    });

    callBack(true, res.data, "");
  } catch (error) {
    callBack(false, null, errorToText(error));
  }
};

export type DocumentsValidationType = "remain" | "complete";

export interface TransferDisabilityList {
  district_code: string;
  total: string;
}
export interface TransferDistricts {
  total: string;
  total_validated: string;
  districts: TransferDisabilityList[];
}

export const FC_TransferDocumentByDisability = async (
  callBack: (
    status: boolean,
    data: TransferDistricts | null,
    error: string
  ) => void
) => {
  try {
    setAxiosToken();
    let res = await axios.get<TransferDistricts>(
      `${APP_URL}/teachertransfer/validationdistrict`
    );

    callBack(true, res.data, "");
  } catch (error) {
    callBack(false, null, errorToText(error));
  }
};
