import React, { useState } from "react";
import { BiReset } from "react-icons/bi";
import { BsFillCheckSquareFill } from "react-icons/bs";
import { ImCheckboxUnchecked } from "react-icons/im";
import { MdSelectAll } from "react-icons/md";
import { GetSubSectorsInterface } from "../../actions/training_settings_create_edit";
import { search } from "../../utils/functions";

interface TagsListProps {
  data: GetSubSectorsInterface[];
  selected_sub_sector_id: string[];
  setTag: (sub_sector_id: string) => void;
  selectAllTags: () => void;
  loadSubSectors: () => void;
}

const TagsList = (props: TagsListProps) => {
  const [searchData, setSearchData] = useState("");

  const isSelected = (sub_sector_id: string) => {
    return props.selected_sub_sector_id.find((itm) => itm === sub_sector_id) ===
      undefined
      ? false
      : true;
  };
  return (
    <div>
      <div className="flex flex-row items-center justify-between gap-2">
        <div>
          <div className="font-bold text-lg mt-3">
            Topic areas{" "}
            <span className="bg-blue-500 text-white px-2 rounded-lg text-sm">
              {props.selected_sub_sector_id.length}
            </span>
          </div>
          <div className="text-sm -mt-1 mb-3 text-gray-500">
            Choose all areas where the topic belongs to
          </div>
        </div>
        <div>
          {props.data.length > 0 && (
            <div
              className={`border ${
                props.data.length > 0 &&
                props.data.length === props.selected_sub_sector_id.length
                  ? "bg-accent-400 border-yellow-500"
                  : "bg-gray-100 border-gray-300"
              } px-2 pr-3 py-2 rounded text-sm font-bold flex flex-row items-center justify-center gap-2 w-max cursor-pointer`}
              onClick={() => props.selectAllTags()}
            >
              <div>
                {props.data.length > 0 &&
                props.data.length === props.selected_sub_sector_id.length ? (
                  <BiReset className="text-xl" />
                ) : (
                  <MdSelectAll className="text-xl" />
                )}
              </div>
              <span>
                {props.data.length > 0 &&
                props.data.length === props.selected_sub_sector_id.length
                  ? "Unselect all"
                  : "Select all"}
              </span>
            </div>
          )}
        </div>
      </div>
      <div>
        {props.data.length === 0 ? (
          <div className="text-sm text-gray-500 font-bold bg-gray-100 rounded px-3 py-2 -my-2">
            <div className="italic">
              Tags are used to simplify search availability for this topic.
            </div>
            <div
              onClick={() => props.loadSubSectors()}
              className="bg-accent-400 text-accent-900 hover:border-yellow-800 hover:text-white px-2 py-1 rounded text-sm font-bold cursor-pointer hover:bg-accent-900 border border-yellow-400 w-max my-2"
            >
              Click to add tags
            </div>
          </div>
        ) : (
          <div>
            <div>
              <input
                type="text"
                className="px-3 py-3 text-sm rounded border border-gray-400 w-full mb-2"
                placeholder="Search..."
                value={searchData}
                onChange={(e) => setSearchData(e.target.value)}
              />
            </div>
            <div className="" style={{ maxHeight: "330px", overflowY: "auto" }}>
              {search(props.data, searchData).length === 0 ? (
                <div className="text-sm font-light">
                  No search result found!
                </div>
              ) : (
                (
                  search(props.data, searchData) as GetSubSectorsInterface[]
                ).map((item, i) => (
                  <div
                    key={i + 1}
                    onClick={() => props.setTag(item.id.toString())}
                    className={`px-3 py-2 rounded-md cursor-pointer ${
                      isSelected(item.id.toString()) === true
                        ? "bg-accent-400"
                        : "bg-gray-100"
                    }  hover:bg-yellow-50 hover:text-accent-900 mb-2 flex flex-row items-center gap-2 group`}
                  >
                    <div>
                      {isSelected(item.id.toString()) === true ? (
                        <BsFillCheckSquareFill className="text-xl text-accent-900" />
                      ) : (
                        <ImCheckboxUnchecked className="text-xl text-gray-400 group-hover:text-accent-900" />
                      )}
                    </div>
                    <span>{item.name}</span>
                  </div>
                ))
              )}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default TagsList;
