interface PlacementBooksProps {
  auth: Auth;
  basic: Basic;
  placement: Placement;
  FC_GetAcademics: (
    callBack: (
      status: boolean,
      data: ReportAcademicYearType[] | null,
      msg: string
    ) => void
  ) => void;
}
export interface Annex1ReportItem {
  qualification_id: string;
  qualification_name: string;
  teaching_level: TEACHING_LEVEL | null;
  teaching_level_id: string | null;
  total: string;
}
export interface Annex1ReportInterface {
  serving_teachers?: Annex1ReportItem[];
  new_teachers?: Annex1ReportItem[];
  vacants?: Annex1ReportItem[];
  users: {
    dde_id: string;
    first_name: string;
    middle_name: string;
    last_name: string;
    dde_name: string;
    phone_numbers: string;
  };
}

interface PlacementBooksState {
  loading: boolean;
  book_type: "placement_book" | "annex_1" | "annex_2" | "annex_3";
  error: {
    element: string;
    msg: string;
  } | null;
  selected_academic: string | null;
  selected_month: string;
  placement_report: API_GetPlacementBySchoolUpdated[] | null;
  selected_school_code: string;
  selected_district_code: string;
  open_select_school: boolean;
  districtList: DistrictList[] | null;
  open_select_district: boolean;
  open_select_role: boolean;
  selected_role: {
    role_id: string;
    role_name: string;
  }[];
  show_district_all_data: boolean;
  reportType: {
    key: TEACHING_LEVEL;
    value: string;
  };
  annex1Report: Annex1ReportInterface | null;
  district_placement_check: DistrictTotalsSummarySubmitted | null;
  loading_summary: boolean;
  show_district_summary: boolean;
  new_teachers: DistrictNewTeachersInterface[] | null;
  redirect: boolean;
  annex3Data: Annex3ToBePlaced | null;
}

