import { Action, ActionTypes } from "../actions";
import { TransferAlgorithmStore } from "../actions/transfer_algorithm.action";

// default state
const defaultState: TransferAlgorithmStore = {
  selected_district: null,
  placed_districts: [],
  placing_loading_status: false,
  transfers: null,
};

export const transferAlgorithmReducer = (
  state: TransferAlgorithmStore = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.GET_ALL_TRANSFERS_DETAILS:
      return {
        ...state,
        transfers: action.payload,
      };
    case ActionTypes.RUN_TRANSFER_ALGORITHM:
      return {
        ...state,
        placing_loading_status: action.payload,
      };
    case ActionTypes.LOGOUT:
      return defaultState;
    default:
      return state;
  }
};
