import { Page, Text, View, StyleSheet, Font } from "@react-pdf/renderer";
import { API_GetPlacementBySchoolUpdated, Auth, Basic } from "../../../actions";
import { ReportAcademicYearType } from "../../../types/PlacementReportTypes";
import SchoolPlacementFooter from "./SchoolPlacementFooter";
import {
  TEACHING_LEVEL,
  PlacementReportTypes,
  UserInfoGet,
} from "./SchoolPlacement";
import { DataElement } from "./DataElement";
import { DATE } from "../../../utils/functions";

interface ReportPageFormatProps {
  data: API_GetPlacementBySchoolUpdated;
  basic: Basic;
  selected_academic: ReportAcademicYearType | undefined;
  headTeacherNames: string;
  schoolName: string;
  districtName: string;
  ddeName: string;
  mayorName: string;
  reportType: {
    key: TEACHING_LEVEL;
    value: string;
  };
  auth: Auth;
  selected_school: string;
}

export const ReportPageFormat = (props: ReportPageFormatProps) => {
  return (
    <Page
      break={true}
      size="A4"
      style={styles.page}
      orientation="landscape"
      wrap={false}
    >
      {/* Heading */}
      <View>
        {/* General info */}
        <View style={{ flexDirection: "row", justifyContent: "space-between" }}>
          <View style={styles.section}>
            <Text style={styles.mb1}>REPUBLIC OF RWANDA</Text>
            <Text style={styles.mb1}>
              {props.data.list !== null &&
                props.data.list.length > 0 &&
                (JSON.parse(props.data.list) as UserInfoGet[])[0].location
                  ?.province_name}{" "}
              PROVINCE{" "}
            </Text>
            <Text style={[styles.mb1, { textTransform: "capitalize" }]}>
              {props.data.district_name} DISTRICT
            </Text>
            <Text style={styles.mb1}>
              {props.data.list !== null &&
                props.data.list.length > 0 &&
                (JSON.parse(props.data.list) as UserInfoGet[])[0].location
                  ?.sector_name}{" "}
              SECTOR
            </Text>
            <Text style={styles.mb1}>
              SCHOOL NAME: {props.data.school_name}
            </Text>
          </View>
          <View style={{ marginRight: "20px", marginTop: "5px" }}>
            <Text>Report date: {DATE(new Date(), "DD/MM/YYYY")}</Text>
          </View>
        </View>
        <View
          style={{
            flexDirection: "column",
            alignItems: "center",
            width: "100%",
            marginBottom: "20px",
          }}
        >
          <Text
            style={[
              styles.title,
              { fontSize: 16, textDecoration: "underline" },
            ]}
          >
            Placement Report{" "}
            {props.selected_academic !== undefined &&
              props.selected_academic.academic_year_name}
          </Text>
        </View>
        {props.auth.role?.role_id !== "2" && (
          <View
            style={{
              flexDirection: "column",
              alignItems: "center",
              width: "100%",
            }}
          >
            <Text style={[styles.title, { color: "#025198" }]}>
              {props.reportType.value} SCHOOL STAFF PLACEMENT
            </Text>
          </View>
        )}
      </View>
      {/* Admin */}
      <DataElement
        reportType={{
          key: "ADMINISTRATIVE" as unknown as TEACHING_LEVEL,
          value: "ADMINISTRATIVE",
        }}
        data={props.data}
        admins={true}
      />
      {/* Other users */}
      {props.auth.role?.role_id === "2" && props.selected_school !== "" ? (
        PlacementReportTypes.map((item, i) => (
          <DataElement
            key={i + 1}
            reportType={item}
            data={props.data}
            admins={false}
          />
        ))
      ) : (
        <DataElement
          reportType={props.reportType}
          data={props.data}
          admins={false}
        />
      )}
      {/* Footer */}
      <SchoolPlacementFooter
        headTeacherNames={
          props.data.users?.find((itm) => itm.role_id === "2") === undefined
            ? "Not found!"
            : `${
                props.data.users?.find((itm) => itm.role_id === "2")?.first_name
              } ${
                props.data.users?.find((itm) => itm.role_id === "2")?.last_name
              }`
        }
        schoolName={props.data.school_name}
        districtName={
          props.data.district_name === undefined
            ? "Not found!"
            : props.data.district_name
        }
        districtCode={
          props.data.district_code === undefined
            ? "Not found!"
            : props.data.district_code
        }
        ddeName={
          props.data.users?.find((itm) => itm.role_id === "3") === undefined
            ? "Not found!"
            : `${
                props.data.users?.find((itm) => itm.role_id === "3")?.first_name
              } ${
                props.data.users?.find((itm) => itm.role_id === "3")?.last_name
              }`
        }
        mayorName={props.mayorName}
      />
    </Page>
  );
};

// Create styles
Font.register({
  family: "myFont",
  src: "https://cdn.jsdelivr.net/npm/react-native-text-weight@0.6.1/Fonts/Roboto-Bold.ttf",
});

const styles = StyleSheet.create({
  page: {
    backgroundColor: "#fff",
    width: "100%",
    flexDirection: "column",
    fontSize: 10,
    margin: 0,
    marginTop: 0,
    marginBottom: 0,
    paddingLeft: 10,
    paddingRight: 10,
    paddingBottom: 30,
    paddingTop: 30,
  },
  flexRow: {
    flexDirection: "row",
    width: "100%",
  },
  flexCol: {
    flexDirection: "column",
    width: "100%",
  },
  section: {
    margin: 10,
    padding: 0,
    flexGrow: 1,
  },
  title: {
    fontSize: 14,
    textAlign: "center",
    fontFamily: "myFont",
  },
  textList: {
    fontSize: 10,
    flexDirection: "column",
  },
  mb1: {
    marginBottom: 4,
  },
  table: {
    width: "100%",
    flexDirection: "column",
    marginTop: "10px",
    fontSize: 7,
    marginBottom: "20px",
    textAlign: "center",
  },
  th: {
    border: "1px",
    borderColor: "black",
    borderWidth: "1px",
  },
  thead: {
    flexDirection: "row",
    width: "100%,",
    justifyContent: "flex-start",
    fontWeight: "bold",
  },
  tr: {
    flexDirection: "row",
    width: "100%,",
    justifyContent: "flex-start",
  },
  td: {
    borderColor: "black",
    borderTop: "0.2px",
    borderBottom: "0.2px",
    borderLeft: "0.5px",
    padding: "2px",
    paddingTop: "3px",
  },
  item: {
    marginTop: "30px",
  },
});
