import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from ".";
import { APP_URL } from "../config/app.config";
import { TransferRequestInterface } from "../containers/TransferApproval/TransferApproval";
import { setAxiosToken } from "../utils/AxiosToken";

export interface TransferAlgorithmStore {
  selected_district: string | null;
  placed_districts: string[];
  placing_loading_status: boolean;
  transfers: TransferRequestInterface[] | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export interface GetAllTransfersInterfaceAction {
  type: ActionTypes.GET_ALL_TRANSFERS_DETAILS;
  payload: TransferRequestInterface[];
}

export interface RunTransferAlgorithmAction {
  type: ActionTypes.RUN_TRANSFER_ALGORITHM;
  payload: boolean;
}

export const FC_Load_All_Transfers = (
  setLoading: (status: boolean) => void
) => {
  setLoading(true);
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<TransferRequestInterface[]>(
        `${APP_URL}/teachertransfers/districtcount`
      );
      dispatch<GetAllTransfersInterfaceAction>({
        type: ActionTypes.GET_ALL_TRANSFERS_DETAILS,
        payload: res.data,
      });
      setLoading(false);
    } catch (error: any) {
      console.log("error: ", { ...error });
      setLoading(false);
    }
  };
};

export const Run_Transfer_Algorithm = (
  setLoading: (status: boolean) => void
) => {
  return async (dispatch: Dispatch) => {
    setLoading(true);
    dispatch<RunTransferAlgorithmAction>({
      type: ActionTypes.RUN_TRANSFER_ALGORITHM,
      payload: true,
    });
    try {
      setAxiosToken();
      const res = await axios.post(`http://www.tmis.reb.rw/algo`);
      console.log("ReSS: ", res);
      if (res.status === 200) {
        setLoading(false);
        dispatch<RunTransferAlgorithmAction>({
          type: ActionTypes.RUN_TRANSFER_ALGORITHM,
          payload: false,
        });
      }
    } catch (error: any) {
      console.log("error: ", { ...error });
      setLoading(false);
      dispatch<RunTransferAlgorithmAction>({
        type: ActionTypes.RUN_TRANSFER_ALGORITHM,
        payload: false,
      });
    }
  };
};
