import React from "react";
import { Text, View, StyleSheet, Font } from "@react-pdf/renderer";
import {
  GetStaffsFiltered,
  GetTotalPeriods,
  TEACHING_LEVEL,
} from "./SchoolPlacement";
import { SchoolPlacementData } from "./SchoolPlacementData";

interface DataElementProps {
  reportType: {
    key: TEACHING_LEVEL;
    value: string;
  };
  data: any;
  admins: boolean;
}

export const DataElement = (props: DataElementProps) => {
  return JSON.parse(
    GetStaffsFiltered(
      props.data,
      props.admins === false ? props.reportType.key : "ADMINISTRATIVE"
    ).list
  ).length > 0 ? (
    <View style={styles.item}>
      <View
        style={{
          flexDirection: "column",
          alignItems: "flex-start",
          width: "100%",
          marginLeft: "20px",
          marginRight: "20px",
        }}
      >
        <Text style={{ color: "#000", fontFamily: "myFont" }}>
          {props.reportType.value}{" "}
          {props.reportType.value === "ADMINISTRATIVE" ? "" : "- Teaching"}{" "}
          staff
        </Text>
      </View>

      {/* Table */}
      {JSON.parse(
        GetStaffsFiltered(
          props.data,
          props.admins === false ? props.reportType.key : "ADMINISTRATIVE"
        ).list
      ).length > 0 ? (
        <SchoolPlacementData
          GetTotalPeriods={GetTotalPeriods}
          data={GetStaffsFiltered(
            props.data,
            props.admins === false ? props.reportType.key : "ADMINISTRATIVE"
          )}
          admins={false}
        />
      ) : (
        <View
          style={{
            backgroundColor: "#0101",
            fontSize: 13,
            fontWeight: "light",
            marginTop: "10px",
            borderRadius: 5,
            padding: "10px",
            marginRight: "10px",
            marginLeft: "10px",
          }}
        >
          <Text>No staffs found!</Text>
        </View>
      )}
    </View>
  ) : (
    <View></View>
  );
};

// Create styles
Font.register({
  family: "myFont",
  src: "https://cdn.jsdelivr.net/npm/react-native-text-weight@0.6.1/Fonts/Roboto-Bold.ttf",
});

