import { View, Text, Font, StyleSheet } from "@react-pdf/renderer";
import React from "react";

interface Annex1FooterProps {
  districtName: string;
  ddeName: string;
  mayorName: string;
  districtCode: string;
}

export const getProvinceFromCode = (district_code: string) => {
  let province_name = "";
  switch (district_code[0]) {
    case "1":
      province_name = "Kigali City";
      break;
    case "2":
      province_name = "Southern Province";
      break;
    case "3":
      province_name = "Western Province";
      break;
    case "4":
      province_name = "Northern Province";
      break;
    case "5":
      province_name = "Eastern Province";
      break;
  }
  return province_name;
};

export const Annex1Footer = (props: Annex1FooterProps) => {
  return (
    <View style={styles.container}>
      <View>
        <Text style={[styles.text, { marginBottom: "30px" }]}>
          Verified by:{" "}
        </Text>
        <View style={styles.text}>
          <Text style={styles.bold}>{props.ddeName}</Text>
          <Text style={{ marginTop: "5px" }}>
            District Director of Education
          </Text>
        </View>
      </View>
      <View>
        <Text style={[styles.text, { marginBottom: "30px" }]}>
          Approved by:{" "}
        </Text>
        <View style={styles.text}>
          <Text style={styles.bold}>{props.mayorName}</Text>
          <Text style={{ marginTop: "5px" }}>
            {props.districtCode[0] === "1"
              ? "District Executive Administrator"
              : "Mayor"}{" "}
            of {props.districtName + " district"}
          </Text>
          {props.districtCode[0] === "1" && (
            <Text style={{ marginTop: "5px" }}>
              {props.districtCode[0] !== "1"
                ? props.districtName + " district"
                : ""}
            </Text>
          )}
        </View>
      </View>
      <View>
        <Text style={[styles.text, { marginBottom: "30px" }]}>
          Confirmed by:{" "}
        </Text>
        <View style={styles.text}>
          <Text style={styles.bold}></Text>
          <Text style={[styles.bold, { marginTop: "15px" }]}>
            Rwanda Basic Education Board
          </Text>
        </View>
      </View>
    </View>
  );
};

// Create styles
Font.register({
  family: "myFont",
  src: "https://cdn.jsdelivr.net/npm/react-native-text-weight@0.6.1/Fonts/Roboto-Bold.ttf",
});

const styles = StyleSheet.create({
  container: {
    marginLeft: 20,
    marginRight: 20,
    marginTop: "100px",
    flexDirection: "row",
    justifyContent: "space-between",
  },
  bold: {
    fontFamily: "myFont",
  },
  title: {
    fontSize: 16,
  },
  text: {
    fontSize: 10,
  },
});
