import React, { useState } from "react";
import { ImCheckboxChecked } from "react-icons/im";
import { MdCheckBoxOutlineBlank } from "react-icons/md";
import Button from "../../../components/Buttons/Button";

interface SelectRoleProps {
  selected_role: {
    role_id: string;
    role_name: string;
  }[];
  roles_list: {
    role_id: string;
    role_name: string;
  }[];
  updateRoles: (data: { role_id: string; role_name: string }[]) => void;
}

const SelectRole = (props: SelectRoleProps) => {
  const [roles_temp, setRolesTemp] = useState(props.selected_role);
  return (
    <div>
      {props.roles_list.length === 0 ? (
        <div>{"No result found"}</div>
      ) : (
        <div className="grid grid-cols-2 gap-10 w-full">
          <div className="col-span-1">
            {props.roles_list.map((item, i) => (
              <div
                key={i + 1}
                onClick={() =>
                  roles_temp.find((itm) => itm.role_id === item.role_id)
                    ?.role_id === undefined
                    ? setRolesTemp([...roles_temp, item])
                    : setRolesTemp(
                        roles_temp.filter((itm) => itm.role_id !== item.role_id)
                      )
                }
                className={`flex flex-row items-center gap-3 rounded-md px-1 py-1 mb-2 cursor-pointer ${
                  roles_temp.find((itm) => itm.role_id === item.role_id)
                    ?.role_id === undefined
                    ? "hover:bg-gray-100"
                    : "bg-primary-100 text-primary-800"
                }`}
              >
                <div>
                  {roles_temp.find((itm) => itm.role_id === item.role_id)
                    ?.role_id === undefined ? (
                    <MdCheckBoxOutlineBlank className="text-2xl" />
                  ) : (
                    <ImCheckboxChecked className="text-2xl" />
                  )}
                </div>
                <div>
                  <span>{item.role_name}</span>
                </div>
              </div>
            ))}
          </div>
          {roles_temp.length > 0 && (
            <div className="col-span-1 p-5 bg-primary-100 rounded-md">
              <div className="text-lg mb-5">Selected roles order</div>
              <div>
                {roles_temp.map((item, i) => (
                  <div
                    key={i + 1}
                    className={`flex flex-row items-center gap-3 rounded-md px-2 py-1 ${
                      roles_temp.find((itm) => itm.role_id === item.role_id)
                        ?.role_id === undefined
                        ? ""
                        : "bg-primary-100 text-primary-800"
                    }`}
                  >
                    <div>
                      <div className="bg-primary-100">{i + 1}</div>
                    </div>
                    <div>
                      <span>{item.role_name}</span>
                    </div>
                  </div>
                ))}
              </div>
              <Button
                onClick={() => props.updateRoles(roles_temp)}
                theme={"primary"}
                className="mt-6"
              >
                Confirm
              </Button>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default SelectRole;
