import React, { PureComponent } from "react";

import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";
import Button from "../../../components/Buttons/Button";
import { Auth, Basic, DistrictList } from "../../../actions";
import { ReportAcademicYearType } from "../../../types/PlacementReportTypes";
import { DATE } from "../../../utils/functions";
import { BsDownload } from "react-icons/bs";
import Loading from "../../../components/Loading/Loading";
import { DistrictNewTeachersInterface } from "./NewTeachers";

interface PDFgeneratorProps {
  data: DistrictNewTeachersInterface[];
  basic: Basic;
  selected_academic: ReportAcademicYearType | undefined;
  auth: Auth;
  selected_school: string;
  selected_district: DistrictList;
}
interface PDFgeneratorState {
  loading: boolean;
  downloading: boolean;
}

var numbering = 0;

export class PDFgenerator extends PureComponent<
  PDFgeneratorProps,
  PDFgeneratorState
> {
  constructor(props: PDFgeneratorProps) {
    super(props);

    this.state = {
      loading: false,
      downloading: false,
    };
  }

  GetSchools = () => {
    var schools: {
      school_code: string;
      school_name: string;
      district_code: string;
      district_name: string;
    }[] = [];
    for (const item of this.props.data) {
      const test = schools.find((itm) => itm.school_code === item.school_code);
      if (test === undefined) {
        schools.push({
          school_code: item.school_code,
          school_name: item.school_name,
          district_code: "",
          district_name: "",
        });
      }
    }
    return schools;
  };

  returnStaffs = (data: DistrictNewTeachersInterface[]) => {
    return data.map((item, i) => {
      numbering = numbering + 1;
      return {
        index: `${i + 1}`,
        full_name: `${item.full_name}`,
        nid: `${item.nid}`,
        sex: `${item.sex}`,
        qualification: `${item.qualification_name}`,
        position: `${item.position_name}`,
        role: `${item.role}`,
        dob: `${item.dob}`,
        marital_status: `${item.marital_status}`,
        specialization: `${item.specialization_name}`,
        date_to_school: `${item.start_date_in_the_school}`,
        rssb_number: `${item.rssb_number}`,
        bank_name: `${item.bank}`,
        bank_account: `${item.bank_account}`,
        user_status: "New",
      };
    });
  };

  createPage = (
    doc: any,
    dataItem: DistrictNewTeachersInterface[],
    marginTop: number
  ) => {
    console.log("Print: ", dataItem);
    if (dataItem.length === 0) {
      return autoTable(doc, {
        theme: "grid",
        showHead: true,
        useCss: true,
        styles: {
          lineColor: "gray",
          cellPadding: 4,
          fontSize: 12,
          fillColor: "#f3f3f3",
          halign: "left",
        },
        startY: marginTop,
        rowPageBreak: "avoid",
        headStyles: {
          fillColor: "white",
          textColor: "black",
          lineColor: "gray",
          fontSize: 6.5,
          lineWidth: 0.1,
        },
        columnStyles: { No: { halign: "left" } },
        columns: [{ header: "", dataKey: "no" }],
        body: [{ no: "No staff found!" }],
      });
    }
    return autoTable(doc, {
      theme: "grid",
      showHead: true,
      useCss: true,
      styles: {
        lineColor: "gray",
        cellPadding: 1,
        fontSize: 7,
        halign: "center",
      },
      startY: marginTop,
      rowPageBreak: "avoid",
      headStyles: {
        fillColor: "white",
        textColor: "black",
        lineColor: "gray",
        fontSize: 6.5,
        lineWidth: 0.1,
      },
      columnStyles: { No: { halign: "left" } },
      columns: [
        { header: "#", dataKey: "index" },
        { header: "Names", dataKey: "full_name" },
        { header: "ID", dataKey: "nid" },
        { header: "Sex", dataKey: "sex" },
        { header: "Qualification", dataKey: "qualification" },
        { header: "Position", dataKey: "position" },
        { header: "Occupation", dataKey: "role" },
        { header: "Date of birth", dataKey: "dob" },
        { header: "Martial status", dataKey: "marital_status" },
        { header: "Specialization", dataKey: "specialization" },
        { header: "Date to hire", dataKey: "date_to_school" },
        { header: "RSSB Number", dataKey: "rssb_number" },
        { header: "Bank name", dataKey: "bank_name" },
        { header: "Bank account", dataKey: "bank_account" },
        { header: "Status", dataKey: "user_status" },
      ],
      body:
        dataItem === null
          ? []
          : dataItem === undefined
          ? []
          : dataItem.length === 0
          ? []
          : this.returnStaffs(
              dataItem.filter(
                (itm) =>
                  itm.school_code === this.props.selected_school ||
                  this.props.selected_school === ""
              )
            ),
    });
  };

  ReturnPageCollection = (
    doc: any,
    data: DistrictNewTeachersInterface[],
    marginTop: number
  ) => {
    // Admin
    return this.createPage(doc, data, marginTop);
  };

  GeneratePdfReport = async (callback: (loading: boolean) => void) => {
    callback(true);
    const doc = new jsPDF("l", "mm", "a4");

    for (let data of this.GetSchools()) {
      doc.setFontSize(10);
      doc.setFont("fontStyle", "normal");

      doc.text("REPUBLIC OF RWANDA", 12, 15);
      doc.text(`Report Date: ${DATE(new Date())}`, 240, 15);

      doc.text("PROVINCE: ", 12, 20);
      doc.setFont("fontStyle", "bold");
      doc.text(
        `${
          this.props.data.find((itm) => itm.school_code === data.school_code)
            ?.province_name
        }`,
        32,
        20
      );

      doc.setFont("fontStyle", "normal");
      doc.text("DISTRICT: ", 12, 25);
      doc.setFont("fontStyle", "bold");
      doc.text(this.props.selected_district.district_name, 31, 25);

      doc.setFont("fontStyle", "normal");
      doc.text("SECTOR: ", 12, 30);
      doc.setFont("fontStyle", "bold");
      doc.text(
        `${
          this.props.data.find((itm) => itm.school_code === data.school_code)
            ?.sector_name
        }`,
        30,
        30
      );

      doc.setFont("fontStyle", "normal");
      doc.text("SCHOOL NAME: ", 12, 35);
      doc.setFont("fontStyle", "bold");
      doc.text(data.school_name, 41, 35);

      // Main titles
      doc.setFontSize(16);
      doc.setFont("fontStyle", "bolder");
      doc.setFont("fontStyle", "underline");

      doc.setFontSize(18);
      doc.setFont("fontStyle", "bold");
      doc.setFont("fontWeight", "bold");
      doc.setTextColor("#025198");
      doc.text(
        `ANNEX 2 REPORT FOR NEW TEACHERS ${new Date().getFullYear()}-${
          new Date().getFullYear() + 1
        }`,
        80,
        70
      );
      doc.setTextColor("#000");

      // TEACHERS CONTAINER
      const teachers = this.props.data.filter(
        (itm) => itm.school_code === data.school_code
      );
      if (teachers.length > 0) {
        doc.setFontSize(12);
        doc.setFont("fontStyle", "bold");
        this.ReturnPageCollection(doc, teachers, 90);
        // currentPosition = (doc as any).lastAutoTable.finalY as number;
        if (
          this.GetSchools() !== null &&
          this.GetSchools().length > 0 &&
          data.school_code !==
            this.GetSchools()![this.GetSchools()!.length - 1].school_code
        ) {
          doc.addPage();
        }
      }
    }
    // this.setLoading(true);
    doc
      .save("Annex 2 Report | New teachers.pdf", { returnPromise: true })
      .then(() => {
        this.setState({ loading: false });
      });
  };

  setLoading = (status: boolean) => this.setState({ loading: status });

  render() {
    if (this.state.loading === true) {
      return (
        <div className="bg-white rounded-md p-4 flex flex-col items-center justify-center w-full">
          <Loading />
          <div className="text-xl font-light text-accent-900">
            Downloading report, please wait...
          </div>
        </div>
      );
    }
    return (
      <div>
        {this.state.downloading === true ? (
          <div className="text-accent-900">
            Your file is being downloaded, if it is taking long time,{" "}
            <span
              onClick={() => {
                this.setState({ downloading: true });
                this.GeneratePdfReport(this.setLoading);
              }}
              className="font-bold underline text-black cursor-pointer"
            >
              Click to download again!
            </span>
          </div>
        ) : (
          <Button
            theme="success"
            className="animate__animated animate__zoomIn"
            onClick={() => {
              this.setState({ downloading: true });
              this.GeneratePdfReport(this.setLoading);
            }}
          >
            <div className="flex flex-row items-center gap-3">
              <div>
                <BsDownload className="text-3xl" />
              </div>
              <span>Download document</span>
            </div>
          </Button>
        )}
      </div>
    );
  }
}
