import React, { Component } from "react";
import { BsCheckCircleFill } from "react-icons/bs";
import { SystemRole } from "../../actions";
import Button from "../../components/Buttons/Button";
import SelectCustom from "../../components/SelectCustom/SelectCustom";

interface SelectRolesProps {
  systemRoles: SystemRole[];
  addedRoles: SystemRole[];
  addRole: (role: SystemRole) => void;
}
interface SelectRolesState {
  loading: boolean;
  openAddRole: boolean;
}

export class SelectRoles extends Component<SelectRolesProps, SelectRolesState> {
  constructor(props: SelectRolesProps) {
    super(props);

    this.state = {
      loading: false,
      openAddRole: false,
    };
  }
  render() {
    return (
      <div>
        <div className="">
          <div className="flex flex-row items-center justify-between mb-1">
            <div className="">
              Trainees roles{" "}
              <span className="bg-accent-400 px-1 rounded-md font-bold text-sm text-accent-900">
                Total: {this.props.addedRoles.length}
              </span>
            </div>
            {this.props.addedRoles.length > 0 &&
              this.state.openAddRole === false && (
                <div
                  onClick={() => this.setState({ openAddRole: true })}
                  className="bg-primary-700 hover:bg-primary-800 text-white text-sm px-2 py-1 rounded cursor-pointer w-max border border-primary-700 font-bold"
                >
                  Add role
                </div>
              )}
          </div>
          <div className="relative">
            {/* Roles list */}
            {this.state.openAddRole === true && (
              <div className="" style={{ zIndex: 9 }}>
                <SelectCustom
                  loading={this.state.loading}
                  id={"id"}
                  title={"name"}
                  close={() => this.setState({ openAddRole: false })}
                  data={this.props.systemRoles
                    .map((item) => ({
                      id: item.role_id,
                      name: item.role,
                    }))
                    .filter(
                      (itm) =>
                        this.props.addedRoles.find(
                          (test) => test.role_id === itm.id
                        ) === undefined
                    )}
                  click={(data: { id: string; name: string }) => {
                    const getItem = this.props.systemRoles.find(
                      (itm) => itm.role_id === data.id
                    );
                    if (getItem !== undefined) {
                      this.props.addRole(getItem);
                      this.setState({ openAddRole: false });
                    }
                  }}
                />
              </div>
            )}
            {this.props.addedRoles.length === 0
              ? this.state.openAddRole === false && (
                  <div className="bg-gray-100 rounded flex flex-col items-center justify-center p-3 mb-2">
                    <div className="text-gray-600 mb-2">
                      No roles added for trainee's category
                    </div>
                    <Button
                      onClick={() => this.setState({ openAddRole: true })}
                      theme="accent-light"
                      type="button"
                    >
                      Add role
                    </Button>
                  </div>
                )
              : this.props.addedRoles.map((item, i) => (
                  <div
                    key={i + 1}
                    className="flex flex-row items-center justify-between gap-2 border-b border-gray-300 py-2"
                  >
                    <div className="flex flex-row items-center gap-2">
                      <div>
                        <BsCheckCircleFill className="text-accent-900" />
                      </div>
                      <div>{item.role}</div>
                    </div>
                    <div>
                      <div
                        onClick={() => this.props.addRole(item)}
                        className="bg-red-100 text-red-800 text-sm px-2 py-1 rounded cursor-pointer w-max"
                      >
                        Remove
                      </div>
                    </div>
                  </div>
                ))}
          </div>
        </div>
      </div>
    );
  }
}

export default SelectRoles;
