import React, { Component } from "react";
import { AiOutlineLoading3Quarters, AiOutlineReload } from "react-icons/ai";
import { BiCheckCircle, BiErrorAlt } from "react-icons/bi";
import { BsArrowRight, BsCheckSquareFill } from "react-icons/bs";
import { MdClose, MdOutlineSave } from "react-icons/md";
import { connect } from "react-redux";
import { Auth, Basic, FC_GetAllSystemRoles, SystemRole } from "../../actions";
import {
  GetAllSubSectors,
  GetAreasOfIntervention,
  GetAreasOfInterventionInterface,
  GetSubSectorsInterface,
  HandleTrainingSettingsData,
  HandleTrainingSettingsDataInterface,
} from "../../actions/training_settings_create_edit";
import Button from "../../components/Buttons/Button";
import TextArea from "../../components/Inputs/TextArea";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import SelectCustom from "../../components/SelectCustom/SelectCustom";
import { StoreState } from "../../reducers";
import {
  GetAreaAsParent,
  GetComponentType,
  SettingsRouteType,
} from "./CreateSettings";
import SelectRoles from "./SelectRoles";
import TagsList from "./TagsList";

interface CreateSettingsComponentProps {
  auth: Auth;
  basic: Basic;
  component_type: SettingsRouteType;
  onSubmit?: () => void;
  isModal?: boolean;
  onCloseModal?: () => void;
  update_data?: HandleTrainingSettingsDataInterface;
  areas?: GetAreasOfInterventionInterface[];
  FC_GetAllSystemRoles: (
    callBack: (status: boolean, msg: string) => void
  ) => void;
}
interface CreateSettingsComponentState {
  loading: boolean;
  component_name: string;
  name: string;
  description: string;
  foreign_value: {
    id: string;
    name: string;
  } | null;
  formError: {
    target: "foreign_value" | "name" | "description" | "main";
    msg: string;
  } | null;
  success: string;
  openSelectionOfParent: boolean;
  is_mandatory: boolean;
  tags: string[];
  sub_sectors: GetSubSectorsInterface[];
  areas_of_intervention: GetAreasOfInterventionInterface[] | null;
  addedRoles: SystemRole[];
}

export class _CreateSettingsComponent extends Component<
  CreateSettingsComponentProps,
  CreateSettingsComponentState
> {
  constructor(props: CreateSettingsComponentProps) {
    super(props);

    this.state = {
      loading: false,
      component_name: "",
      formError: null,
      success: "",
      name:
        this.props.update_data !== undefined ? this.props.update_data.name : "",
      description:
        this.props.update_data !== undefined &&
        this.props.update_data.description !== undefined
          ? this.props.update_data.description
          : "",
      foreign_value:
        this.props.update_data !== undefined
          ? {
              id:
                this.props.update_data.intervention_area_id === undefined
                  ? ""
                  : this.props.update_data.intervention_area_id.toString(),
              name:
                this.props.update_data.intervention_area === undefined
                  ? ""
                  : this.props.update_data.intervention_area.toString(),
            }
          : null,
      openSelectionOfParent: false,
      is_mandatory:
        this.props.update_data === undefined ||
        this.props.update_data.is_mandatory === undefined
          ? false
          : this.props.update_data.is_mandatory,
      tags:
        this.props.update_data === undefined ||
        this.props.update_data.tags === undefined
          ? []
          : this.props.update_data.tags,
      sub_sectors: [],
      areas_of_intervention:
        this.props.areas === undefined ? [] : this.props.areas,
      addedRoles:
        this.props.update_data === undefined ||
        this.props.update_data.roles === undefined
          ? []
          : this.props.update_data.roles === null
          ? []
          : this.props.update_data.roles,
    };
  }
  // Add new role
  SetAddNewRole = (role: SystemRole) => {
    if (
      this.state.addedRoles.find((itm) => itm.role_id === role.role_id) ===
      undefined
    ) {
      this.setState({ addedRoles: [...this.state.addedRoles, role] });
    } else {
      this.setState({
        addedRoles: this.state.addedRoles.filter(
          (itm) => itm.role_id !== role.role_id
        ),
      });
    }
  };
  //   Update name field
  HandleName = (name: string) => {
    this.setState({
      name: name,
      formError: null,
      success: "",
    });
  };
  //   Update description field
  HandleDescription = (description: string) => {
    this.setState({
      description: description,
      formError: null,
      success: "",
    });
  };

  setTag = (sub_sector_id: string) => {
    if (this.state.tags.find((itm) => itm === sub_sector_id) !== undefined) {
      this.setState({
        tags: this.state.tags.filter((itm) => itm !== sub_sector_id),
      });
    } else {
      this.setState({ tags: [...this.state.tags, sub_sector_id] });
    }
  };

  selectAllTags = () => {
    this.setState({
      tags:
        this.state.sub_sectors.length === this.state.tags.length
          ? []
          : this.state.sub_sectors.map((itm) => itm.id.toString()),
    });
  };

  GetSubSectors = () => {
    this.setState({ loading: true });
    GetAllSubSectors(
      (
        loading: boolean,
        res: {
          status: "success" | "error";
          msg: string;
          data: GetSubSectorsInterface[];
        } | null
      ) => {
        this.setState({
          loading: loading,
          formError: null,
          success: "",
          openSelectionOfParent: false,
        });
        if (res !== null) {
          if (res.status === "success") {
            this.setState({ loading: false, sub_sectors: res.data });
          } else {
            this.setState({
              loading: false,
              sub_sectors: [],
              formError: {
                target: "main",
                msg: res.msg,
              },
            });
          }
        }
      }
    );
  };

  GetParentData = () => {
    if (this.state.areas_of_intervention !== null) {
      return GetAreaAsParent(
        this.state.areas_of_intervention,
        GetComponentType(this.props.component_type)
      );
    }
    return [];
  };

  // Submit the form
  SubmitForm = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (this.state.name === "" || this.state.name.length < 2) {
      return this.setState({
        formError: { target: "name", msg: "Please fill name or title" },
        success: "",
      });
    }
    if (this.state.foreign_value === null && this.GetParentData().length > 0) {
      return this.setState({
        formError: { target: "foreign_value", msg: "Please select category" },
        success: "",
      });
    }
    if (
      this.state.tags.length === 0 &&
      GetComponentType(this.props.component_type)!.hasTags === true
    ) {
      return this.setState({
        formError: { target: "main", msg: "Please select tags" },
        success: "",
      });
    }
    if (
      (this.state.description === "" || this.state.description.length < 2) &&
      GetComponentType(this.props.component_type)!.hasDescription === true
    ) {
      return this.setState({
        formError: { target: "description", msg: "Please fill description" },
        success: "",
      });
    }
    // if (
    //   this.state.addedRoles.length === 0 &&
    //   GetComponentType(this.props.component_type)!.hasRoles === true
    // ) {
    //   return this.setState({
    //     formError: { target: "main", msg: "Please select roles" },
    //     success: "",
    //   });
    // }
    this.setState({
      loading: true,
      success: "",
      formError: null,
      openSelectionOfParent: false,
    });
    HandleTrainingSettingsData(
      this.props.update_data === undefined ? "add" : "update",
      {
        id:
          this.props.update_data === undefined
            ? undefined
            : this.props.update_data.id === undefined
            ? undefined
            : this.props.update_data.id,
        intervention_area_id: this.state.foreign_value?.id,
        name: this.state.name,
        description: this.state.description,
        is_mandatory: this.state.is_mandatory,
        tags: this.state.tags.length === 0 ? undefined : this.state.tags,
        roles: this.state.addedRoles,
      },
      GetComponentType(this.props.component_type)!.key, //Route
      (
        loading: boolean,
        res: {
          status: "success" | "error";
          msg: string;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (loading === false) {
          if (res !== null) {
            if (res.status === "success") {
              this.setState({
                success: `${
                  GetComponentType(this.props.component_type)!.title
                } ${
                  this.props.update_data === undefined
                    ? "registered"
                    : "updated"
                } successfully!`,
                formError: null,
                name:
                  this.props.update_data === undefined ? "" : this.state.name,
                description:
                  this.props.update_data === undefined
                    ? ""
                    : this.state.description,
                is_mandatory:
                  this.props.update_data === undefined
                    ? false
                    : this.state.is_mandatory,
                tags:
                  this.props.update_data === undefined ? [] : this.state.tags,
              });
              if (this.props.onSubmit) {
                this.props.onSubmit();
              }
            }
            if (res.status === "error") {
              this.setState({
                success: "",
                formError: { target: "main", msg: res.msg },
              });
            }
          }
        }
      }
    );
  };
  ForeignValue = () => {
    if (
      GetComponentType(this.props.component_type)!.key ===
        SettingsRouteType.topic &&
      this.state.areas_of_intervention !== null
    ) {
      const test = this.SetParentForTopic(
        this.state.tags,
        this.state.areas_of_intervention
      );
      console.log("Tes ", this.state.areas_of_intervention);
      if (test !== null) {
        return {
          name: test.name,
          id: test.id.toString(),
        };
      }
    }
    return this.GetParentData().find(
      (itm) =>
        this.state.foreign_value !== null &&
        itm.id.toString() === this.state.foreign_value.id.toString()
    );
  };

  resetForm = () => {
    this.setState({
      name: "",
      description: "",
      foreign_value: null,
      openSelectionOfParent: false,
      loading: false,
      success: "",
      formError: null,
      tags: [],
    });
  };

  // Get Selected for topic
  SetParentForTopic = (
    topics: string[],
    areas: GetAreasOfInterventionInterface[]
  ) => {
    const test = areas.find(
      (itm) =>
        topics.find((ts) => ts.toString() === itm.id.toString()) !== undefined
      // Find area for the selected topic ??????
    );
    if (test !== undefined) {
      return test;
    }
    return null;
  };

  FC_GetAreasIntervention = (selected_key: SettingsRouteType) => {
    if (
      selected_key === SettingsRouteType.subarea ||
      selected_key === SettingsRouteType.topic
    ) {
      this.setState({ loading: true, formError: null, success: "" });
      GetAreasOfIntervention(
        (
          loading: boolean,
          res: {
            status: "success" | "error";
            msg: string;
            data: GetAreasOfInterventionInterface[];
          } | null
        ) => {
          this.setState({ loading: loading });
          if (loading === false && res !== null && res.data.length > 0) {
            this.setState({
              areas_of_intervention: res.data,
              loading: false,
            });
          }
          if (loading === false && res !== null && res.status === "error") {
            this.setState({
              success: "",
              formError: { target: "main", msg: res.msg },
            });
          }
        }
      );
    }
  };

  componentDidMount = () => {
    if (
      this.props.component_type === SettingsRouteType.trainees_category &&
      (this.props.basic.roles === null || this.props.basic.roles.length === 0)
    ) {
      this.setState({ loading: true });
      this.props.FC_GetAllSystemRoles((status: boolean, msg: string) => {
        if (status === true) {
          this.setState({ loading: false });
        } else {
          this.setState({
            loading: false,
            formError: { target: "main", msg: msg },
          });
        }
      });
    }
    if (
      this.props.update_data !== undefined &&
      GetComponentType(this.props.component_type)!.hasTags === true
    ) {
      GetComponentType(this.props.component_type)!.hasTags === true &&
        this.ForeignValue() !== undefined &&
        this.GetSubSectors();
    }
    if (this.props.component_type === SettingsRouteType.topic) {
      this.GetSubSectors();
    }
    if (this.props.areas === undefined) {
      this.FC_GetAreasIntervention(this.props.component_type);
    }
  };
  render() {
    if (
      this.state.areas_of_intervention === null &&
      (GetComponentType(this.props.component_type)!.key ===
        SettingsRouteType.subarea ||
        GetComponentType(this.props.component_type)!.key ===
          SettingsRouteType.topic)
    ) {
      return (
        <div className="flex flex-col items-center justify-center">
          <Loading />
          <div className="text-center">Loading, please wait...</div>
        </div>
      );
    }
    return (
      <div className="bg-white border border-gray-200 rounded p-5 px-6 pb-6 animate__animated animate__fadeIn">
        <div className="flex flex-row items-center justify-between">
          <div className="text-xl font-extrabold text-primary-800">
            {this.props.update_data !== undefined
              ? `Update ${this.state.name} details`
              : `Create new ${
                  GetComponentType(this.props.component_type)!.title
                }`}
          </div>
          {this.props.isModal && this.props.onCloseModal && (
            <div>
              <div
                onClick={this.props.onCloseModal}
                className="flex items-center justify-center h-10 w-10 rounded-full bg-gray-50 cursor-pointer hover:bg-accent-800"
              >
                <MdClose className="text-2xl" />
              </div>
            </div>
          )}
        </div>
        {/* Form here */}
        <div className="mt-4">
          <form onSubmit={this.SubmitForm}>
            {/* Category */}
            {this.props.component_type === SettingsRouteType.subarea && (
              <div>
                {this.GetParentData().length > 0 && (
                  <div className="">Select area of intervention</div>
                )}
                {this.GetParentData().length > 0 &&
                  this.state.openSelectionOfParent === false && (
                    <div>
                      <div
                        onClick={() =>
                          this.setState({ openSelectionOfParent: true })
                        }
                        className={`border-2 ${
                          this.state.formError !== null &&
                          this.state.formError.target === "foreign_value"
                            ? "border-red-600 bg-red-100"
                            : this.ForeignValue() === undefined
                            ? "border-primary-800 text-gray-500 font-normal"
                            : "border-primary-800 text-primary-800 font-bold animate__animated animate__fadeIn"
                        } rounded px-3 py-3 flex flex-row items-center justify-between gap-2 hover:text-primary-800 hover:bg-primary-100 hover:border-primary-800 cursor-pointer`}
                      >
                        <div className="flex flex-row items-center gap-2">
                          <div>
                            {this.ForeignValue() === undefined ? (
                              <></>
                            ) : (
                              // <BsCheckCircleFill className="text-2xl" />
                              <></>
                            )}
                          </div>
                          <span>
                            {this.ForeignValue() === undefined
                              ? "Click to select area of intervention"
                              : this.ForeignValue()!.name}
                          </span>
                        </div>
                        <div>
                          <BsArrowRight className="text-2xl" />
                        </div>
                      </div>
                      {this.state.formError !== null &&
                        this.state.formError.target === "foreign_value" && (
                          <span className="text-red-600 text-sm">
                            {this.state.formError.msg}
                          </span>
                        )}
                    </div>
                  )}
                {GetComponentType(this.props.component_type)!.hasParentData ===
                  true &&
                  this.state.openSelectionOfParent === true && (
                    <div className="animate__animated animate__fadeIn">
                      <SelectCustom
                        loading={this.state.loading}
                        id={"id"}
                        title={"name"}
                        close={() => {
                          this.setState({ openSelectionOfParent: false });
                        }}
                        data={this.GetParentData()}
                        click={(data: { name: string; id: string }) => {
                          this.setState({
                            foreign_value: data,
                            formError: null,
                            success: "",
                            openSelectionOfParent: false,
                            tags: [],
                          });
                          // GetComponentType(this.props.component_type)!
                          //   .hasTags === true && this.GetSubSectors(data.id);
                        }}
                      />
                    </div>
                  )}
              </div>
            )}
            <TextInput
              onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                this.HandleName(event.target.value);
              }}
              value={this.state.name}
              error={
                this.state.formError !== null &&
                this.state.formError.target === "name"
                  ? this.state.formError.msg
                  : ""
              }
              label={`Name / Title`}
              placeholder={"Enter name or title"}
              name={"name"}
              disabled={this.state.loading}
            />

            {/* --------- */}
            {GetComponentType(this.props.component_type)!.hasDescription ===
              true && (
              <TextArea
                onChange={(event: React.ChangeEvent<HTMLTextAreaElement>) => {
                  this.HandleDescription(event.target.value);
                }}
                value={this.state.description}
                error={
                  this.state.formError !== null &&
                  this.state.formError.target === "description"
                    ? this.state.formError.msg
                    : ""
                }
                label={`Description`}
                placeholder={"Enter description"}
                name={"description"}
                disabled={this.state.loading}
              />
            )}
            {GetComponentType(this.props.component_type)!.hasTags === true && (
              <div>
                {/* Choose sub sectors */}
                {this.state.loading === true ? (
                  <div className="text-lg font-light text-accent-900 mt-2">
                    Loading...
                  </div>
                ) : (
                  <TagsList
                    data={this.state.sub_sectors}
                    selected_sub_sector_id={this.state.tags}
                    setTag={this.setTag}
                    selectAllTags={this.selectAllTags}
                    loadSubSectors={this.GetSubSectors}
                  />
                )}
              </div>
            )}
            {/* Choose roles */}
            {this.props.component_type ===
              SettingsRouteType.trainees_category &&
              this.props.basic.roles !== null && (
                <SelectRoles
                  systemRoles={this.props.basic.roles}
                  addedRoles={this.state.addedRoles}
                  addRole={this.SetAddNewRole}
                />
              )}
            <div className="mt-4">
              {this.state.formError !== null &&
                this.state.formError.target === "main" && (
                  <div
                    onClick={() =>
                      this.setState({ formError: null, success: "" })
                    }
                    className="bg-red-50 text-red-700 flex flex-row items-center gap-3 w-full px-4 py-3 rounded cursor-pointer animate__animated animate__shakeX mb-3 -mt-1"
                  >
                    <div>
                      <BiErrorAlt className="text-3xl" />
                    </div>
                    <span className="">{this.state.formError.msg}</span>
                  </div>
                )}
              {this.state.success !== "" && (
                <div
                  onClick={() =>
                    this.setState({ formError: null, success: "" })
                  }
                  className="bg-green-50 text-green-700 flex flex-row items-center gap-3 w-full animate__animated animate__zoomIn px-3 py-2 rounded cursor-pointer border border-green-300 mb-2 animate__faster -mt-1"
                >
                  <div>
                    <BiCheckCircle className="text-2xl" />
                  </div>
                  <span className="font-semibold">{this.state.success}</span>
                </div>
              )}
              <div className="flex flex-row items-center justify-between gap-2">
                {this.state.loading === true ? (
                  <div></div>
                ) : this.props.update_data !== undefined ? (
                  <div></div>
                ) : (
                  <Button
                    type="button"
                    onClick={() => {
                      this.resetForm();
                    }}
                    theme="default"
                    disabled={this.state.loading}
                    className="flex flex-row items-center gap-2"
                  >
                    <div>
                      <AiOutlineReload className="text-2xl text-gray-400" />
                    </div>
                    <span>Reset</span>
                  </Button>
                )}

                <div className="flex flex-row items-center justify-end gap-2">
                  {GetComponentType(this.props.component_type)!.hasMandatory ===
                    true && (
                    <div
                      className={`${
                        this.state.is_mandatory === true
                          ? "bg-primary-100 text-primary-800 font-bold"
                          : "bg-gray-100"
                      } px-3 py-2 rounded cursor-pointer flex flex-row items-center justify-center gap-2`}
                      onClick={() =>
                        this.setState({
                          is_mandatory: !this.state.is_mandatory,
                        })
                      }
                    >
                      <div>
                        {this.state.is_mandatory === true ? (
                          <BsCheckSquareFill className="text-2xl" />
                        ) : (
                          <div className="h-6 w-6 bg-white border-2 border-gray-500 rounded"></div>
                        )}
                      </div>
                      <span>Is Mandatory (CPD element)</span>
                    </div>
                  )}
                  <Button
                    theme={this.state.loading === true ? "default" : "accent"}
                    className="flex flex-row items-center gap-2 justify-center"
                    type="submit"
                    disabled={this.state.loading}
                  >
                    <div>
                      {this.state.loading === true ? (
                        <AiOutlineLoading3Quarters className="text-2xl animate-spin" />
                      ) : (
                        <MdOutlineSave className="text-2xl" />
                      )}
                    </div>
                    <span>
                      {this.state.loading === true
                        ? "Loading, please wait..."
                        : "Submit data"}
                    </span>
                  </Button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const CreateSettingsComponent = connect(mapStateToProps, {
  FC_GetAllSystemRoles,
})(_CreateSettingsComponent);
