export type AlertSizeType = "danger" | "success" | "info" | "warning";

export const AlertType = (key: AlertSizeType): string => {
  switch (key) {
    case "danger":
      return "text-white bg-red-500";
    case "info":
      return "text-white bg-blue-300";
    case "success":
      return "text-white bg-green-500";
    case "warning":
      return "text-black bg-yellow-400";
    default:
      return "";
  }
};
