import { combineReducers } from "redux";
import {
  App,
  Auth,
  Basic,
  PermutationsStore,
  Placement,
  SchoolHierarchyStore,
  TransferAlgorithmStore,
} from "../actions";

import { authReducer } from "./auth.reducer";
import { appReducer } from "./app.reducer";
import { basicReducer } from "./basic.reducer";
import {
  terminationReducer,
  TerminationReducerType,
} from "./termination.reducer";
import { placementReducer } from "./placement.reducer";
import { schoolHierarchyReducer } from "./study_hierarchy.reducer";
import { transferReducer, TransferReducerType } from "./transfer.reducer";
import { systemUsersReducer } from "./system_users.reducer";
import { SystemUsersStore } from "../actions/system_users.action";
import {
  AcademicCalenderReducerInterface,
  academicAcademicReducer,
} from "./academic.reducer";
import { transferAlgorithmReducer } from "./transfer_algorithm.reducer";
import { permutationsReducer } from "./permutations.reducer";

// define the entire state into the entire sire
export interface StoreState {
  auth: Auth;
  app: App;
  basic: Basic;
  terminations: TerminationReducerType;
  placement: Placement;
  schoolHierarchyStore: SchoolHierarchyStore;
  transfer: TransferReducerType;
  systemUsers: SystemUsersStore;
  academicCalender: AcademicCalenderReducerInterface;
  transferAlgorithm: TransferAlgorithmStore;
  permutations: PermutationsStore;
}

export const reducers = combineReducers<StoreState>({
  auth: authReducer,
  app: appReducer,
  basic: basicReducer,
  terminations: terminationReducer,
  placement: placementReducer,
  schoolHierarchyStore: schoolHierarchyReducer,
  transfer: transferReducer,
  systemUsers: systemUsersReducer,
  academicCalender: academicAcademicReducer,
  transferAlgorithm: transferAlgorithmReducer,
  permutations: permutationsReducer,
});
