import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";
import {
  Annex3Subjects,
  Annex3ToBePlaced,
  DistrictList,
  Teaching_Level__Interface,
} from "../../../actions";
import { commaFy, DATE, mayors_list } from "../../../utils/functions";
import { getProvinceFromCode } from "../Annex1/Annex1Footer";

const convertSubjectsToString = (tempSubjects: Annex3Subjects[]): string => {
  var subjects = "";
  for (const item of tempSubjects) {
    subjects += `${item.grade_name} ${item.course_name}`;
    if (
      tempSubjects[tempSubjects.length - 1].shool_hierarch_id !==
      item.shool_hierarch_id
    ) {
      subjects += ", ";
    }
  }
  return subjects;
};

const GetPbByLevel = (level_name: string, data: Annex3ToBePlaced) => {
  if (data !== null) {
    const level_count = data.total_teach_level.find(
      (itm) =>
        itm.teaching_level_id.toLocaleLowerCase() ===
        level_name.toLocaleLowerCase()
    );
    if (level_count !== undefined) {
      return parseInt(level_count.total);
    }
  }
  return 0;
};

const GetTotalsPbByLevel = (level_name: string, data: Annex3ToBePlaced) => {
  const level_count = data.district_needed_posts.filter(
    (itm) =>
      itm.teaching_level.toLocaleLowerCase() === level_name.toLocaleLowerCase()
  );
  return level_count.length;
};

const GetTeacherPB = (
  level_name: string,
  school_need_post_id: string,
  data: Annex3ToBePlaced
) => {
  var filteredData =
    level_name === null
      ? data.district_needed_posts
      : data.district_needed_posts.filter(
          (itm) =>
            itm.teaching_level.toLocaleLowerCase() ===
            level_name.toLocaleLowerCase()
        );
  var PDData = [];
  var iteration = 0;
  for (const item of filteredData) {
    iteration += 1;
    let pb = GetPbByLevel(item.teaching_level, data) + iteration;
    PDData.push({
      ...item,
      pb: pb,
    });
  }
  const selectedPB = PDData.find(
    (itm) => itm.school_need_post_id === school_need_post_id
  );
  if (selectedPB !== undefined) {
    return selectedPB.pb;
  }
  return 0;
};

const returnDataItems = (data: Annex3ToBePlaced) => {
  var n_index = 0;
  return data.district_needed_posts.map((item, i) => {
    n_index += 1;
    return {
      pb: `${GetTeacherPB(
        item.teaching_level,
        item.school_need_post_id,
        data
      )}`,
      index: `${n_index}`,
      school_code: `${item.school_code}`,
      school_name: `${item.school_name}`,
      occupation: `${item.occupation}`,
      position_name: `${item.position_name}`,
      specialization_name: `${item.specialization_name}`,
      qualification_name: `${item.qualification_name}`,
      subjects_taught: `${convertSubjectsToString(item.subjects)}`,
      total_periods: `${item.total_period}`,
    };
  });
};

const TableData = (
  doc: any,
  marginTop: number,
  tableDataTemp: Annex3ToBePlaced,
  level_id: string
) => {
  const tableData = {
    district_needed_posts: tableDataTemp.district_needed_posts.filter(
      (itm) => itm.teaching_level_id === level_id
    ),
    total_teach_level: tableDataTemp.total_teach_level,
  };
  if (tableData.district_needed_posts.length === 0) {
    return autoTable(doc, {
      theme: "grid",
      showHead: true,
      useCss: true,
      styles: {
        lineColor: "gray",
        cellPadding: 4,
        fontSize: 12,
        fillColor: "#f3f3f3",
        halign: "left",
      },
      startY: marginTop,
      rowPageBreak: "avoid",
      headStyles: {
        fillColor: "white",
        textColor: "black",
        lineColor: "gray",
        fontSize: 6.5,
        lineWidth: 0.1,
      },
      columnStyles: { No: { halign: "left" } },
      columns: [{ header: "", dataKey: "no" }],
      body: [],
    });
  }
  if (tableData.district_needed_posts.length > 0) {
    return autoTable(doc, {
      theme: "grid",
      showHead: true,
      useCss: true,
      styles: {
        lineColor: "gray",
        cellPadding: 1,
        fontSize: 7,
        //   halign: "center",
      },
      startY: marginTop,
      rowPageBreak: "avoid",
      headStyles: {
        fillColor: "white",
        textColor: "black",
        lineColor: "gray",
        fontSize: 6.5,
        lineWidth: 0.1,
      },
      columnStyles: { No: { halign: "left" } },
      columns: [
        { header: "PB", dataKey: "pb" },
        { header: "No", dataKey: "index" },
        { header: "School code", dataKey: "school_code" },
        { header: "School name", dataKey: "school_name" },
        { header: "Position code", dataKey: "occupation" },
        { header: "Position name", dataKey: "position_name" },
        { header: "Specialization", dataKey: "specialization_name" },
        { header: "Qualification", dataKey: "qualification_name" },
        { header: "Subjects taught", dataKey: "subjects_taught" },
        { header: "Total periods", dataKey: "total_periods" },
      ],
      body:
        tableData.district_needed_posts.length === 0
          ? []
          : returnDataItems(tableData),
    });
  }
};

export const DownloadToBePlaced = (
  data: Annex3ToBePlaced,
  levels: Teaching_Level__Interface[] | null,
  Selected_district: DistrictList,
  callBack: (loading: boolean) => void
) => {
  callBack(true);
  const doc = new jsPDF("l", "mm", "a4");
  let currentPosition = 10;
  doc.setFontSize(10);
  doc.setFont("fontStyle", "normal");

  doc.text("REPUBLIC OF RWANDA", 12, 15);
  doc.text(`Report Date: ${DATE(new Date())}`, 240, 15);

  doc.text("PROVINCE: ", 12, 20);
  doc.setFont("fontStyle", "bold");
  doc.text(
    `${getProvinceFromCode(Selected_district.district_code[0])}`,
    35,
    20
  );
  doc.setFont("fontStyle", "normal");
  doc.text("DISTRICT NAME: ", 12, 25);
  doc.setFont("fontStyle", "bold");
  doc.text(
    `${Selected_district.district_code}-${Selected_district.district_name} District`,
    45,
    25
  );
  doc.setFont("fontStyle", "normal");
  doc.text("POSTS TO BE PLACED FOR PRE-PRIMARY: ", 12, 40);
  doc.setFont("fontStyle", "bold");
  doc.text(`${commaFy(GetTotalsPbByLevel("PRE-PRIMARY", data))}`, 83, 40);
  doc.setFont("fontStyle", "normal");
  doc.text("POSTS TO BE PLACED FOR PRIMARY: ", 12, 45);
  doc.setFont("fontStyle", "bold");
  doc.text(`${commaFy(GetTotalsPbByLevel("PRIMARY", data))}`, 75, 45);
  doc.setFont("fontStyle", "normal");
  doc.text("POSTS TO BE PLACED FOR SECONDARY: ", 12, 50);
  doc.setFont("fontStyle", "bold");
  doc.text(`${commaFy(GetTotalsPbByLevel("SECONDARY", data))}`, 81, 50);
  doc.setFont("fontStyle", "normal");
  doc.text("TOTAL POSTS TO BE PLACED: ", 12, 55);
  doc.setFont("fontStyle", "bold");
  doc.text(`${commaFy(data.district_needed_posts.length)}`, 63, 55);
  // Main titles
  doc.setFontSize(16);
  doc.setFont("fontStyle", "bold");
  doc.setTextColor("#025198");
  doc.text(
    `LIST OF UNFIELD TEACHING, ADMINISTRATIVE AND SUPPORTING STAFF POSTS`,
    12,
    75
  );
  doc.setTextColor("#000");

  if (levels !== null) {
    currentPosition = 75;
    var level_iteration = 0;
    for (const level of levels) {
      if (
        data.district_needed_posts.filter(
          (itm) => itm.teaching_level_id === level.teaching_level_id
        ).length > 0
      ) {
        level_iteration = level_iteration + 1;
        doc.setFont("fontStyle", "bold");
        doc.setFontSize(14);
        doc.text(
          `${level_iteration}) ${level.teaching_level.toUpperCase()} SCHOOLS STAFF`,
          12,
          currentPosition + 20
        );
        doc.setFont("fontStyle", "normal");
        TableData(doc, currentPosition + 30, data, level.teaching_level_id);
        currentPosition = (doc as any).lastAutoTable.finalY as number;
      }
      //   ---------------
      if (currentPosition > 160) {
        doc.addPage();
        currentPosition = 10;
      }
    }
  }
  //   ---------------
  if (currentPosition > 160) {
    doc.addPage();
    currentPosition = 30;
  }
  // Footer
  doc.setFontSize(10);
  // column one
  doc.setFont("fontWeight", "normal");
  doc.text("Prepared by", 15, currentPosition + 15);
  doc.setFont("fontWeight", "bold");
  doc.text(
    `........................................`,
    15,
    currentPosition + 35
  );
  // column 2
  doc.setFont("fontWeight", "normal");
  doc.text("Verified by", 110, currentPosition + 15);
  doc.setFont("fontWeight", "bold");
  //   doc.text(`District Director of Education`, 110, currentPosition + 35);
  doc.text(
    `........................................`,
    110,
    currentPosition + 35
  );

  // column 3
  doc.setFont("fontWeight", "normal");
  doc.text("Approved by", 200, currentPosition + 15);
  doc.text(
    `${mayors_list
      .find((itm) => itm.district_code === Selected_district.district_code)
      ?.mayor_family_name.toUpperCase()} ${mayors_list
      .find((itm) => itm.district_code === Selected_district.district_code)
      ?.mayor_first_name.toUpperCase()}`,
    200,
    currentPosition + 35
  );
  doc.setFont("fontWeight", "bold");
  doc.text(
    `${
      Selected_district.district_code[0] === "1"
        ? "District Executive Administrator of"
        : "Mayor of "
    } ${
      Selected_district.district_code[0] !== "1"
        ? Selected_district.district_name + " district"
        : ""
    }`,
    200,
    currentPosition + 40
  );
  doc.text(
    `${
      Selected_district.district_code[0] === "1"
        ? Selected_district.district_name + " district"
        : ""
    }`,
    200,
    currentPosition + 45
  );
  //
  doc
    .save(
      `${Selected_district.district_code}-${Selected_district.district_name} vacant posts to be placed.pdf`,
      { returnPromise: true }
    )
    .then(() => {
      callBack(false);
    });
};
